<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formTrade" id="formwrapper"  class="demo-form-inline crm-form" label-width="100px">
      <el-row>
        <el-form-item label="华润通交易流水号" label-width="130px">
          <el-input v-model="formTrade.tradeNo" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="用户编号">
          <el-input v-model="formTrade.memberId" class="el-winp" placeholder=" "></el-input>
        </el-form-item>


        <el-form-item label="支付用户">
          <el-input v-model="formTrade.payUserId" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="渠道侧交易流水号" label-width="130px">
          <el-input v-model="formTrade.channelTradeSn" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="交易商户">
          <search-Merchant v-on:communicate="setMerchantInfo" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formTrade.merchantName"></search-Merchant>
        </el-form-item>

        <el-form-item label="交易门店">
          <el-autocomplete v-model="formTrade.storeName" :fetch-suggestions="querySearchAsyncStore" placeholder="请输入并选择门店" class="el-winp"  :disabled="!formTrade.merchantCode" @select="setStoreInfo">
          </el-autocomplete>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="开始交易时间"  label-width="130px">
          <el-date-picker v-model="formTrade.startTradeTime" :editable="false" class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="结束交易时间">
          <el-date-picker v-model="formTrade.endTradeTime" :editable="false" class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button class="btn-default el-btnw " icon="el-icon-search" @click="handleReset">重置</el-button>

        </el-form-item>
      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" :fit="true" border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="tradeNo" align="center" width="150" label="华润通交易流水号"></el-table-column>
        <el-table-column prop="channelTradeSn" align="center" width="120"  label="银行交易流水号" ></el-table-column>
        <el-table-column prop="payUserId" align="center" width="110" label="支付用户编号" ></el-table-column>
        <el-table-column prop="tradeType" align="center" label="交易类型" ></el-table-column>
        <el-table-column prop="tradeAmount" align="center" label="交易金额" ></el-table-column>
        <el-table-column prop="tradeTime" align="center" width="160" label="交易时间" ></el-table-column>
        <el-table-column prop="channelCompleteTime" align="center" width="160" label="处理时间" ></el-table-column>
        <el-table-column prop="status" align="center" label="状态" ></el-table-column>
        <el-table-column prop="errorMsg" align="center" label="结果说明" ></el-table-column>
      </el-table>
      <el-row  class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  import searchMerchant from 'components/common/search'
  export default {
    components: {ElRow, searchMerchant},
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置
        //form 表单字段
        formTrade: {
          tradeNo:'',	        //华润通侧交易流水号	string
          channelTradeSn:'',	//渠道侧交易流水号	string
          limit:'',         	//每页记录数量	number
          memberId:'',	      //会员id	string
          offset:0,	          //偏移量	number
          payUserId:'',	      //支付用户id	string
          startTradeTime:'',  //开始交易时间	string
          endTradeTime:'',	  //结束交易时间	string
          merchantCode:'',	  //商户编号	string
          merchantName:'',	  //商户名称	string
          storeCode:'',       //门店编号	string
          storeName:'',       //门店名称	string
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
      }
    },
    methods: {
      // 可用门店选择
      querySearchAsyncStore(queryString, cb){
        var self=this;
        self.formTrade.storeCode=""
        if(!queryString){
          cb([])
          return
        }
        var url=merchantPostLink+"/merchant/admin/store/storeQueryList.htm";
        var postData={
          sysId:"T0000017",
          token:this.getCookie('token'),
          transactionUuid:this.uuid(),
          storeName:queryString,
          merchantCode:self.formTrade.merchantCode
        };
        this.$http.post(url, postData).then(function(res){
          res=res.data
          if(res.code=="S0A00000"){
            var list=[];
            res.data.forEach(function(e){
              if(e.storeName==queryString){
                self.formTrade.storeCode=e.storeCode;
              }
              list.push({value: e.storeName, code: e.storeCode})
            })
            cb(list)
          }
          else{
            cb([])
            self.formTrade.storeCode=""
          }
        })
      },
      setStoreInfo(item){
        this.formTrade.storeName = item.value
        this.formTrade.storeCode = item.code
      },
      setMerchantInfo(item){
        this.formTrade.merchantName = item.value   // form  value
        this.formTrade.merchantCode = item.id // form id
        this.formTrade.storeName = ""
        this.formTrade.storeCode = ""
      },
      // 表单重置
      handleReset() {
        let formTrade={
          tradeNo:'',	        //华润通侧交易流水号	string
          channelTradeSn:'',	//渠道侧交易流水号	string
          limit:'',         	//每页记录数量	number
          memberId:'',	      //会员id	string
          offset:0,	          //偏移量	number
          payUserId:'',	      //支付用户id	string
          startTradeTime:'',  //开始交易时间	string
          endTradeTime:'',	  //结束交易时间	string
          merchantCode:'',	  //商户编号	string
          merchantName:'',	  //商户名称	string
          storeCode:'',       //门店编号	string
          storeName:'',       //门店名称	string
        }
        this.formTrade=formTrade
      },
      // 表单查询
      handleInquire(offsetNum) {
        let dataUrl= payTrade+'/payment/order/withdraw/list.htm';
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let params={
          'tradeNo':this.formTrade.tradeNo,	        //华润通侧交易流水号	string
          'channelTradeSn':this.formTrade.channelTradeSn,	//渠道侧交易流水号	string
          'limit':this.formTrade.limit,         	//每页记录数量	number
          'memberId':this.formTrade.memberId,	      //会员id	string
          'offset':offsetNum,	          //偏移量	number
          'payUserId':this.formTrade.payUserId,	      //支付用户id	string
          'startTradeTime':this.formTrade.startTradeTime,  //开始交易时间	string
          'endTradeTime':this.formTrade.endTradeTime,	  //结束交易时间	string
          'merchantCode':this.formTrade.merchantCode,	  //商户编号	string
          'storeCode':this.formTrade.storeCode,       //门店编号	string
          'token':this.getCookie('token'),
          'transactionUuid':this.uuid()
        }
        //let params={'data':data,'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data
            this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
            console.log(this.pagination.total)
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //表格 查看  编辑  禁用 操作
      handleDetail(index, row){
        this.$router.push({path: '/home/walletUser/walletUserDetail', query: {userId: row.userId}})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



