      //暂存保存数据处理
      couponsInfo(){
        // 满减折扣现金
        if (this.formCoupon.couponCategory == 'DISCOUNT') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponDiscountRate;
          this.formCoupon.couponDiscountTrueRate=this.filterDiscount(100-this.formCoupon.couponDiscountRate);
          this.formCoupon.couponCategoryValue2 = this.formCoupon.couponDiscountLimit;
        }
        else if (this.formCoupon.couponCategory == 'MONEYOFF') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponMinOrderAmt;
          this.formCoupon.couponCategoryValue2 = this.formCoupon.couponMaxOrderAmt;
        }
        else if (this.formCoupon.couponCategory == 'CASH') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponFaceAmt;
        }
        // 时间日期
        if (this.formCoupon.validDateType == 'POSTPONED') {
          this.formCoupon.validDateValue1 = this.formCoupon.postponedDay;
        }
        else if (this.formCoupon.validDateType == 'ENDFIXED') {

          if(this.formCoupon.validDateEnd=='datetime'){
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.endfixedDatetime,13);
          }else if(this.formCoupon.validDateEnd=='date'){
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.endfixedDate,10);
          }
        }
        else if (this.formCoupon.validDateType == 'RANGEFIXED') {
          if(this.formCoupon.validDateRang=='datetime'){
            this.formCoupon.validDateValue1 = this.filterDateStr(this.formCoupon.formatedStartDatetime,13);
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.formatedEndDatetime,13);
