        }
        if(this.formCoupon.shareTotle!=100){
          this.$message({showClose: true,duration:2000,type: 'error',message:'分摊比例总和不是100%, 不能更新为启用状态！'});
          return
        }
        if(!this.formCoupon.uploadImage){
          this.$message({showClose: true,duration:2000,type: 'error',message:'图片不能为空！'});
          return
        }
        if(!this.formCoupon.couponExt.remark){
          this.$message({showClose: true,duration:2000,type: 'error',message:'券说明不能为空！'});
          return
        }
                  let data = {
                    "costSharelist":this.formCoupon.shareSmList,
                    "couponExt":JSON.stringify(this.formCoupon.couponExt),
                    "couponType": {
                      "couponCategory": this.formCoupon.couponCategory,
                      "couponCategoryValue1": this.formCoupon.couponCategoryValue1,
                      "couponCategoryValue2": this.formCoupon.couponCategoryValue2,
                      "couponName": this.formCoupon.couponName,
                      "fileBase64": imgBase64[1],
                      "issueCount": this.formCoupon.issueCount,
                      "logoName": this.formCoupon.logoName,
                      "operateRemark": this.formCoupon.operateRemark,
                      "validDateType": this.formCoupon.validDateType,
                      "validDateValue1": this.formCoupon.validDateValue1,
                      "validDateValue2": this.formCoupon.validDateValue2,
                    },
                    "merchantStoreFlag": this.formCoupon.merchantStoreFlag,
                    "payFlag": this.formCoupon.payFlag,
                    'payItem': this.formCoupon.payItem,
                    "storeScopeList": this.formCoupon.storeScopeList,
                  };
                let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
                let viewObj={
                  "couponName": this.formCoupon.couponName,
                  "couponCategory": this.formCoupon.couponCategory,
                  "couponMinOrderAmt": this.formCoupon.couponMinOrderAmt,  //券满减使用门槛金额
                  "couponMaxOrderAmt":this.formCoupon.couponMaxOrderAmt,  //券满减使用门槛金额
                  "couponFaceAmt":this.formCoupon.couponFaceAmt,      //券现金面等值金额
                  "couponDiscountRate": this.formCoupon.couponDiscountRate, //券折扣值
                  "couponDiscountTrueRate":this.formCoupon.couponDiscountTrueRate, //券折真实值
                  "couponDiscountLimit":this.formCoupon.couponDiscountLimit, //券折扣值限额
                  "validDateType":this.formCoupon.validDateType,       //有效期类型
                  "postponedDay":this.formCoupon.postponedDay,
                  "endfixedDate":this.filterDateStr(this.formCoupon.endfixedDate,13),        //固定失效期天
                  "endfixedDatetime":this.filterDateStr(this.formCoupon.endfixedDatetime,13),    //固定失效期时间段
                  "validDateEnd":this.formCoupon.validDateEnd,         //固定失效日期状态
                  "validDateRang":this.formCoupon.validDateRang,        //固定失效区间日期状态
                  "formatedStartDate":this.filterDateStr(this.formCoupon.formatedStartDate,13),   //券生效截起始日期天
                  "formatedEndDate":this.filterDateStr(this.formCoupon.formatedEndDate,13),     //券失效截止日期天
                  "formatedStartDatetime":this.filterDateStr(this.formCoupon.formatedStartDatetime,13),//券生效截起始日期区间
                  "formatedEndDatetime":this.filterDateStr(this.formCoupon.formatedEndDatetime,13),  //券失效截止日期区间
                  "couponExt":JSON.stringify(this.formCoupon.couponExt),      //券说明
                  "uploadImage":this.formCoupon.uploadImage
                }
                let formJson=this.formCoupon;
                 formJson.endfixedDate=this.filterDateStr(this.formCoupon.endfixedDate,13);
                 formJson.endfixedDatetime=this.filterDateStr(this.formCoupon.endfixedDatetime,13);
                 formJson.formatedStartDate=this.filterDateStr(this.formCoupon.formatedStartDate,13);
                 formJson.formatedEndDate=this.filterDateStr(this.formCoupon.formatedEndDate,13);
                 formJson.formatedStartDatetime=this.filterDateStr(this.formCoupon.formatedStartDatetime,13);
                 formJson.formatedEndDatetime=this.filterDateStr(this.formCoupon.formatedEndDatetime,13);
                 this.$router.push({path: '/home/couponType/couponTypeView', query:{'viewObj':viewObj,'params':params,'formJson':formJson,}});
        },
        //重置form
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
      //返回
      goContype(){
        this.$router.push({path: '/home/couponType'})
      },
      // 切换清空数据
      // paramsName  radio
      emptyData(paramsName){
        //清空券数量
        if(paramsName=='couponRadio'){
          if(this.formCoupon.couponRadio=='1'){
            this.formCoupon.issueCount=0;
          }
        }
        else if(paramsName=='couponCategory'){
          if(this.formCoupon.couponCategory=='MONEYOFF'){
            this.formCoupon.couponMinOrderAmt='';
            this.formCoupon.couponMaxOrderAmt='';
          }
          else if(this.formCoupon.couponCategory=='DISCOUNT'){
            this.formCoupon.couponDiscountRate='';
            this.formCoupon.couponDiscountTrueRate='';
            this.formCoupon.couponDiscountLimit='';
          }
          else if(this.formCoupon.couponCategory=='CASH'){
            this.formCoupon.couponFaceAmt='';
          }
        }
        else if(paramsName=='validDateType') {
          if (this.formCoupon.validDateType == 'POSTPONED') {
            this.formCoupon.endfixedDatetime = '';
            this.formCoupon.endfixedDate = '';
            this.formCoupon.postponedDay = '';
            this.formCoupon.formatedStartDate = '';
            this.formCoupon.formatedEndDate = '';
            this.formCoupon.formatedStartDatetime = '';
            this.formCoupon.formatedEndDatetime = '';
            this.formCoupon.validDateValue1='';
            this.formCoupon.validDateValue2 = '';
          }
          else if (this.formCoupon.validDateType == 'ENDFIXED') {
              this.formCoupon.endfixedDatetime = '';
              this.formCoupon.endfixedDate = '';
              this.formCoupon.postponedDay = '';
              this.formCoupon.formatedStartDate = '';
              this.formCoupon.formatedEndDate = '';
              this.formCoupon.formatedStartDatetime = '';
              this.formCoupon.formatedEndDatetime = '';
              this.formCoupon.validDateValue1='';
              this.formCoupon.validDateValue2 = '';
          }
          else if (this.formCoupon.validDateType == 'RANGEFIXED') {
            this.formCoupon.endfixedDatetime = '';
            this.formCoupon.endfixedDate = '';
            this.formCoupon.postponedDay = '';
            this.formCoupon.formatedStartDate = '';
            this.formCoupon.formatedEndDate = '';
            this.formCoupon.formatedStartDatetime = '';
            this.formCoupon.formatedEndDatetime = '';
            this.formCoupon.validDateValue1='';
            this.formCoupon.validDateValue2 = '';
          }
        }
        else if(paramsName=='validDateEnd') {
          if( this.formCoupon.validDateEnd=='datetime'){
            this.formCoupon.endfixedDatetime = '';
            this.formCoupon.validDateValue2 = '';
          }
          else{
            this.formCoupon.endfixedDate = '';
            this.formCoupon.validDateValue2 = '';
          }
        }
        else if(paramsName=='validDateRang') {
          if( this.formCoupon.validDateRang=='datetime'){
            this.formCoupon.formatedStartDate = '';
            this.formCoupon.formatedEndDate = '';
            this.formCoupon.validDateValue1='';
            this.formCoupon.validDateValue2='';
          }else{
            this.formCoupon.formatedStartDatetime = '';
            this.formCoupon.formatedEndDatetime = '';
            this.formCoupon.validDateValue1='';
            this.formCoupon.validDateValue2='';
          }
        }
        else if(paramsName=='merchantStoreFlag'){
          if(this.formCoupon.merchantStoreFlag=='true'){
            this.formCoupon.infoData='';
            this.formCoupon.merchantStoreFlag='true';
          }else if(this.formCoupon.merchantStoreFlag=='false'){
            this.formCoupon.merchantStoreFlag='false';
          }
        }
        else if(paramsName=='payFlag'){
          if(this.formCoupon.payFlag=='true'){
            this.formCoupon.payFlag='true';
            this.formCoupon.payBankList=[];
            this.formCoupon.checkedDepositList=[];
            this.formCoupon.checkedCreditList=[];
            this.formCoupon.payDeposit = false;
            this.formCoupon.payCredit = false;
          }else if(this.formCoupon.payFlag=='false'){
            this.formCoupon.payFlag='false';
          }
        }
      }
    },
    created(){
      this.fileUrl= couponPostLink+"/fin-rules-osprest/couponType/couponTypeFileUpload.htm";
      this.loadStoreList();// 搜索店铺名字
      var formCouponData= this.$route.query.formJson;
      if(formCouponData){
       this.formCoupon=formCouponData;
         if(formCouponData.uploadImage){
           console.log(formCouponData.uploadImage)
           this.formCoupon.uploadShow=true;
         }
      }
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style lang="less" scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item {
    margin-bottom: 20px;
  }
  .payDepositList, .payCreditList{
    width: 540px;
  }
  .payDepositList span, .payCreditList span{
    color:#7794b3;
  }

</style>



