<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formMarket" id="formwrapper"  class="demo-form-inline crm-form" label-width="96px">
      <el-row>
        <el-form-item label="营销事件编号">
          <el-input v-model="formMarket.eventCode" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="营销事件名称">
          <el-input v-model="formMarket.eventName" class="el-winp" placeholder=" "></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="formMarket.status" class="el-winp" placeholder=" ">
            <el-option label="全部" value=""></el-option>
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="启用" value="VALID"></el-option>
            <el-option label="禁用" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="适用会员等级">
          <el-select v-model="formMarket.memberLevel" class="el-winp" placeholder=" ">
            <el-option label="全部" value=""></el-option>
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="已提交" value="COMMITTED"></el-option>
            <el-option label="启用" value="VALID"></el-option>
            <el-option label="禁用" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="事件类型编号">
          <el-input v-model="formMarket.typeCode" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="事件类型名称">
          <el-input v-model="formMarket.typeName" class="el-winp" placeholder=" "></el-input>
        </el-form-item>


        <el-form-item label="适用商户">
          <search-Merchant v-on:communicate="setMerchantInfo" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formMarket.merchantName"></search-Merchant>
        </el-form-item>

        <el-form-item label="适用门店">
          <el-autocomplete v-model="formMarket.storeName" :fetch-suggestions="querySearchAsyncStore" placeholder="请输入并选择门店" class="el-winp"  :disabled="!formMarket.merchantCode" @select="setStoreInfo">
          </el-autocomplete>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="有效开始时间">
          <el-date-picker v-model="formMarket.startDate" :editable="false"  class="el-winp"  type="datetime"  placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="有效结束时间">
          <el-date-picker v-model="formMarket.endDate"  :editable="false" class="el-winp"  type="datetime"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button type="primary" class="el-btnw el-marl" icon="el-icon-plus" @click="handleAdd">新增</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>
      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" :fit="true" border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="eventCode" align="center" width="90" label="事件编码"></el-table-column>
        <el-table-column prop="eventName" align="center" width="90" label="事件名称" ></el-table-column>
        <el-table-column prop="typeCode" align="center" width="120" label="所属事件类型" ></el-table-column>
        <el-table-column prop="startDate" align="center" width="160" label="有效开始时间" ></el-table-column>
        <el-table-column prop="endDate" align="center"  width="160" label="有效结束时间" ></el-table-column>
        <el-table-column prop="status" align="center"  label="状态" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='VALID'">有效</span>
            <span v-if="scope.row.status=='INVALID'">无效</span>
            <span v-if="scope.row.status=='COMMITTED'">已提交</span>
            <span v-if="scope.row.status=='NEW'">新建</span>
          </template>
        </el-table-column>
        <el-table-column prop="createdBy" align="center" label="创建人" ></el-table-column>
        <el-table-column prop="createTime" align="center" width="160" label="创建时间" ></el-table-column>
        <el-table-column prop="approvedBy" align="center"   label="审核人" ></el-table-column>
        <el-table-column prop="approveTime" align="center" width="160" label="审核时间" ></el-table-column>
        <el-table-column  align="center" label="操作" fixed="right" width="90px">
          <template slot-scope="scope">
            <span  class="el-alink" @click="handleDetail(scope.$index, scope.row)">查看</span>
            <span  class="el-alink"   @click="handleEdit(scope.$index, scope.row)">编辑</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  import searchMerchant from 'components/common/search'
  export default {
    components: {ElRow, searchMerchant},
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置

        //form 表单字段
        formMarket: {
          endDate:'',            //结束日期
          eventCode:'',          //事件编码
          eventName:'',          //事件名称
          memberLevel:'',        //会员等级
          merchantCode:'',       //商户编码
          merchantName:'',       //商户名称
          offset:0,              //请求分页的offset
          startDate:'',          //开始日期
          status:'',             //状态
          storeCode:'',          //门店编码
          storeName:'',          //门店名称
          typeCode:'',           //事件类型编码
          typeName:'',           //事件类型名称
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
      }
    },
    methods: {
      // 可用门店选择
      querySearchAsyncStore(queryString, cb){
        var self=this;
        self.formMarket.storeCode=""
        if(!queryString){
          cb([])
          return
        }
        var url=merchantPostLink+"/merchant/admin/store/storeQueryList.htm";
        var postData={
          sysId:"T0000017",
          token:this.getCookie('token'),
          transactionUuid:this.uuid(),
          storeName:queryString,
          merchantCode:self.formMarket.merchantCode
        };
        this.$http.post(url, postData).then(function(res){
          res=res.data
          if(res.code=="S0A00000"){
            var list=[];
            res.data.forEach(function(e){
              if(e.storeName==queryString){
                self.formMarket.storeCode=e.storeCode;
              }
              list.push({value: e.storeName, code: e.storeCode})
            })
            cb(list)
          }
          else{
            cb([])
            self.formMarket.storeCode=""
          }
        })
      },
      setStoreInfo(item){
        this.formMarket.storeName = item.value
        this.formMarket.storeCode = item.code
      },
      setMerchantInfo(item){
        this.formMarket.merchantName = item.value   // form  value
        this.formMarket.merchantCode = item.id // form id
        this.formMarket.storeName = ""
        this.formMarket.storeCode = ""
      },
      // 钱包券新增
      handleAdd(){
        this.$router.push({path: '/home/incidentMarket/incidentMarketAdd'})
      },
      // 表单重置
      handleReset() {
        let formMarket={
          endDate:'',            //结束日期
          eventCode:'',          //事件编码
          eventName:'',          //事件名称
          memberLevel:'',        //会员等级
          merchantCode:'',       //商户编码
          merchantName:'',       //商户民称
          offset:0,              //请求分页的offset
          startDate:'',          //开始日期
          status:'',             //状态
          storeCode:'',          //门店编码
          storeName:'',          //门店名称
          typeCode:'',           //事件类型编码
          typeName:'',           //事件类型名称
        }
        this.formMarket=formMarket
      },
      // 表单查询
      handleInquire(offsetNum) {
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let dataUrl= incidentMarket+'/fin-rules-osprest/event/eventList.htm';
        let data={
          endDate:this.formMarket.endDate,              //结束日期
          eventCode:this.formMarket.eventCode,          //事件编码
          eventName:this.formMarket.eventName,          //事件名称
          memberLevel:this.formMarket.memberLevel,      //会员等级
          merchantCode:this.formMarket.merchantCode,    //商户编码
          offset:offsetNum,                             //请求分页的offset
          startDate:this.formMarket.startDate,          //开始日期
          status:this.formMarket.status,                //状态
          storeCode:this.formMarket.storeCode,          //门店编码
          typeCode:this.formMarket.typeCode,            //事件类型编码
          typeName:this.formMarket.typeName,            //事件类型名称
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //表格 查看  编辑  禁用 操作
      handleDetail(index, row){
        this.$router.push({path: '/home/incidentMarket/incidentMarketDetail', query: {id: row.id}})
      },
      handleEdit(index, row) {
        this.$router.push({path: '/home/incidentMarket/incidentMarketEdit', query: {id: row.id}})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



