<template>
  <div class="crm-main" >
    <el-form :inline="true" :model="formCoupon" id="formwrapper"  class="demo-form-inline crm-form" label-width="96px">
      <el-row>
        <el-form-item label="券编码">
          <el-input v-model="formCoupon.couponCode" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="券类型名称">
          <el-input v-model="formCoupon.couponName" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="券绑定支付支付ID" label-width="140px">
          <el-input v-model="formCoupon.userId" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="券类型编码">
          <el-input v-model="formCoupon.couponTypeCode" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="关联事件编号">
          <el-input v-model="formCoupon.eventCode" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="关联使用支付订单号" label-width="140px">
          <el-input v-model="formCoupon.orderNo" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="状态">
          <el-select v-model="formCoupon.status" class="el-winps" >
            <el-option label="全部" value="">全部</el-option>
            <el-option label="有效" value="VALID">有效</el-option>
            <el-option label="冻结" value="FROZEN">冻结</el-option>
            <el-option label="已经使用" value="USED">已经使用</el-option>
            <el-option label="作废" value="INVALID">作废</el-option>
            <el-option label="过期" value="OVERDUE">过期</el-option>
          </el-select>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw el-marl" @click="handleDownload" :loading="downloadLoading">导出发券模板</el-button>

          <div  class="el-upload el-upload--picture el-relative">
            <el-button type="primary" class="el-btnw el-marl10"><span>上传发券</span></el-button>
            <input type="file" name="file" v-if="clearShow"  :value="formCoupon.uploadFile" class="el-file-input"  @change="uploadFileExcel">
          </div>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>

      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" fit border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="couponCode" align="center" label="券编码"></el-table-column>
        <el-table-column prop="couponTypeCode" align="center" label="券类型编码"></el-table-column>
        <el-table-column prop="couponName" align="center" label="券类型名称" ></el-table-column>
        <el-table-column prop="couponCategory" align="center" label="券类型类别" ></el-table-column>
        <el-table-column prop="userId" align="center" label="关联支付用户" ></el-table-column>
        <el-table-column prop="eventCode" align="center" label="关联事件编号" ></el-table-column>
        <el-table-column prop="status" align="center" label="状态" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='VALID'">有效</span>
            <span v-if="scope.row.status=='INVALID'">作废</span>
            <span v-if="scope.row.status=='FROZEN'">冻结</span>
            <span v-if="scope.row.status=='USED'">已使用</span>
            <span v-if="scope.row.status=='OVERDUE'">过期</span>
          </template>
        </el-table-column>
        <el-table-column prop="validDate" align="center" label="有效期区间" ></el-table-column>
        <el-table-column prop="orderNo" align="center" label="关联支付订单" ></el-table-column>
        <el-table-column prop="useTime" align="center" label="使用时间" ></el-table-column>
        <el-table-column prop="createTime" align="center" label="创建时间" ></el-table-column>
        <el-table-column align="center" label="操作" >
          <template slot-scope="scope">
            <span  class="el-alink" v-if="scope.row.status=='VALID'" @click="handleVoid(scope.$index, scope.row)">作废</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination" >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size="pagination.pageSize"
          :total="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  export default {
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置
        clearShow:true,
        //form 表单字段
        formCoupon: {
          couponCode:'',     //券编码
          couponName:'',     //券名称
          couponTypeCode:'', //券类型编码
          eventCode:'',      //关联事件编码
          orderNo:'',        //支付订单号
          status:'',         //状态
          userId: '',        //支付用户id
          uploadFile:'',
          referNo:''        //异步处理业务流水号
        },
        //表格字段定义
        inquiryTable:{
          couponCode:'',         //券编码
          couponTypeCode:'',     //券类型编码
          couponName:'',         //券名称
          couponCategory:'',     //券类型
          userId:'',       //关联支付用户
          eventCode:'',       //关联事件编号
          status:'',              //状态
          validDate:'',  //券有效期区间
          orderNo:'',   //关联使用支付订单号
          useTime:'',            //使用时间
          createTime:'',          //创建时间
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
        downloadLoading:false,
        fileList:[],
      }
    },
    methods: {
      //上传发券
      uploadFileExcel(event){
        event.preventDefault();
        this.clearShow=false;
        let formData = new FormData();
        formData.append('file', event.target.files[0] || event.dataTransfer.files[0]);//文件对象
        formData.append('token',this.getCookie('token'));//token
        formData.append('transactionUuid',this.uuid());//uuid
        let dataUrl= discountPostLink+'/fin-rules-osprest/manualcoupon/upload.htm';
        let config = {headers: {'Content-Type': 'multipart/form-data'}}
        this.$http.post(dataUrl, formData, config).then((response)=>{
          this.clearShow=true;
          if(response.data.code === 'S0A00000'){
            this.$message({showClose: true,duration:2000,type: 'success',message:response.data.msg});
          }
          else if(response.data.code === 'EF6B0004'){
            this.formCoupon.referNo=response.data.msg;
            this.handleInquireResult(response.data.msg);
          }
          else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      // 导出表格excel文件
      handleDownload() {
        window.location.href='../../../../static/manual_coupons_tmplate.xlsx';
      },
      handleReset() {
        let formCoupon={
          couponCode:'',     //券编码
          couponName:'',     //券名称
          couponTypeCode:'', //券类型编码
          eventCode:'',      //关联事件编码
          orderNo:'',        //支付订单号
          status:'',         //状态
          userId: '',        //支付用户id
        }
        this.formCoupon=formCoupon
      },
      // 表单查询
      handleInquire(offsetNum) {
        let dataUrl= couponPostLink+'/fin-rules-osprest/userCoupon/userCouponList.htm';
        let data = {
          couponCode:this.formCoupon.couponCode,     //券编码
          couponName:this.formCoupon.couponName,     //券名称
          couponTypeCode:this.formCoupon.couponTypeCode, //券类型编码
          offset:offsetNum,
          eventCode:this.formCoupon.eventCode,      //关联事件编码
          orderNo:this.formCoupon.orderNo,        //支付订单号
          status:this.formCoupon.status,         //状态
          userId:this.formCoupon.userId,        //支付用户id
        }
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let params={data:data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
           this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //批量发券结果查询 失败轮询一次
      handleInquireResult(referNo){
        var that=this;
        let dataUrl= discountPostLink+'/fin-rules-osprest/manualcoupon/result.htm';
        let data = {
          referNo:referNo,
        }
        let params = {'data': data, 'transactionUuid': '1999000000001'}
        that.$http.post(dataUrl, params).then((response)=>{
          console.log(response.data.code)
          console.log(response)
            if(response.data.code=='EF6B0004'){
               setTimeout(function(){
                that.handleInquireResult(referNo);
                that.$message({showClose: true,duration:2000,type: 'success',message:response.data.msg});
              },5000);
            }else if(response.data.code==='S0A00000'){
                that.$message({showClose: true,duration:2000,type: 'success',message:response.data.msg});
                return;
            }
        })
      },
      //全作废操作
      handleVoid(index,row){
        this.$confirm('是否作废券?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
          let data={'id':row.id,'status':row.status}     //ruleCode
          let dataUrl= couponPostLink+'/fin-rules-osprest/userCoupon/userCouponInvalid.htm';
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              row.status='INVALID'
            }else{
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }).catch(() => {
          this.$message({showClose: true,duration:2000,type: 'info',message:'取消作废券操作！'});
        });
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



