<template>
  <div class="crm-main" >
    <el-form :inline="true" :model="billFrom" id="formwrapper"  class="demo-form-inline crm-form" label-width="90px">
      <el-row>
        <el-form-item label="支持渠道">
          <el-select v-model="billFrom.billChannelCodes" class="el-winps" filterable placeholder="请选择">
            <el-option v-for="item in channelList"  :key="item.index"  :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="对账状态">
          <el-select v-model="billFrom.status" class="el-winps" placeholder=" ">
            <el-option label="有差异" value="00"></el-option>
            <el-option label="已匹配" value="01"></el-option>
            <el-option label="未匹配" value="02"></el-option>
            <el-option label="已提交" value="03"></el-option>
          </el-select>
        </el-form-item>
      </el-row>
        <el-row>
        <el-form-item label="对账日期起" >
          <el-date-picker v-model="billFrom.beginTradeDate"   class="el-winps"  type="date" :editable="false" placeholder="对账日期起"></el-date-picker>
        </el-form-item>
        <el-form-item label="对账日期止" >
          <el-date-picker v-model="billFrom.endTradeDate"    class="el-winps"  type="date" :editable="false" placeholder="对账日期止"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="el-btnw" icon="el-icon-search"  @click="handleInquire(0)">查询</el-button>
          <el-button class="btn-default el-btnw "   icon="el-icon-refresh" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <el-row>
      <el-table :data="billData" :fit="true" border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="tradeNo" align="center" label="支付流水号" width="100"></el-table-column>
        <el-table-column prop="crtAmount" align="center" label="支付金额(元)" width="120"></el-table-column>
        <el-table-column prop="tradeTime" align="center" :formatter="tradeTime" label="交易日期" width="110"></el-table-column>
        <el-table-column prop="realPayChannelNameZh" align="center"  label="支付渠道" width="80"></el-table-column>
        <el-table-column prop="tradeType" align="center"  label="交易类型" width="80"></el-table-column>
        <el-table-column prop="channelTradeSn" align="center" label="第三方支付流水号" width="160"></el-table-column>
        <el-table-column prop="channelAmount" align="center" label="第三方支付金额(元)" width="160"></el-table-column>
        <el-table-column prop="channelTradeDate" align="center"  label="第三方交易日期" width="160"></el-table-column>
        <el-table-column prop="dsfFile" align="center" label="第三方对账文件名" width="160"></el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {ElRow},
    name: 'crmMenu',
    data () {
      return {
        wrapperHeight:'',
        billFrom: {
          billChannelCodes:[],                   //对账渠道编码
          beginTradeDate:(Date.now()- 86400000), //对账日期起
          endTradeDate:Date.now(),               //对账日期止
          status:'01',                             //对账状态
          offset:0,                              //起始偏移量
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:30,
          total:0
        },
        billData: [],
        channelList:[],
      }
    },
    methods: {
      //格式化导入时间
      tradeTime(row, column){
        var tradeTime='';
        if(row.tradeTime){
          tradeTime=this.filterDate(row.tradeTime,'YMDHMS');
        }
        return tradeTime;
      },
  /*    //格式化时间
      channelTradeDate(row, column){
        var channelTradeDate='';
        if(row.channelTradeDate){
          channelTradeDate=this.filterDate(row.channelTradeDate,'YMD');
        }
        return channelTradeDate;
      },*/
      loadSearchRoad() {
        let dataUrl= billLink+'/payment/bill/rel/pay/channel/list.htm';
        this.$http.post(dataUrl).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            response.data.data.forEach((result,index) => {
              this.channelList.push({id:index,label: result.billChannelName,value: result.billChannelCodes})
              if(result.billChannelName=='华润银行'){
                this.billFrom.billChannelCodes=result.billChannelCodes; //对账渠道编码
              }
            })
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      // 表单查询
      handleInquire(offsetNum) {
       if(!this.billFrom.beginTradeDate){
          this.$message({showClose: true,duration:2000,type: 'error',message:'请输入对账日期起！'});
          return
        }
        if(!this.billFrom.endTradeDate){
          this.$message({showClose: true,duration:2000,type: 'error',message:'请输入对账日期止！'});
          return
        }
        if(this.billFrom.beginTradeDate>=this.billFrom.endTradeDate){
          this.$message({showClose: true,duration:2000,type: 'error',message:'请对账日期起必须小于对账日期止！'});
          return
        }
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let dataUrl= billLink+'/payment/bill/rel/list.htm';
        var beginTradeDate=this.filterDateStr(this.billFrom.beginTradeDate,10);
        var endTradeDate=this.filterDateStr(this.billFrom.endTradeDate,10);
        let params={
          billChannelCodes:this.billFrom.billChannelCodes, //对账渠道编码
          beginTradeDate:beginTradeDate,  //对账日期起
          endTradeDate:endTradeDate,      //对账日期止
          status:this.billFrom.status,                                    //对账状态
          offset:offsetNum,                              //起始偏移量
          sysId:'',
          'token':this.getCookie('token'), //token
          'transactionUuid':this.uuid()    //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            this.billData =response.data.data
            this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
          }else{
            this.billData =[];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      // 表单重置
      handleReset() {
        let billFrom={
          billChannelCodes:[],                   //对账渠道编码
          beginTradeDate:'', //对账日期起
          endTradeDate:'',               //对账日期止
          status:'',                             //对账状态
          offset:0,                              //起始偏移量
        }
        this.billFrom=billFrom
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*30);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.loadSearchRoad();
      var that=this;
      setTimeout(function(){that.handleInquire(0);},1000)
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-document.getElementById('pagination').clientHeight-100;
    }
  }
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
</style>



