import Vue from 'vue'
import Router from 'vue-router'

// 路由实例化
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component:  require('views/base/login.vue'), // 登陆界面
    },
    {
      path: '/modifyPsd',
      name: 'modifyPsd',
      component:  require('views/base/modifyPsd.vue'), // 修改密码
    },
    {
      path: '/register',
      name: 'register',
      component:  require('views/base/register.vue'), // 注册界面
    },
    {
      path: '/home',
      name: 'home',
      component: require('views/home/home.vue'), // 异步加载组件
      children: [
        {
          path: '/home/table',
          name: 'table',
          component: require('components/table/formTable.vue'), // 异步加载组件
        },
        {
          path: '/home/charts',
          name: 'charts',
          component: require('components/table/charts.vue'), // 异步加载组件
        },
        {
          path: '/home/mapDemo',
          name: 'mapDemo',
          component: require('components/table/mapDemo.vue'), // 地图组件案例
        },
        {
          path: '/home/main',
          name: '功能模块',
          component:  require('views/base/main.vue'), // 后天主页面
        },
        {
          path: '/home/walletUser',
          name: '钱包用户',
          component: require('components/pages/walletManage/walletUser.vue'), // 钱包用户
        },
        {
          path: '/home/walletUser/walletUserDetail',
          name: '钱包用户详情',
          component: require('components/pages/walletManage/walletUserDetail.vue'), // 钱包用户详情
        },
        {
          path: '/home/payTradeList',
          name: '支付交易',
          component: require('components/pages/transactionDetails/payTradeList.vue'), // 支付交易
        },
        {
          path: '/home/payTradeList/payTradeDetail',
          name: '支付交易详情',
          component: require('components/pages/transactionDetails/payTradeDetail.vue'), // 支付交易详情
        },
        {
          path: '/home/refundTradeList',
          name: '退款交易',
          component: require('components/pages/transactionDetails/refundTradeList.vue'), // 退款交易
        },
        {
          path: '/home/refundTradeList/refundTradeDetail',
          name: '退款交易详情',
          component: require('components/pages/transactionDetails/refundTradeDetail.vue'), // 退款交易详情
        },
        {
          path: '/home/walletTradeList',
          name: '钱包交易',
          component: require('components/pages/transactionDetails/walletTradeList.vue'), // 钱包交易
        },
        {
          path: '/home/accountInquiry',
          name: '结算单查询',
          component: require('components/pages/accountSettlement/accountInquiry.vue'), // 结算单查询
        },
        {
          path: '/home/accountInquiry/accountInquiryDetail',
          name: '结算单详情',
          component: require('components/pages/accountSettlement/accountInquiryDetail.vue'), // 结算单详情
        },
        {
          path: '/home/incidentType',
          name: '事件类型',
          component: require('components/pages/incidentMarket/incidentType.vue'), // 事件类型
        },
        {
          path: '/home/couponType',
          name: '钱包券类型管理',
          component: require('components/pages/couponManage/couponType.vue'), // 钱包券类型管理
        },
        {
          path: '/home/couponType/couponTypeAdd',
          name: '钱包券类型新增',
          component: require('components/pages/couponManage/couponTypeAdd.vue'), // 钱包券类型新增
        },
        {
          path: '/home/couponType/couponTypeView',
          name: '钱包券预览',
          component: require('components/pages/couponManage/couponTypeView.vue'), // 钱包券预览
        },
        {
          path: '/home/couponType/couponTypeEdit',
          name: '钱包券类型编辑',
          component: require('components/pages/couponManage/couponTypeEdit.vue'), // 钱包券类型编辑
        },
        {
          path: '/home/couponType/couponTypeDetail',
          name: '钱包券类型详情',
          component: require('components/pages/couponManage/couponTypeDetail.vue'), // 钱包券类型详情
        },
        {
          path: '/home/couponInfomation',
          name: '钱包券信息明细',
          component: require('components/pages/couponInfomation/couponInfomation.vue'), // 钱包券信息明细
        },
        {
          path: '/home/incidentMarket',
          name: '营销事件',
          component: require('components/pages/incidentMarket/incidentMarket.vue'), // 营销事件
        },
        {
          path: '/home/incidentMarket/incidentMarketAdd',
          name: '营销事件新增',
          component: require('components/pages/incidentMarket/incidentMarketAdd.vue'), // 营销事件添加
        },
        {
          path: '/home/incidentMarket/incidentMarketDetail',
          name: '营销事件详情',
          component: require('components/pages/incidentMarket/incidentMarketDetail.vue'), // 营销事件详情
        },
        {
          path: '/home/incidentMarket/incidentMarketEdit',
          name: '营销事件编辑',
          component: require('components/pages/incidentMarket/incidentMarketEdit.vue'), // 营销事件编辑
        },
        {
          path: '/home/discountRules',
          name: '折扣规则',
          component: require('components/pages/discountRules/discountRules.vue'), // 折扣规则
        },
        {
          path: '/home/discountRules/discountRulesAdd',
          name: '折扣规则新增',
          component: require('components/pages/discountRules/discountRulesAdd.vue'), // 折扣规则新增
        },
        {
          path: '/home/discountRules/discountRulesDetail',
          name: '折扣规则详情',
          component: require('components/pages/discountRules/discountRulesDetail.vue'), // 折扣规则详情
        },
        {
          path: '/home/discountRules/discountRulesEdit',
          name: '折扣规则编辑',
          component: require('components/pages/discountRules/discountRulesEdit.vue'), // 折扣规则编辑
        },
        {
          path: '/home/billMatch',
          name: '对账匹配',
          component: require('components/pages/billMatch/billMatch.vue'), // 对账匹配
        },
        {
          path: '/home/billMatchFile/billMatchFileDetail',
          name: '对账文件详情',
          component: require('components/pages/billMatch/billMatchFileDetail.vue'), // 对账文件详情
        },{
          path: '/home/billMatchFile',
          name: '对账文件',
          component: require('components/pages/billMatch/billMatchFile.vue'), // 对账文件
        }
      ]
    },
    {
      path: '/',
      redirect: '/login'// 重定向
    }
  ]
})
export default router



