<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formWalletUser" id="formwrapper"  class="demo-form-inline crm-form" label-width="96px">
      <el-row>
        <el-form-item label="用户编号">
          <el-input v-model="formWalletUser.userId" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="身份证号">
          <el-input v-model="formWalletUser.md5CertNo" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="手机号码">
          <el-input v-model="formWalletUser.userMobile" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="银行卡号">
          <el-input v-model="formWalletUser.md5CardNo" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="会员编号">
          <el-input v-model="formWalletUser.memberId" class="el-winps" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>
      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" :fit="true" border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="userId" align="center" label="用户编号"></el-table-column>
        <el-table-column prop="status" align="center"  label="状态" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='ACTIVE'">解冻</span>
            <span v-if="scope.row.status=='FROZEN'">冻结</span>
            <span v-if="scope.row.status=='CANCEL'">注销</span>
          </template>
        </el-table-column>
        <el-table-column prop="userName" align="center" label="姓名" ></el-table-column>
        <el-table-column prop="md5CertNo" align="center" label="身份证号码" ></el-table-column>
        <el-table-column prop="userMobile" align="center" label="手机号码" ></el-table-column>
        <el-table-column prop="memberId" align="center" label="会员编号" ></el-table-column>
        <el-table-column  align="center" label="操作" fixed="right" width="120px">
          <template slot-scope="scope">
            <span  class="el-alink" @click="handleDetail(scope.$index,scope.row)">查看</span>
            <span  class="el-alink" v-if="scope.row.status=='FROZEN'" @click="handleUnFreeze(scope.$index, scope.row)">解冻</span>
            <span  class="el-alink"v-if="scope.row.status=='ACTIVE'" @click="handleFreeze(scope.$index, scope.row)">冻结</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
    <el-dialog :title="formWalletUser.title" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
         <div>

            <el-input type="textarea"  class="el-winpTl el-minh el-emI" v-model="formWalletUser.reason"></el-input>

         </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
    </el-dialog>
  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {ElRow},
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置
        dialogVisible:false,     //解冻dialog
        //form 表单字段
        formWalletUser: {
          md5CardNo:'',      //银行卡号
          md5CertNo:'',      //身份证号
          memberId	:'',     //会员编号
          offset:0,          //请求分页的offset
          userId:'',         //支付用户id
          userMobile:'',     //注册手机号
          reason:'',         //原因
          title:'',          //解冻  冻结原因标题
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
      }
    },
    methods: {
      // 表单重置
      handleReset() {
        let formWalletUser={
          md5CardNo:'',      //银行卡号
          md5CertNo:'',      //身份证号
          memberId	:'',     //会员编号
          offset:0,          //请求分页的offset
          userId:'',         //支付用户id
          userMobile:'',     //注册手机号
        }
        this.formWalletUser=formWalletUser
      },
      // 表单查询
      handleInquire(offsetNum) {
        let dataUrl= walletUser+'/fin-account-osprest/account/queryList.htm';
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let data={
          md5CardNo:this.formWalletUser.md5CardNo,      //银行卡号
          md5CertNo:this.formWalletUser.md5CertNo,      //身份证号
          memberId:this.formWalletUser.memberId,        //会员编号
          offset:0,                                     //请求分页的offset
          userId:this.formWalletUser.userId,            //支付用户id
          userMobile:this.formWalletUser.userMobile,    //注册手机号
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //表格 查看  编辑  禁用 操作
      handleDetail(index, row){
        this.$router.push({path: '/home/walletUser/walletUserDetail', query: {userId: row.userId}})
      },
      //表格禁用启用设置
      handleFreeze(index, row) {
        console.log(row)
         if(!row.userId){return}
          this.$confirm('是否冻结?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'userId': row.userId}     //券id
            let dataUrl = walletUser +'/fin-account-osprest/account/userFrozen.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              console.log(response)
              if (response.data.code === 'S0A00000') {
                  row.status = 'FROZEN'
              } else {
                this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
              }
            })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消启用操作！'});
          });
      },
      handleUnFreeze(index, row) {
        if(!row.userId){return}
          this.$confirm('是否解冻?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'userId': row.userId}     //券id
            let dataUrl = walletUser +'/fin-account-osprest/account/userUnFrozen.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              console.log(response)
              if (response.data.code === 'S0A00000') {
                row.status = 'ACTIVE'
              } else {
                this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
              }
            })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消解冻操作！'});
          });
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



