<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formCoupon" id="formwrapper" :rules="ruleValidate" ref="formCoupon"  class="demo-form-inline crm-form" label-width="95px">

      <el-row>

        <div class="el-card-title">钱包券基本信息</div>

        <el-form-item label="券类型编号" prop="couponTypeCode">
          <el-input v-model="formCoupon.couponTypeCode"  placeholder=" " readonly="readonly"></el-input>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="券类型名称" prop="couponName">
          <el-input v-model="formCoupon.couponName"   class="el-winpl"  placeholder=" "></el-input>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="券发行数量" prop="couponRadio">
            <el-radio  @change="emptyData('couponRadio')" v-model="formCoupon.couponRadio" label="1">不限制</el-radio>
            <el-radio  @change="emptyData('couponRadio')" v-model="formCoupon.couponRadio" label="2">限制</el-radio>
        </el-form-item>

        <el-form-item label="最大发行张数" label-width="110px" v-if="formCoupon.couponRadio=='2'" prop="issueCount">
          <el-input v-model="formCoupon.issueCount" class="el-winpMin" placeholder=" "></el-input>
          <span class="el-span">张</span>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="券类型额度"  prop="couponCategory">
          <el-radio-group v-model="formCoupon.couponCategory"  @change="emptyData('couponCategory')"  disabled="disabled">
            <el-radio label="MONEYOFF"  >满减券</el-radio>
            <el-radio label="DISCOUNT" >折扣券</el-radio>
            <el-radio label="CASH"  >现金券</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="订单金额(元)" label-width="110px" prop="couponMinOrderAmt"   v-show="formCoupon.couponCategory=='MONEYOFF'">
          <el-input v-model="formCoupon.couponMinOrderAmt" @input="couponOrderAmt('min')" class="el-winpMin" placeholder="大于0"></el-input>
        </el-form-item>
        <el-form-item label="减" class="el-labelMinw" label-width="40px"  prop="couponMaxOrderAmt"  v-show="formCoupon.couponCategory=='MONEYOFF'">
          <el-input v-model="formCoupon.couponMaxOrderAmt" @input="couponOrderAmt('max')" class="el-winpMin" placeholder="大于0"></el-input>
        </el-form-item>
        <el-form-item label="优惠比例(%)" label-width="100px"  v-show="formCoupon.couponCategory=='DISCOUNT'" prop="couponDiscountRate">
          <el-input  v-model="formCoupon.couponDiscountRate" @input="discountRate" class="el-winpMin" placeholder="大于0"></el-input>
          <span class="el-point" v-if="formCoupon.couponDiscountRate">{{formCoupon.couponDiscountTrueRate}}折</span>
        </el-form-item>
        <el-form-item label="优惠限额(元)"  v-show="formCoupon.couponCategory=='DISCOUNT'" prop="couponDiscountLimit">
          <el-input v-model="formCoupon.couponDiscountLimit" class="el-winpMin" placeholder="大于0"></el-input>
          <span class="el-point">字段为空不填则为不限额</span>
        </el-form-item>

        <el-form-item label="面值(元)"  v-show="formCoupon.couponCategory=='CASH'" label-width="40px" prop="couponFaceAmt">
          <el-input  v-model="formCoupon.couponFaceAmt"  class="el-winpMin" placeholder="大于0"></el-input>
        </el-form-item>
      </el-row>


      <el-row>

        <el-form-item label="券有效期"  prop="validDateType">
          <el-radio-group v-model="formCoupon.validDateType" @change="emptyData('validDateType')" >
            <el-radio label="POSTPONED" >顺延有效期</el-radio>
            <el-radio label="ENDFIXED" >固定失效期</el-radio>
            <el-radio label="RANGEFIXED"  >固定生效区间</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="领取后"  v-show="formCoupon.validDateType=='POSTPONED'" label-width="50px" prop="postponedDay">
          <el-input v-model="formCoupon.postponedDay" class="el-winpMin" placeholder=""></el-input>
          <span class="el-span">天</span>
        </el-form-item>

        <el-form-item label="失效时间"  v-show="formCoupon.validDateType=='ENDFIXED'" label-width="50px" >
          <el-select v-model="formCoupon.validDateEnd"  class="el-DateMin"  @change="emptyData('validDateEnd')">
            <el-option label="时间段" value="datetime"></el-option>
            <el-option label="天" value="date"></el-option>
          </el-select>
          <el-form-item v-if="formCoupon.validDateEnd=='datetime'" prop="endfixedDatetime">
            <el-date-picker v-model="formCoupon.endfixedDatetime" :editable="false" :picker-options="startPickerOptions" class="el-DateTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item v-if="formCoupon.validDateEnd=='date'" prop="endfixedDate">
            <el-date-picker v-model="formCoupon.endfixedDate" :editable="false" :picker-options="startPickerOptions" class="el-Date" type="date" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
        </el-form-item>

        <el-form-item>
          <el-select v-model="formCoupon.validDateRang" v-show="formCoupon.validDateType=='RANGEFIXED'" class="el-DateMin" @change="emptyData('validDateRang')" >
            <el-option label="时间段" value="datetime"></el-option>
            <el-option label="天" value="date"></el-option>
          </el-select>
          <span v-if="formCoupon.validDateRang=='datetime'">
            <el-form-item label="生效时间"  v-show="formCoupon.validDateType=='RANGEFIXED'" label-width="50px" prop="formatedStartDatetime">
              <el-date-picker v-model="formCoupon.formatedStartDatetime" :editable="false" class="el-DateTime" :picker-options="startPickerOptions" type="datetime" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>

            <el-form-item label="失效时间"  v-show="formCoupon.validDateType=='RANGEFIXED'" label-width="50px" prop="formatedEndDatetime">
              <el-date-picker v-model="formCoupon.formatedEndDatetime" :editable="false" class="el-DateTime" :picker-options="endPickerOptions" type="datetime" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
          </span>
          <span v-if="formCoupon.validDateRang=='date'">
            <el-form-item label="生效时间"  v-show="formCoupon.validDateType=='RANGEFIXED'" label-width="50px" prop="formatedStartDate">
              <el-date-picker v-model="formCoupon.formatedStartDate" :editable="false" class="el-Date" :picker-options="startPickerOptions" type="date" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>

            <el-form-item label="失效时间"  v-show="formCoupon.validDateType=='RANGEFIXED'" label-width="50px" prop="formatedEndDate">
              <el-date-picker v-model="formCoupon.formatedEndDate" :editable="false" class="el-Date" :picker-options="endPickerOptions" type="date" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
          </span>
        </el-form-item>
      </el-row>

      <el-row>
        <div class="el-card-title">钱包券使用规则</div>

        <el-form-item label="商户/门店"  prop="merchantStoreFlag">
           <el-radio @change="emptyData('couponUseRadio')" v-model="formCoupon.couponUseRadio" label="1">全部可用</el-radio>
           <el-radio @change="emptyData('couponUseRadio')" v-model="formCoupon.couponUseRadio" label="2">部分可用</el-radio>
        </el-form-item>

        <el-form-item  v-if="formCoupon.couponUseRadio=='2'" >

          <el-button type="primary" v-if="formCoupon.status=='VALID'" class="el-btnw  el-marl" @click="setStoreDown"><i class="el-icon-download"></i>门店批量下载</el-button>

          <el-button type="primary" class="el-btnw  el-marl" @click="downLoadFile"><i class="el-icon-download"></i>下载模板</el-button>

          <div  class="el-upload el-upload--picture el-relative">
            <el-button type="primary" class="el-btnw el-marl10" :disabled="!formCoupon.couponCategory" ><span>文件导入</span></el-button>
            <input type="file" name="file"  v-if="clearShow" :value="formCoupon.uploadFile" class="el-file-input"  @change="uploadFileExcel">
          </div>

          <el-autocomplete class="el-input el-winp el-marl10 " v-model="formCoupon.queryStore" :fetch-suggestions="queryStoreSearch" placeholder="请输入门店编码" @select="handleStoreSelect"></el-autocomplete>

        </el-form-item>

        <el-form-item  label="上传导入详情" v-if="formCoupon.couponUseRadio=='2'" >

          <el-table :data="formCoupon.infoData"  border  style="width: 100%">
            <el-table-column prop="merchantCode" align="center" width="210" label="商户编码"></el-table-column>
            <el-table-column prop="storeCode" align="center" width="180" label="门店编码" ></el-table-column>
            <el-table-column prop="storeScope" align="center" width="120" label="全部和部分" v-if="display" ></el-table-column>
          </el-table>

        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="支付方式" prop="payFlag">
            <el-radio @change="emptyData('couponPayRadio')" v-model="formCoupon.couponPayRadio" label="1">全部可用</el-radio>
            <el-radio @change="emptyData('couponPayRadio')" v-model="formCoupon.couponPayRadio" label="2">部分可用</el-radio>
        </el-form-item>

        <el-row class="el-marl210">
          <el-form-item  prop="payBankList">
            <el-checkbox-group v-model="formCoupon.payBankList" v-if="formCoupon.couponPayRadio=='2'" >
              <el-row>
                <el-checkbox label="BALANCE" >润钱包账户余额</el-checkbox>
              </el-row>
              <el-row>
                <el-checkbox label="D" @change="payCard('D')">存蓄卡支付</el-checkbox>
                <div class="payDepositList"  v-show="formCoupon.checkedDepositList[1]" ><span class="el-span"  v-for="checked in formCoupon.checkedDepositList" >{{checked.bankName}}</span></div>
                <div><span class="el-span el-blue el-cursor"  v-if="formCoupon.payDeposit" @click="addBankCard('D')" >设置指定银行卡+</span></div>
              </el-row>
              <el-row>
                <el-checkbox label="C" @change="payCard('C')">信用卡支付</el-checkbox>
                <div class="payCreditList"  v-show="formCoupon.checkedCreditList[1]"><span class="el-span" v-for="checked in formCoupon.checkedCreditList">{{checked.bankName}}</span></div>
                <div><span class="el-span el-blue el-cursor" v-if="formCoupon.payCredit" @click="addBankCard('C')">设置指定银行卡+</span></div>
              </el-row>
              <el-row>
                <el-checkbox label="CRTXDFENQI" >小贷支付</el-checkbox>
              </el-row>
            </el-checkbox-group>
          </el-form-item>
        </el-row>
      </el-row>

      <el-row>

        <div class="el-card-title">券成本分摊规则</div>
        <el-row>
          <el-form-item label="分摊商户" prop="shareMerchantName">
            <search-Merchant v-on:communicate="setMerchantShare" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formCoupon.shareMerchantName"></search-Merchant>
          </el-form-item>

          <el-button type="primary" class="el-btnw  el-marb20"  @click="shareAdd"><i class="el-icon-plus"></i> 新增</el-button>
        </el-row>
        <el-form-item label="分摊方设置" >

          <el-table :data="formCoupon.shareList"  border  style="width: 100%">

            <el-table-column   prop="shareMerchantCode"  align="center" width="240" label="分摊方商户编号">
              {{formCoupon.shareMerchantCode}}
            </el-table-column>
            <el-table-column prop="shareMerchantName" align="center" width="240" label="分摊方商户名" >
              {{formCoupon.shareMerchantName}}
            </el-table-column>
            <el-table-column prop="shareRate" align="center" width="100" label="分摊比例%" >
              <template slot-scope="scope">
                <el-input v-model="scope.row.shareRate"  ></el-input>
              </template>
            </el-table-column>

            <el-table-column  align="center" width="220" label="操作">
              <template slot-scope="scope">
               <el-button size="small" type="danger" @click="setDelete(scope.$index, scope.row)"  >删除</el-button>
              </template>
            </el-table-column>

          </el-table>

        </el-form-item>

      </el-row>

      <el-row>

        <div class="el-card-title">钱包券展示信息</div>

        <el-form-item label="优惠券样式"  prop="uploadImage">
          <span class="el-point">为保证展示效果请上传，格式为jpg.png的图片</span>
          <el-row>
              <ul class="el-upload-list el-upload-list--picture-card">
                <li  class="el-upload-list__item is-success" v-if="uploadShow">
                  <img :src="uploadImage" class="el-upload-list__item-thumbnail" />
                  <label class="el-upload-list__item-status-label"><i class="el-icon-upload-success el-icon-check"></i></label>
                  <i class="el-icon-close"></i>
                  <i class="el-icon-close-tip">按 delete 键可删除</i>
                  <span class="el-upload-list__item-actions"><span class="el-upload-list__item-delete" @click="deleteImage"><i class="el-icon-delete"></i></span></span>
                </li>
              </ul>
              <div  class="el-upload el-upload--picture-card el-relative">
                <i  class="el-icon-plus"></i>
                <input type="file" name="file" accept="image/jpeg,image/jpg,image/png" class=" el-file-input" @change="uploadImages">
              </div>
          </el-row>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="券说明" prop="extRemark">
          <quill-editor ref="myTextEditor" class="el-editor"  v-model="formCoupon.extRemark"  @change="onEditorChange($event)" :config="formCoupon.editorOption"></quill-editor>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="操作备注">

          <el-input type="textarea"  class="el-winpTl el-minh" v-model="formCoupon.operateRemark"></el-input>

        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="钱包券预览">

          <div class="previewMain">

            <div class="previewBox">

              <div class="previewList" v-show="previewList">
                <div class="ln-header"><i class="ln-goback" ></i>钱包优惠券</div>
                <div class="ln-wrapper">

                  <div class="couponRow"  @click="goPreviewDetail">
                    <div class="couponBox">
                      <span class="couponIcon"><img :src="formCoupon.logoUrl"/></span>
                      <div  class="couponMain">
                        <p class="couponTxt">{{formCoupon.couponName}}</p>
                        <p class="couponDate" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime" v-if="formCoupon.postponedDay"><i class="ln-gey">xxxx. xx . xx - xxxx . xx . xx</i></p>
                        <p class="couponDate" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.postponedDay" v-if="formCoupon.endfixedDate"><i class="ln-gey">xxxx . xx . xx - {{this.filterDateFrm(formCoupon.endfixedDate,'YMD')}}</i><i class="dateOver" v-if="formCoupon.willDue=='Y'">即将到期</i></p>
                        <p class="couponDate" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.postponedDay" v-if="formCoupon.endfixedDatetime"><i class="ln-gey">xxxx . xx . xx - {{this.filterDateFrm(formCoupon.endfixedDatetime,'YMD')}}</i><i class="dateOver" v-if="formCoupon.willDue=='Y'">即将到期</i></p>
                        <p class="couponDate" v-show="!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime&&!formCoupon.formatedStartDatetime&&!formCoupon.formatedEndDatetime&&!formCoupon.postponedDay" v-if="formCoupon.formatedStartDate||formCoupon.formatedEndDate"><i class="ln-gey">{{this.filterDateFrm(formCoupon.formatedStartDate,'YMD')}} - {{this.filterDateFrm(formCoupon.formatedEndDate,'YMD')}}</i><i class="dateOver" v-if="formCoupon.willDue=='Y'">即将到期</i></p>
                        <p class="couponDate" v-show="!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime&&!formCoupon.formatedStartDate&&!formCoupon.formatedEndDate&&!formCoupon.postponedDay" v-if="formCoupon.formatedStartDatetime ||formCoupon.formatedEndDatetime"><i class="ln-gey">{{this.filterDateFrm(formCoupon.formatedStartDatetime,'YMD')}} - {{this.filterDateFrm(formCoupon.formatedEndDatetime,'YMD')}}</i><i class="dateOver" v-if="formCoupon.willDue=='Y'">即将到期</i></p>
                      </div>
                      <span class="couponWorth ln-ac">
                              <p v-if="formCoupon.couponCategory=='MONEYOFF'">￥<i class="couponMoney">{{this.filterInter(formCoupon.couponMaxOrderAmt)}}</i></p>
                              <p v-if="formCoupon.couponCategory=='CASH'">￥<i class="couponMoney">{{this.filterInter(formCoupon.couponFaceAmt)}}</i></p>
                              <p v-if="formCoupon.couponCategory=='DISCOUNT'" class=" zk_martop"><i class="couponMoney">{{formCoupon.couponDiscountTrueRate}}</i> 折</p>
                              <p class="couponTip" v-if="formCoupon.couponCategory=='DISCOUNT'"><i></i></p>
                              <p class="couponTip" v-if="formCoupon.couponCategory=='CASH'"><i class="couponBtn">无门槛</i></p>
                              <p class="couponTip ln-mart20" v-if="formCoupon.couponMinOrderAmt&&formCoupon.couponCategory=='MONEYOFF'"><i class="couponMj">满{{this.filterInter(formCoupon.couponMinOrderAmt)}}可用</i></p>
                             </span>
                    </div>
                  </div>

                </div>
              </div>
              <div class="previewDetail" v-show="!previewList">
                <div class="ln-header"><i class="ln-goback"  @click="goPreviewList"></i>详情</div>

                <div class="ln-content">
                  <div class="ln-wrap">

                    <div class="coupon-header ln-ac">
                      <span class="couponIcon"><img :src="formCoupon.logoUrl"/></span>
                      <p class="couponTitle ln-ft45">{{formCoupon.couponName}}</p>
                      <p class="couponMoney ln-yellow" v-if="formCoupon.couponCategory=='MONEYOFF'">￥<i >{{this.filterInter(formCoupon.couponMaxOrderAmt)}}</i></p>
                      <p class="couponMoney ln-yellow" v-if="formCoupon.couponCategory=='CASH'">￥<i >{{this.filterInter(formCoupon.couponFaceAmt)}}</i></p>
                      <p class="couponMoney ln-yellow" v-if="formCoupon.couponCategory=='DISCOUNT'"><i>{{formCoupon.couponDiscountTrueRate}}</i>折</p>
                      <p class="couponMj ln-yellow" v-if="formCoupon.couponCategory=='MONEYOFF'">满{{this.filterInter(formCoupon.couponMinOrderAmt)}}可用</p>
                      <p class="couponTip" v-if="formCoupon.couponCategory=='CASH'"><i class="couponBtn">无门槛</i></p>
                      <p class="couponDate  ln-gey ln-ft36" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime" v-if="formCoupon.postponedDay">有效期：xxxx. xx . xx - xxxx . xx . xx</p>
                      <p class="couponDate  ln-gey ln-ft36" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.postponedDay" v-if="formCoupon.endfixedDate" >有效期：xxxx . xx . xx - {{this.filterDateFrm(formCoupon.endfixedDate,'YMD')}}</p>
                      <p class="couponDate  ln-gey ln-ft36" v-show="!formCoupon.formatedStartDate&&!formCoupon.formatedStartDatetime&&!formCoupon.postponedDay" v-if="formCoupon.endfixedDatetime" >有效期：xxxx . xx . xx xx : xx : xx - {{this.filterDateFrm(formCoupon.endfixedDatetime,'YMDHMS')}}</p>
                      <p class="couponDate  ln-gey ln-ft36" v-show="!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime&&!formCoupon.formatedStartDatetime&&!formCoupon.formatedEndDatetime&&!formCoupon.postponedDay" v-if="formCoupon.formatedStartDate||formCoupon.formatedEndDate">有效期：{{this.filterDateFrm(formCoupon.formatedStartDate,'YMD')}} - {{this.filterDateFrm(formCoupon.formatedEndDate,'YMD')}}</p>
                      <p class="couponDate  ln-gey ln-ft36" v-show="!formCoupon.endfixedDate&&!formCoupon.endfixedDatetime&&!formCoupon.formatedStartDate&&!formCoupon.formatedEndDate&&!formCoupon.postponedDay" v-if="formCoupon.formatedStartDatetime ||formCoupon.formatedEndDatetime">有效期：{{this.filterDateFrm(formCoupon.formatedStartDatetime,'YMDHMS')}} - {{this.filterDateFrm(formCoupon.formatedEndDatetime,'YMDHMS')}}</p>

                    </div>
                    <div class="coupon-main">
                      <img class="coupon-main-bg" src="../../../assets/images/coupons_info_bg.png"/>
                      <div class="coupon-con">

                       <div class="coupon-h1"><i class="coupon-boll"></i>该券将在使用润钱包付款时依据使用规则自动抵扣</div>
                        <div class="coupon-h2" v-if="formCoupon.couponCategory=='DISCOUNT'"><i class="coupon-boll"></i>折扣券：同订单满减券和折扣券仅能使用其中一张</div>
                        <div class="coupon-h2" v-if="formCoupon.couponCategory=='CASH'"><i class="coupon-boll"></i>现金券：在优惠折扣后使用，同订单可使用多张</div>
                        <div class="coupon-h2" v-if="formCoupon.couponCategory=='MONEYOFF'"><i class="coupon-boll"></i>满减券：同订单满减券和折扣券仅能使用其中一张</div>

                        <div class="coupon-line" >
                          <div class="coupon-row" id="couponText"  :class="formCoupon.moreStyle"  :style="{ height: formCoupon.curHeight + 'px' }" v-html="formCoupon.couponExt.remark"></div>
                          <div v-if="display" v-show="show" class="ln-linear"></div>
                        </div>
                        <div class="coupon-more ln-clearbox"  v-if="display" v-show="show" @click="moreLook"><span><i>查看全部</i><img src="../../../assets/images/collse.png"/></span></div>
                        <div class="coupon-copyright">活动最终解释权归华润网络(深圳)有限公司所有</div>
                      </div>

                    </div>
                  </div>
                </div>

              </div>

            </div>

          </div>

        </el-form-item>

      </el-row>

      <el-row  class="ln-ac">

          <el-button v-if="rowStatus=='NEW'" class="el-winpMin el-marr" type="primary" size="big" @click="couponTemporary">暂存</el-button>
          <el-button class="el-winpMin el-marr" type="success" size="big" @click="couponSubmit('formCoupon')">保存</el-button>
          <el-button type="info" class="el-winpMin"  size="big" @click="goContype">返回</el-button>

      </el-row>

    </el-form>
    <el-dialog title="选择支持的储蓄卡发行卡(可多选)" class="ln-bankList ln-ac"  width="360px" :visible.sync="dialogBankCard">
      <el-form  label-width="68px">

        <el-checkbox :indeterminate="formCoupon.isIndeterminate" v-model="formCoupon.checkAll" @change="handleCheckAllChange">全选</el-checkbox>

        <el-checkbox-group v-for="(card , index) in formCoupon.cardList" :key ="card.id"  v-model="formCoupon.checkedCard" @change="handleCheckedCardChange">

          <el-checkbox  :label="card.bankCode" :key="card.bankName">{{card.bankName}}</el-checkbox>

        </el-checkbox-group>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogBankCard = false">取 消</el-button>
        <el-button type="primary" @click="submitBankCard">提 交</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import { quillEditor } from 'vue-quill-editor'
  import searchMerchant from 'components/common/search'
  export default {
    components: {
      quillEditor, searchMerchant
    },
    data () {
      //表单验证校验
      var couponName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('券类型编号不能为空'));
        }
        else{
          callback();
        }
      };
      //输入正整数
      var  numberInter=(rule, value, callback) => {
        var reg = /^\+?[1-9]\d*$/
        if (!reg.test(value)) {
          return callback(new Error('请输入大于零数字'));
        }
        else{
          callback();
        }
      };
      //输入非负整数
      var  numberInterZero=(rule, value, callback) => {
        var reg = /^0\.([1-9]|\d[1-9])$|^[1-9]\d{0,8}\.\d{0,2}$|^[1-9]\d{0,8}$/
        if (!reg.test(value)) {
          return callback(new Error('请输入大于零数字，且最多保留2为小数'));
        }
        else{
          callback();
        }
      };
      //输入非负整数
      var  numberInterAll=(rule, value, callback) => {
        var reg = /^0\.([1-9]|\d[1-9])$|^[1-9]\d{0,8}\.\d{0,2}$|^[1-9]\d{0,8}$/
        if(!value){
          callback();
        }else{
          if (!reg.test(value)) {
            return callback(new Error('请输入大于零数字，且最多保留2为小数'));
          }
          else{
            callback();
          }
        }
      };
      //折扣限制0至90
      var  numberDiscount=(rule, value, callback) => {
        var reg = /^([1-8]?[0-9]|90)$/
        if (!reg.test(value)) {
          return callback(new Error('请输入正确的优惠折扣范围0至90'));
        }
        else{
          callback();
        }
      };
      return {
        display: false,           //隐藏状态
        show: false,              //隐藏状态
        clearShow:true,           //input 清空状态
        previewList: true,        //手机页面显示隐藏状态
        wrapperHeight: '',        //页面高度动态设置
        rowId: '',                //券编码
        rowStatus: '',            //券状态
        queryStoreList:[],        //门店编号列表
        //form 表单字段
        formCoupon: {
          id:'',               //券类型id
          newId:'',            //暂存表的id
          shareList:[ ],      // 券成本分摊规 data
          shareSmList:[],     //券成本分摊提交
          shareTotle:0,       //总分分摊值
          shareMerchantCode:'',//分摊可用商户名称
          shareMerchantName:'',//分摊可用商户code
          shareRate:'',        //分摊比率
          extRemark:'',
          couponExt:{           //券扩展信息节点
            remark: '',          //券说明
          },
          couponTypeCode: '',     //券编码
          couponName: '',         //券名称
          couponCategory: '',     //券类型额度
          couponMinOrderAmt: '',  //券满减使用门槛金额
          couponMaxOrderAmt: '',  //券满减使用门槛金额
          couponFaceAmt: '',      //券现金面等值金额
          couponDiscountRate: '', //券折扣值
          couponDiscountTrueRate: '', //券折真实值
          couponDiscountLimit: '', //券折扣值限额
          couponCategoryValue1:'',//券类型额度第一个值
          couponCategoryValue2:'',//券类型额度第二个值
          validDateType: '',       //有效期类型
          postponedDay: '',        //顺延有效期
          endfixedDate: '',        //固定失效期天
          endfixedDatetime: '',    //固定失效期时间段
          validDateEnd:'',         //固定失效日期状态
          validDateRang:'',        //固定失效区间日期状态
          formatedStartDate: '',   //券生效截起始日期天
          formatedEndDate: '',     //券失效截止日期天
          formatedStartDatetime: '',//券生效截起始日期区间
          formatedEndDatetime: '',  //券失效截止日期区间
          validDateValue1:'', //有效期的第一个值
          validDateValue2:'',   //有效期的第二个值
          issueCount: '',         //券发行数量
          couponRadio: '',        //券发行状态
          merchantStoreFlag: '',  //门店类别 全部true  部分false
          couponUseRadio: '',     //全可用状态变量
          infoData: [],           // 导入表格数据
          storeScopeList: [{      //券可用关系
            couponTypeCode: '',   //券类型编码
            merchantCode: '',     //商户编码
            id:'',                //数据唯一标识
            storeCode: '',        //门店编码
          }],
          uploadFile:'',          //上传excel文件
          payFlag: '',            //支付类别 全部true  部分false
          couponPayRadio: '',     //支付状态变量
          payBankList: [],        //支付方式数组
          payDeposit: false,      //储蓄卡支付显示状态
          payCredit: false,       //信用卡显示状态
          payItem: [],            //数据保存数组对象
          //弹出框选择银行卡
          checkAll: false,
          checkedCard: [],
          cardType: '',           //选择的银行卡类别
          cardList: [],
          isIndeterminate: false,
          checkedDepositList: [], //选中的checkedDepositList
          checkedDepositCode: [], //选中的checkedDepositListCode
          checkedCreditList: [], //选中的checkedCreditList
          checkedCreditCode: [], //选中的checkedCreditListCode
          //上传图片
          logoName: '',          //logo图片名称
          logoUrl: '',           //logoUrl
          operateRemark: '',     //操作备注
          slideHeight: 180,
          curHeight: '',
          moreStyle: '',
        },
        startPickerOptions:this.startPickerDate(),
        endPickerOptions:this.endPickerDate(),
        ruleValidate: {
          couponName: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 0, max: 50, message: '字符长度做多为50', trigger: 'blur' },
            {validator: couponName, trigger: 'blur'}
          ],
          couponRadio:[
            { required: true, message: '请输入券发行数量', trigger: 'blur' },
          ],
          issueCount:[
            { required: true, message: '请输入张数', trigger: 'blur' },
            {validator: numberInter, trigger: 'blur'}
          ],
          couponMinOrderAmt:[
            { required: true, message: '请输入数字', trigger: 'blur' },
            {validator: numberInterZero, trigger: 'blur'}
          ],
          couponMaxOrderAmt:[
            { required: true, message: '请输入数字', trigger: 'blur' },
            {validator: numberInterZero, trigger: 'blur'}
          ],
          couponDiscountRate:[
            { required: true, message: '请输入数字', trigger: 'blur' },
            {validator: numberDiscount, trigger: 'blur'}
          ],
          couponDiscountLimit:[
            {validator: numberInterAll, trigger: 'blur'}
          ],
          couponFaceAmt:[
            { required: false},
            {validator: numberInterZero, trigger: 'blur'}
          ],
          couponCategory: [
            { required: true, message: '请输入券类型额度', trigger: 'blur' },
          ],
           validDateType: [
            { required: true, message: '请选择券有效期', trigger: 'blur' },
          ],
          postponedDay: [
            { required: true, message: '请输入天数', trigger: 'blur' },
            {validator: numberInter, trigger: 'blur'}
          ],
          endfixedDate:[
            {type: 'date', required: true, message: '请输入券失效时间', trigger: 'change' },
          ],
          endfixedDatetime:[
            {type: 'date', required: true, message: '请输入券失效时间', trigger: 'change' },
          ],
          formatedStartDate:[
            { type: 'date',required: true, message: '请输入券生效时间', trigger: 'change' },
          ],
          formatedEndDate:[
            { type: 'date',required: true, message: '请输入券失效时间', trigger: 'change' },
          ],
          formatedStartDatetime:[
            { type: 'date',required: true, message: '请输入券生效时间', trigger: 'change' },
          ],
          formatedEndDatetime:[
            { type: 'date',required: true, message: '请输入券失效时间', trigger: 'change' },
          ],
          merchantStoreFlag:[
            { required: true, message: '请选择可用商户/门店', trigger: 'blur' },
          ],
          shareMerchantName:[
            { required: true, message: '请选择分摊商户', trigger: 'change' },
          ],
          payFlag:[
            { required: true, message: '请选择可用支付方式', trigger: 'blur' },
          ],
          uploadImage:[
            { required: true, message: '请上传图片', trigger: 'blur' },
          ],
          extRemark:[
            { required: true, message: '请输入券说明', trigger: 'blur' }
          ],
        },
        queryList: [],
        dialogBankCard: false,   //银行卡选择
        picValue:'',      // 上传图片url
        uploadImage:'',   // 上传图片
        uploadShow:false,   // 上传图片显示状态
      }
    },
    methods: {
      //起始结束日期大小禁止开始
      startPickerDate(){
        let self = this
        return {
          disabledDate(time){
            return time.getTime()< (Date.now()- 86400000)//开始时间不选时，结束时间最大值小于等于当天
          }
        }
      },
      //起始结束日期大小禁止开始
      endPickerDate(){
        let self = this
        return {
          disabledDate(time){
            if(self.formCoupon.formatedStartDate){
              return (new Date(self.formCoupon.formatedStartDate).getTime()- 86400000) > time.getTime()
            }else{
              return time.getTime() < (Date.now()- 86400000)//开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      //折扣去零
      discountRate(){
        this.formCoupon.couponDiscountTrueRate=this.filterDiscount(100-this.formCoupon.couponDiscountRate)
      },
      //满减大小
      couponOrderAmt(str){
        if(str=='min'){
          if(parseFloat(this.formCoupon.couponMinOrderAmt)<parseFloat(this.formCoupon.couponMaxOrderAmt)){
            this.$message({showClose: true,duration:2000,type: 'error',message:'订单金额大于满减金额！'});
          }
        }else if(str=='max'){
          if(parseFloat(this.formCoupon.couponMinOrderAmt)<parseFloat(this.formCoupon.couponMaxOrderAmt)){
            this.$message({showClose: true,duration:2000,type: 'error',message:'订单金额大于满减金额！'});
          }
        }
      },
      queryStoreSearch(queryString, cb) {
        var queryStoreList = this.queryStoreList;
        var results = queryString ? queryStoreList.filter(this.createFilter(queryString)) : queryStoreList;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (queryStoreList) => {
          return (queryStoreList.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      loadStoreList(){
        var dataUrl= couponPostLink+"/fin-rules-osprest/couponType/merchantStoreList.htm"
        let data={
          merchantCode:this.formCoupon.merchantCode,
          storeCode:this.formCoupon.storeCode,
        }     //券id
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            response.data.data.forEach((result) => {
              this.queryStoreList.push({value: result.storeCode,merchantCode: result.merchantCode})
            })
          }
        })
      },
      handleStoreSelect(item) {
        console.log(item);
      },
      //选择储蓄卡发卡行
      addBankCard(type){
        this.getCardList(type);
        this.dialogBankCard = true;
      },
      //获取银行卡列表
      getCardList(cardType){
        //清空checked选中状态
        this.formCoupon.checkedCard = [];
        this.formCoupon.checkAll = false;
        let dataUrl = bankPostLink + '/fin-account-osprest/bankCard/cardList.htm';
        let data = {
          cardType: cardType,//卡类型
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response) => {
          this.formCoupon.cardList = []
          if (response.data.code === 'S0A00000') {
            this.formCoupon.cardType = cardType;
            this.formCoupon.cardList = response.data.data
          } else {
            this.formCoupon.cardList = []
          }
        })
      },
      //全选按钮fun
      handleCheckAllChange(val) {
        let cardListArr = [];
        for (let i = 0; i < this.formCoupon.cardList.length; i++) {
          cardListArr.push(this.formCoupon.cardList[i].bankCode);
        }
        this.formCoupon.checkedCard = val ? cardListArr : [];
        if (val) {
          if (this.formCoupon.cardType == 'D') {
            this.formCoupon.checkedDepositList = this.formCoupon.cardList;
            this.formCoupon.checkedDepositCode=this.formCoupon.checkedCard;
          } else if (this.formCoupon.cardType == 'C') {
            this.formCoupon.checkedCreditList = this.formCoupon.cardList;
            this.formCoupon.checkedCreditCode=this.formCoupon.checkedCard;
          }
        } else {
          this.formCoupon.checkedDepositList = [];
          this.formCoupon.checkedCreditList = [];
        }
        this.formCoupon.isIndeterminate = false;
      },
      //部分选择
      handleCheckedCardChange(value) {
        let checkedCount = value.length;
        let cardListObj = {};
        //组装checked对象，push到checkedList
        if (this.formCoupon.cardType == 'D') {
          //重新选择清空对象数组
          this.formCoupon.checkedDepositList = [];
          this.formCoupon.checkedDepositCode=value;
          for (let i = 0; i < this.formCoupon.cardList.length; i++) {
            for (let j = 0; j < value.length; j++) {
              if (this.formCoupon.cardList[i].bankCode == value[j]) {
                cardListObj = this.formCoupon.cardList[i]
                this.formCoupon.checkedDepositList.push(cardListObj);
              }
            }
          }
        } else if (this.formCoupon.cardType == 'C') {
          //重新选择清空对象数组
          this.formCoupon.checkedCreditList = [];
          this.formCoupon.checkedCreditCode=value;
          for (let i = 0; i < this.formCoupon.cardList.length; i++) {
            for (let j = 0; j < value.length; j++) {
              if (this.formCoupon.cardList[i].bankCode == value[j]) {
                cardListObj = this.formCoupon.cardList[i]
                this.formCoupon.checkedCreditList.push(cardListObj);
              }
            }
          }
        }
        this.formCoupon.checkAll = checkedCount === this.formCoupon.cardList.length;
        this.formCoupon.isIndeterminate = checkedCount > 0 && checkedCount < this.formCoupon.cardList.length;
      },
      //提交选择的银行卡
      submitBankCard(){
        this.dialogBankCard = false;
        //this.formCoupon.checkedList=
      },
      //储蓄卡和信用卡切换
      payCard(type){
        if (type == 'D') {
          if (this.formCoupon.payDeposit == false) {
            this.formCoupon.payDeposit = true;
          } else {
            this.formCoupon.payDeposit = false;
            this.formCoupon.checkedDepositList=[];
          }
        }
        else if (type == 'C') {
          if (this.formCoupon.payCredit == false) {
            this.formCoupon.payCredit = true;
          } else {
            this.formCoupon.payCredit = false;
            this.formCoupon.checkedCreditList=[];
          }
        }
      },
      //获取优惠券的信息
      getCouponDetail(rowId, rowStatus){
        let dataUrl = couponPostLink + '/fin-rules-osprest/couponType/getCouponTypeInfo.htm';
        let data = {
          id: rowId,           //券编码
          status: rowStatus,   //券状态
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response) => {
          if (response.data.code === 'S0A00000') {
            let totalData = response.data.data;
            this.formCoupon.couponTypeCode = totalData.couponType.couponTypeCode;
            this.formCoupon.couponName = totalData.couponType.couponName;
            this.formCoupon.newId=totalData.id;//新建券id
            this.formCoupon.id = totalData.couponType.id;//券类型id

            //券发行数量为空
            if(totalData.couponType.issueCount==null||totalData.couponType.issueCount==''||totalData.couponType.issueCount==undefined){
                this.formCoupon.issueCount=0;
              }
              else{
                this.formCoupon.issueCount = totalData.couponType.issueCount;
              }
              if(this.formCoupon.issueCount==0){
                this.formCoupon.couponRadio='1';
              }else{
                this.formCoupon.couponRadio='2';
              }

            this.formCoupon.couponCategory = totalData.couponType.couponCategory;
            if (this.formCoupon.couponCategory == 'DISCOUNT') {
              this.formCoupon.couponDiscountRate = totalData.couponType.couponCategoryValue1;
              this.formCoupon.couponDiscountTrueRate=this.filterDiscount(100-this.formCoupon.couponDiscountRate);
              this.formCoupon.couponDiscountLimit = totalData.couponType.couponCategoryValue2;
            }
            else if (this.formCoupon.couponCategory == 'MONEYOFF') {
              this.formCoupon.couponMinOrderAmt = totalData.couponType.couponCategoryValue1;
              this.formCoupon.couponMaxOrderAmt = totalData.couponType.couponCategoryValue2;
            }
            else if (this.formCoupon.couponCategory == 'CASH') {
              this.formCoupon.couponFaceAmt = totalData.couponType.couponCategoryValue1;
            }
             this.formCoupon.couponCategoryValue1=totalData.couponType.couponCategoryValue1;
             this.formCoupon.couponCategoryValue2=totalData.couponType.couponCategoryValue2;
            this.formCoupon.validDateType = totalData.couponType.validDateType;
            if (this.formCoupon.validDateType == 'POSTPONED') {
              this.formCoupon.postponedDay = totalData.couponType.validDateValue1;
            }
            else if (this.formCoupon.validDateType == 'ENDFIXED') {
              var dateValue=totalData.couponType.validDateValue2
              if(dateValue.length==13){
                this.formCoupon.validDateEnd='datetime';
                this.formCoupon.endfixedDatetime = this.filterDate(totalData.couponType.validDateValue2,'YMDHMS');

              }else if(dateValue.length==10){
                this.formCoupon.validDateEnd='date';
                this.formCoupon.endfixedDate = this.filterDate(totalData.couponType.validDateValue2+'000','YMDHMS');
              }
            }
            else if (this.formCoupon.validDateType == 'RANGEFIXED') {
              var dateValue1=totalData.couponType.validDateValue1
              var dateValue2=totalData.couponType.validDateValue2
              if(dateValue1.length==13&&dateValue2.length==13) {
                this.formCoupon.validDateRang = 'datetime';
                this.formCoupon.formatedStartDatetime = this.filterDate(totalData.couponType.validDateValue1, 'YMDHMS');
                this.formCoupon.formatedEndDatetime = this.filterDate(totalData.couponType.validDateValue2, 'YMDHMS');
              }else if(dateValue1.length==10&&dateValue2.length){
                this.formCoupon.validDateRang = 'date';
                this.formCoupon.formatedStartDate = this.filterDate(totalData.couponType.validDateValue1+'000', 'YMD');
                this.formCoupon.formatedEndDate = this.filterDate(totalData.couponType.validDateValue2+'000', 'YMD');
              }
             }
             console.log(this.formCoupon.formatedStartDatetime)
            console.log(this.formCoupon.formatedEndDatetime)
             this.formCoupon.validDateValue1=totalData.couponType.validDateValue1;
             this.formCoupon.validDateValue2=totalData.couponType.validDateValue2;
            //商户选择门店
            this.formCoupon.merchantStoreFlag = totalData.merchantStoreFlag;
            if(this.formCoupon.merchantStoreFlag=='true'){
              this.formCoupon.couponUseRadio='1';
            }else if(this.formCoupon.merchantStoreFlag=='false'){
              this.formCoupon.couponUseRadio='2';
            }
            this.formCoupon.storeScopeList = totalData.storeScopeList;
            this.formCoupon.infoData = totalData.storeScopeList;
            //支付方式
            this.formCoupon.payFlag = totalData.payFlag;
            if(this.formCoupon.payFlag=='true'){
              this.formCoupon.couponPayRadio='1';
            }else if(this.formCoupon.payFlag=='false'){
              this.formCoupon.couponPayRadio='2';
            }

            for (let i = 0; i < totalData.payScopeList.length; i++) {
