            }
            // 图片显示
            if(this.uploadImage){
              this.uploadShow=true;
            }

            this.formCoupon.couponExt.remark = totalData.couponExt.remark;
            this.formCoupon.extRemark=this.formCoupon.couponExt.remark ;
