        this.formCoupon.shareMerchantCode = item.id // form id
      },
      // 下载文件
      downLoadFile(){
        window.location.href='../../../../static/template.xlsx';
      },
      //上传文件导入模板
      uploadFileExcel(event){
        event.preventDefault();
        this.clearShow=false;
        let formData = new FormData();
        formData.append('file', event.target.files[0] || event.dataTransfer.files[0]);
        formData.append('token',this.getCookie('token'));//token
        formData.append('transactionUuid',this.uuid());//uuid
        let dataUrl= couponPostLink+'/fin-rules-osprest/couponType/couponTypeFileUpload.htm';
        let config = {headers: {'Content-Type': 'multipart/form-data'}}
        this.$http.post(dataUrl, formData, config).then((response)=>{
          this.clearShow=true;
          if(response.data.code === 'S0A00000'){
            this.formCoupon.infoData=response.data.data;
            this.formCoupon.storeScopeList=response.data.data;
            this.$message({showClose: true,duration:2000,type: 'success',message:'恭喜你，文件导入成功！'});
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
