        if (this.formCoupon.couponCategory == 'DISCOUNT') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponDiscountRate;
          this.formCoupon.couponCategoryValue2 = this.formCoupon.couponDiscountLimit;
        }
        else if (this.formCoupon.couponCategory == 'MONEYOFF') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponMinOrderAmt;
          this.formCoupon.couponCategoryValue2 = this.formCoupon.couponMaxOrderAmt;
        }
        else if (this.formCoupon.couponCategory == 'CASH') {
          this.formCoupon.couponCategoryValue1 = this.formCoupon.couponFaceAmt;
        }
        // 时间日期
        if (this.formCoupon.validDateType == 'POSTPONED') {
          this.formCoupon.validDateValue1 = this.formCoupon.postponedDay;
        }
        else if (this.formCoupon.validDateType == 'ENDFIXED') {
          if(this.formCoupon.validDateEnd=='datetime'){
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.endfixedDatetime,13);
          }else if(this.formCoupon.validDateEnd=='date'){
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.endfixedDate,10);
          }
        }
        else if (this.formCoupon.validDateType == 'RANGEFIXED') {
          if(this.formCoupon.validDateRang=='datetime'){
            this.formCoupon.validDateValue1 = this.filterDateStr(this.formCoupon.formatedStartDatetime,13);
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.formatedEndDatetime,13);
          }else if(this.formCoupon.validDateRang=='date'){
            this.formCoupon.validDateValue1 = this.filterDateStr(this.formCoupon.formatedStartDate,10);
            this.formCoupon.validDateValue2 = this.filterDateStr(this.formCoupon.formatedEndDate,10);
          }
        }
        //银行卡
        this.formCoupon.payItem=[];
        for(let m=0;m<this.formCoupon.payBankList.length;m++){
          if (this.formCoupon.payBankList[m] == 'BALANCE') {
            var payItemObj={ list:[],payType:this.formCoupon.payBankList[m]}
            this.formCoupon.payItem.push(payItemObj);
          }
          else if(this.formCoupon.payBankList[m] == 'D'){
            this.formCoupon.checkedDepositCode=[];
            for(let s = 0; s < this.formCoupon.checkedDepositList.length; s++){
              this.formCoupon.checkedDepositCode.push(this.formCoupon.checkedDepositList[s].bankCode)
            }
            var payItemObj={ list:this.formCoupon.checkedDepositCode,payType:this.formCoupon.payBankList[m]}
            this.formCoupon.payItem.push(payItemObj);
          }
          else if(this.formCoupon.payBankList[m] == 'C'){
            this.formCoupon.checkedCreditCode=[];
            for(let s = 0; s < this.formCoupon.checkedCreditList.length; s++){
              this.formCoupon.checkedCreditCode.push(this.formCoupon.checkedCreditList[s].bankCode)
            }
            var payItemObj={ list:this.formCoupon.checkedCreditCode,payType:this.formCoupon.payBankList[m]}
            this.formCoupon.payItem.push(payItemObj);
          }
          else if(this.formCoupon.payBankList[m] == 'CRTXDFENQI'){
            var payItemObj={ list:[],payType:this.formCoupon.payBankList[m]}
            this.formCoupon.payItem.push(payItemObj);
          }
        }
        // 分摊shareSmList对象数组保存组装
        this.formCoupon.shareSmList=[];
        this.formCoupon.shareTotle=0;
        for(let i=0;i<this.formCoupon.shareList.length;i++){
          var shareSmObj={'shareMerchantCode':this.formCoupon.shareList[i].shareMerchantCode,'shareRate':this.formCoupon.shareList[i].shareRate}
          this.formCoupon.shareTotle+=parseFloat(this.formCoupon.shareList[i].shareRate);
          this.formCoupon.shareSmList.push(shareSmObj);
        }
      },
      // 券暂时保存
      couponTemporary(){
        this.couponsInfo();//信息保存
        this.$confirm('是否暂存券信息?', '提示', {
          cancelButtonText: '取消',
