            "payFlag": this.formCoupon.payFlag,
            'payItem': this.formCoupon.payItem,
            "storeScopeList": this.formCoupon.storeScopeList,
          };
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
