        if(this.formCoupon.shareTotle!=100){
          this.$message({showClose: true,duration:2000,type: 'error',message:'分摊比例总和不是100%, 不能更新为启用状态！'});
          return
        }
        if(!this.uploadImage){
          this.$message({showClose: true,duration:2000,type: 'error',message:'图片不能为空！'});
          return
        }

          if(!this.formCoupon.couponExt.remark){
            this.$message({showClose: true,duration:2000,type: 'error',message:'券说明不能为空！'});
            return
          }
        this.$confirm('是否保存券信息?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
            let dataUrl = couponPostLink + '/fin-rules-osprest/couponType/submit.htm';
            let data = {
              "costSharelist":this.formCoupon.shareSmList,
