          }else if(this.formCoupon.couponPayRadio=='2'){
            this.formCoupon.payFlag='false';
          }
        }
      }
     },
      created(){
        this.rowId = this.$route.query.id;
        this.rowStatus = this.$route.query.status;
        this.getCouponDetail(this.rowId, this.rowStatus);
        this.loadStoreList();// 搜索店铺名字
        this.fileUrl= couponPostLink+"/fin-rules-osprest/couponType/couponTypeFileUpload.htm";

      },
      computed: {
        editor() {
          return this.$refs.myQuillEditor.quill
        }
      },
      mounted () {
        this.wrapperHeight = document.documentElement.clientHeight - 120;
      }
    }
</script>
