    width: 540px;
  }
  .payDepositList span, .payCreditList span{
    color:#7794b3;
  }
  .previewMain{
    background: url("../../../assets/images/device-sprite.png") no-repeat;
    background-size: 300%;
    background-position: -395px 0;
    display: block;
    width: 395px;
    height: 813px;
    position: relative;
  }
  .previewMain .previewBox{
    position: absolute;
    top: 117px;
    left: 37px;
    width: 321px;
    height: 569px;
    overflow: hidden;
    font-size: 0.85rem;
    line-height: 1.05rem;
    background: #eeeeee;
  }
  .ln-header{
    text-align: center;
    font-size:16px;
    line-height: 38px;
    padding:3px 0 ;
    background:#fff;
    color:#666 ;
    position: relative;
    .ln-goback{
      position: absolute;
      width:25px;
      height: 40px;
      padding:3px 0 ;
      top:50%;
      left:8px;
      transform:translate(0,-50%);
      background: url("../../../assets/images/back_icon.png") no-repeat center left;
      background-size:40%;
      cursor: pointer;
      z-index:10 ;
    }
  }
  .previewMain .ln-wrapper{
    .couponRow{
      padding:8px 8px 0 8px;
      .couponBox{
        position: relative;
        background-color:#fff;
        padding:8px 0rem 8px 0.2rem;
        border-radius:6px 0 0 6px ;
        cursor: pointer;
        .couponIcon{
          position: absolute;
          left: 8px;
          top:50%;
          width:44px;
          height:44px;
          margin-top: -24px ;
          border-radius:44px;
          border:1px solid #e5e5e5 ;
          background-color:#eeeeee;
          z-index:10 ;
          display: flex;
          justify-content: center;
          align-items: center;
          img{
            width: 70%;
            height:70%;
            display: inline-block;
          }
        }
        .couponMain{
          padding: 5px 60px 5px 56px;
          .couponTxt{
            font-size:14px;
            line-height:16px;
            height:16px;
            margin-top:2px ;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;

          }
          .couponDate{
            margin-top:2px;
            font-size:12px;
            line-height:16px;
            height: 16px;
            i{
              font-style: normal!important;
            }
            .dateOver{
              color:#ff5500 ;
              margin-left:0.16rem;
            }
          }
        }
        .couponWorth{
          position: absolute;
          display: inline-block;
          right:0;
          top:0;
          width:60px;
          padding-left:0.12rem ;
          height: 100%;
          z-index:100 ;
          background:url(../../../assets/images/coupos_bg.png) center  no-repeat #eeeeee;
          background-size: 100% 100%;
          color:#fa9632;
          p:nth-child(1){
            margin-top:10px ;
            font-size:14px;
            height:20px;

          }
          .zk_martop{
            margin-top:21px!important; ;
          }
          .couponMoney{
            font-size:18px;
            line-height:20px;
            height:20px;
            display: inline-block;
            font-style: normal;
          }
          .couponTip{
            line-height:14px;
            margin-top:4px;
            .couponBtn{
              border:1px solid #fa9632;
              height: 14px;
              font-size: 12px;
              line-height: 14px;
              border-radius: 14px;
              padding: 0 3px;
              display: inline-block;
            }
            .couponMj{
              font-size:12px ;
            }
          }
        }
      }

    }
  }
  .previewDetail{
    .ln-content{
