      background:#e9e9e9;
      padding:32px  8px  10px  8px;
      .ln-wrap{
        box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
        border-radius:8px;
      }
      .coupon-header{
        position: relative;
        border-radius: 8px 8px 0  0;
        background-color:#ffffff;
        padding-top:32px;
        .couponIcon {
          position: absolute;
          left: 50%;
          top: -22px;
          width: 44px;
          height:  44px;
          margin-left: -22px;
          border-radius:  44px;
          background-color: #ffffff;
          border: 1px solid #ededed;
          box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
          z-index: 10;
          display: flex;
          justify-content: center;
