            line-height:20px;
            height:20px;
            display: inline-block;
            font-style: normal;
          }
        }
        .couponMj{
          font-size: 12px;
          line-height:16px ;
          margin-top:3px ;
          margin-bottom:5px ;
        }
        .couponTip{
          font-size:12px;
          line-height:14px;
          margin-top:8px ;
          margin-bottom:6px ;
          .couponBtn{
            border:1px solid #fa9632;
            height: 14px;
            font-size: 12px;
            line-height: 14px;
            border-radius: 14px;
            padding: 0 3px;
            display: inline-block;
            color:#fa9632;
          }
        }
        .couponDate{
          padding-top:8px ;
          letter-spacing: -0.5px;
          font-size: 12px;
          i{
            font-style: normal!important;
          }

        }
      }
      .coupon-main{
        .coupon-main-bg{
          width: 100%;
          display: block;
          vertical-align: top;
          margin-top: -1px;
        }
        .coupon-con{
          padding:0 0.5rem ;
          background:#fff;
          border-radius: 0  0 8px 8px;
          margin-top: -1px;
          .coupon-h1{
            font-size:0.34rem ;
            line-height:0.6rem ;
            padding:0.5rem 0 0 0.32rem ;

            position: relative;
            .coupon-boll{
              position: absolute;
              width: 0.14rem;
              height: 0.14rem;
              border-radius:0.14rem;
              top:0.72rem;
              left:0rem;
              background:#cccccc ;
              display: inline-block;
            }
          }
          .coupon-h2{
            border-bottom: 1px solid #e8e8e8;
            padding:5px 8px 5px ;
            font-size:12px;
            line-height:20px ;
            margin-bottom:8px ;
            position: relative;
            .coupon-boll{
              position: absolute;
              width: 0.14rem;
              height: 0.14rem;
              border-radius:0.14rem;
              top:0.8rem;
              left:0rem;
              background:#cccccc ;
              display: inline-block;
            }
          }

          .coupon-row{
            font-size:12px;
            line-height: 12px;
            justify-content: space-around;
            color:#999;
            display: block;
            overflow: hidden;
            min-height: 100px;
            max-height: 280px;
            overflow-y: scroll;
          }
          .coupon-row p{
            font-size:12px;
          }
          .coupon-row img{
            max-width:100%;
            display: inline-block;
            margin:0.3rem 0 ;
          }
          .coupon-row em{
            font-style: italic;
          }
          .coupon-line{
            position: relative;
            .ln-linear{
              position: absolute;
              width: 100%;
              height:0.6rem;
              left: 0px;
              bottom:0rem;
              background:url("../../../assets/images/linner.png") no-repeat bottom center;
              background-size:100% ;
              z-index:100 ;
            }
          }
          .couponHeight{
            height:auto!important;
            overflow: auto!important;
          }
          .coupon-more{
            color:#00a7ee;
            margin-top:0rem ;
            text-align: center;
            span{
              display: inline-block;
              height:0.6rem;
              line-height:0.6rem ;
              padding-right:0.5rem;
              position: relative;
            }
            i{
              height:0.6rem;
              line-height:0.6rem ;
              font-size:0.34rem ;
              display: inline-block;
            }
            img{
              position: absolute;
              top:50%;
              right: 0px;
              width:0.34rem;
              height:0.34rem;
              margin-top:-0.17rem ;
              display: inline-block;
              z-index: 100;
            }
          }
          .coupon-copyright{
            border-top: 1px solid #e8e8e8;
            margin-top:12px;
            font-size:12px;
            line-height:20px;
            padding:8px 0 8px 0px ;
            color:#999;
            text-align: center;
          }
        }
      }
    }
  }

</style>



