<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formRule"  :rules="ruleValidate" ref="formRule"  id="formwrapper"  class="demo-form-inline crm-form" label-width="110px">
      <el-row>
        <div class="el-card-title">折扣规则基本信息</div>

        <el-form-item label="折扣编号" prop="ruleCode" >
          <el-input v-model="formRule.ruleCode"  class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="折扣名称" prop="ruleName">
          <el-input v-model="formRule.ruleName"   class="el-winps"  placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="折扣类型" prop="ruleType">
          <el-select v-model="formRule.ruleType" class="el-winps" placeholder=" ">
            <el-option label="活动折扣" value="ACTIVITY"></el-option>
            <el-option label="常规折扣" value="GENERAL"></el-option>
          </el-select>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="折扣(百分比)" prop="discountRate">
          <el-input v-model="formRule.discountRate"   class="el-winps"  placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="限额类型" prop="limitType">
          <el-select v-model="formRule.limitType" class="el-winps" @change="limitType">
            <el-option label="无限额" value="UNLIMITED"></el-option>
            <el-option label="本单限额" value="TXNLIMITED"></el-option>
            <el-option label="累积限额" value="ACCUMULATED"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="限额（元）" prop="limitValue">
          <el-input v-model="formRule.limitValue"  :disabled="formRule.limitType=='UNLIMITED'"  class="el-winps"  placeholder=" "></el-input>
        </el-form-item>

      </el-row>
      <el-row>

        <el-form-item class="el-datepicker" label="有效开始时间" prop="startDate">
          <el-date-picker v-model="formRule.startDate"   class="el-winps" :picker-options="startPickerOptions" type="datetime" :editable="false" placeholder="选择日期"></el-date-picker>
       </el-form-item>

        <el-form-item label="有效结束时间" prop="endDate">
          <el-date-picker v-model="formRule.endDate"   class="el-winps"  :picker-options="endPickerOptions" type="datetime" :editable="false" placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="状态"  prop="status">
          <el-select v-model="formRule.status" class="el-winps" placeholder=" ">
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="启用" value="VALID"></el-option>
            <el-option label="禁用" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="备注" prop="remark" >
          <el-input  v-model="formRule.remark"  type="textarea" class="el-winpl el-winph" ></el-input>
        </el-form-item>
      </el-row>
      <el-row>
        <div class="el-card-title">折扣使用规则</div>
       <el-row>
         <el-form-item label="可用商户"  prop="merchantName">
           <search-Merchant v-on:communicate="setMerchantStore" description="merchant"  placeholder="请输入商户名称" class="el-winp" :empty="formRule.merchantName"></search-Merchant>
         </el-form-item>

         <el-form-item label="可用门店" label-width="60px" prop="storeName">
           <el-autocomplete v-model="formRule.storeName" :fetch-suggestions="querySearchAsyncStore" placeholder="请输入并选择门店" class="el-winp"  :disabled="!formRule.merchantCode" @select="setStoreInfo">
           </el-autocomplete>
         </el-form-item>

       </el-row>

        <el-form-item label="可用门店"   prop="allStore">
          <el-radio-group v-model="formRule.allStore" @change="storeValidate" :disabled="!formRule.merchantName">
            <el-radio label="true">全部可用</el-radio>
            <el-radio label="false">部分可用</el-radio>
          </el-radio-group>
        </el-form-item>


        <el-form-item  v-if="formRule.allStore=='false'" >

          <el-button type="primary" class="el-btnw el-marl" @click="setStoreAdd"><i class="el-icon-plus"></i> 新增</el-button>

          <el-button type="primary" class="el-btnw  el-marl" @click="setStoreDown"><i class="el-icon-download"></i>门店批量下载</el-button>

          <div  class="el-upload el-upload--picture el-relative el-marl10">
            <el-button type="primary" class="el-btnw "><span>文件导入</span></el-button>
            <input type="file" name="file" v-if="clearShow"  :value="formRule.uploadFile" class="el-file-input"  @change="uploadFileExcel">
          </div>

          <el-button type="primary" class="el-btnw  el-marl20" @click="setModelDown"><i class="el-icon-download"></i>模板下载</el-button>

        </el-form-item>


        <el-form-item  label="门店列表" v-if="formRule.allStore=='false'" >

          <el-table :data="formRule.storeTable"  border  style="width: 100%">

          <el-table-column prop="storeCode" align="center" width="180" label="门店编码" ></el-table-column>
            <el-table-column prop="storeName" align="center" width="210" label="门店名称"></el-table-column>
            <el-table-column  align="center" width="80" label="操作">
              <template slot-scope="scope">
                <el-button size="small" type="danger" @click="setStoreDelete(scope.$index, scope.row)"  >删除</el-button>
              </template>
            </el-table-column>
          </el-table>

        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="可用会员等级" prop="allUser">
          <el-radio-group v-model="formRule.allUser">
            <el-radio label="true">全部可用</el-radio>
            <el-radio label="false">部分可用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item >
          <el-checkbox-group v-model="formRule.userScope.scopeTypeValues" v-if="formRule.allUser=='false'" >
            <el-row>
              <el-checkbox label="1">V1 - 畅享会员</el-checkbox>
              <el-checkbox label="2">V2 - 优享会员</el-checkbox>
              <el-checkbox label="3">V3 - 尊享会员</el-checkbox>
            </el-row>
          </el-checkbox-group>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="可用支付方式" prop="allPay">
          <el-radio-group v-model="formRule.allPay">
            <el-radio label="true">全部可用</el-radio>
            <el-radio label="false">部分可用</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-row class="el-marl210">
          <el-form-item >
            <el-checkbox-group v-model="formRule.payBankList" v-if="formRule.allPay=='false'" >
              <el-row>
                <el-checkbox label="BALANCE">润钱包账户余额</el-checkbox>
              </el-row>
              <el-row>
                <el-checkbox label="D" @change="payCard('D')">存蓄卡支付</el-checkbox>
                <div class="payDepositList"><span class="el-span" v-for="checked in formRule.checkedDepositList">{{checked.bankName}}</span></div>
                <span class="el-span el-blue el-cursor"  v-if="formRule.payDeposit" @click="addBankCard('D')" >设置指定银行卡+</span>
              </el-row>
              <el-row>
                <el-checkbox label="C"  @change="payCard('C')">信用卡支付</el-checkbox>
                <div class="payCreditList"><span class="el-span" v-for="checked in formRule.checkedCreditList">{{checked.bankName}}</span></div>
                <span class="el-span el-blue el-cursor" v-if="formRule.payCredit" @click="addBankCard('C')">设置指定银行卡+</span>
              </el-row>
              <el-row>
                <el-checkbox label="CRTXDFENQI">小贷支付</el-checkbox>
              </el-row>
            </el-checkbox-group>
          </el-form-item>
        </el-row>
      </el-row>
      <el-row>
        <div class="el-card-title">费用分摊规则</div>
        <el-row>
          <el-form-item label="可用商户" prop="shareMerchantName">
            <search-Merchant v-on:communicate="setMerchantShare" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formRule.shareMerchantName"></search-Merchant>
          </el-form-item>

          <el-button type="primary" class="el-btnw  el-marb20"  @click="shareAdd"><i class="el-icon-plus"></i> 新增</el-button>
        </el-row>
        <el-form-item label="分摊方设置" >

          <el-table :data="formRule.shareList"  border  style="width: 100%">

            <el-table-column   prop="shareMerchantCode"  align="center" width="240" label="商户编号">
              {{formRule.shareMerchantCode}}
            </el-table-column>
            <el-table-column prop="shareMerchantName" align="center" width="240" label="商户名称" >
              {{formRule.shareMerchantName}}
            </el-table-column>
            <el-table-column prop="shareRate" align="center" width="150" label="分摊比例%" >
              <template slot-scope="scope">
                <el-input v-model="scope.row.shareRate"  ></el-input>
              </template>
            </el-table-column>

            <el-table-column  align="center" width="80" label="操作">
              <template slot-scope="scope">
                <el-button size="small" type="danger" @click="shareDelete(scope.$index, scope.row)"  >删除</el-button>
              </template>
            </el-table-column>

          </el-table>

        </el-form-item>

      </el-row>

      <el-row class="ln-ac">

          <el-button class="el-winpMin el-marr" type="primary" size="big" @click="saveDiscountRule('formRule')" >保存</el-button>
          <el-button type="info" class="el-winpMin"  size="big" @click="goBack">返回</el-button>

      </el-row>

    </el-form>

    <el-dialog title="选择支持的储蓄卡发行卡(可多选)" class="ln-bankList ln-ac"  width="360px" :visible.sync="dialogBankCard">
      <el-form  label-width="68px">

        <el-checkbox :indeterminate="formRule.isIndeterminate" v-model="formRule.checkAll" @change="handleCheckAllChange">全选</el-checkbox>

        <el-checkbox-group v-for="(card , index) in formRule.cardList" :key ="card.id" v-model="formRule.checkedCard" @change="handleCheckedCardChange">

          <el-checkbox  :label="card.bankCode" :key="card.bankName">{{card.bankName}}</el-checkbox>

        </el-checkbox-group>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogBankCard = false">取 消</el-button>
        <el-button type="primary" @click="submitBankCard">提 交</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  import searchMerchant from 'components/common/search'
  export default {
    components: {ElRow, searchMerchant},
    data () {
      //表单验证校验
      //折扣限制
      var  discount=(rule, value, callback) => {
        var reg = /^(\d?\d(\.\d{0,2})?|100)$/
        if (!reg.test(value)) {
          return callback(new Error('请输入正确的优惠折扣范围0至100'));
        }else{
          callback();
        }
      };
      //输入2位正数
      var  numberDoube=(rule, value, callback) => {
        var reg = /^0\.([1-9]|\d[1-9])$|^[1-9]\d{0,8}\.\d{0,2}$|^[1-9]\d{0,8}$/
        if(this.formRule.limitType!=='UNLIMITED'){
          if (!reg.test(value)) {
            return callback(new Error('请输入正确的金额'));
          }else{
            callback();
          }
        }else{
          callback();
        }

      };
      return {
        display: false,           //隐藏状态
        show: false,              //隐藏状态
        clearShow:true,           //input 清空状态
        wrapperHeight: '',        //页面高度动态设置
        multipleSelection:'',
        queryList: [],
        dialogBankCard: false,   //银行卡选择
        //formRule 表单字段
        formRule: {
          allPay:'',           //是否全部可用支付方式
          allUser:'',          //用户范围是所有用户
          discountRate:'',     //优惠比例
          startDate:'',        //有效开始日期
          endDate:'',          //有效截止日期
          id:'',               //规则ID
          limitType:'',        //限额类型
          limitTypeDesc:'',
          limitValue:'',       //限额类型
          allStore:'',
          merchantStores:[{
            allStore:'',       //该商户范围是否所有门店
            merchantCode:'',   //商户编码
            storeCodes:[],     //门店编码集合
          }],
          merchantName:'',  //可用商户
          merchantCode:'',  //可用商户

          storeTable:[],
          storeCode:'',         //可用门店编码
          storeName:'',         //可用门店名称
          uploadFile:'',

          ruleLevel:'',         //会员等级状态 全部 部分
          ruleFlag:'',          //会员等级状态 全部 部分
          payBankList:[],
          payDeposit:false,
          payCredit:false,
          payList:[],
          //弹出框选择银行卡
          checkAll: false,
          checkedCard: [],
          cardType: '',           //选择的银行卡类别
          cardList: [],
          isIndeterminate: false,
          checkedDepositList: [], //选中的checkedDepositList
          checkedDepositCode: [], //选中的checkedDepositListCode
          checkedCreditList: [], //选中的checkedCreditList
          checkedCreditCode: [], //选中的checkedCreditListCode

          remark:'',           //备注
          ruleCode:'',         //规则编码
          ruleName:'',         //规则名称
          ruleType:'',         //规则类型

          shareMerchantCode:'',//分摊可用商户名称
          shareMerchantName:'',//分摊可用商户code
          shareRate:'',        //分摊比率

          shareList:[],         //分摊table
          shareSmList:[],       //分摊提交对象
          shareTotle:0,       //总分分摊值

          status:'',              //状态
          userScope:{
            scopeType:'ML',         //范围类型
            scopeTypeValues:[],   //用户范围类型对应的值集合
          },
        },
        startPickerOptions:this.startPickerDate(),
        endPickerOptions:this.endPickerDate(),
        ruleValidate: {
          ruleName: [
            { required: true, message: '请输入折扣名称', trigger: 'blur' },
            { min: 0, max: 50, message: '字符长度做多为50', trigger: 'blur' }
          ],
          ruleType: [
            { required: true, message: '请选择折扣类型', trigger: 'change' },
          ],
          discountRate: [
            { required: true, message: '请输入折扣值', trigger: 'blur' },
            {validator: discount, trigger: 'blur'}
          ],
          limitType: [
            { required: true, message: '请选择限额类型', trigger: 'change' },
          ],
          limitValue: [
            { required: false, validator: numberDoube, trigger: 'blur'},
          ],
          startDate:[
            {type: 'date', required: true, message: '请输入有效开始时间', trigger: 'blur' },
          ],
          endDate:[
            {type: 'date', required: true, message: '请输入有效开始时间', trigger: 'blur' },
          ],
          status: [
            { required: true, message: '请选择限折扣状态', trigger: 'change' },
          ],
          allStore: [
            { required: true, message: '请选择可用门店', trigger: 'change' },
          ],
          allUser: [
            { required: true, message: '请选择会员等级', trigger: 'change' },
          ],
          allPay: [
            { required: true, message: '请选择支付方式', trigger: 'change' },
          ],
          merchantName: [
           { required: true, message: '请输入可用商户', trigger: 'change' },
          ],
          shareMerchantName: [
            { required: true, message: '请输入分摊可用商户', trigger: 'change' },
          ],
        },
      }
    },
    methods: {
      //起始结束日期大小禁止开始
      startPickerDate(){
        let self = this
        return {
          disabledDate(time){
            return time.getTime()< (Date.now()- 86400000)//开始时间不选时，结束时间最大值小于等于当天
          }
        }
      },
      //起始结束日期大小禁止开始
      endPickerDate(){
        let self = this
        return {
          disabledDate(time){
            if(self.formRule.startDate){
              return (new Date(self.formRule.startDate).getTime()- 86400000) > time.getTime()
            }else{
              return time.getTime() < (Date.now()- 86400000)//开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      //限额清空值
      limitType(){
        if(this.formRule.limitType=='UNLIMITED'){
           this.formRule.limitValue='';
        }
      },
      //活动&规则编码获取
      getRuleCode(){
        let dataUrl= discountPostLink+'/fin-rules-osprest/activityRule/acquireCode.htm';
        let params={'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            this.formRule.activityCode=response.data.data.activityCode;
            this.formRule.ruleCode=response.data.data.ruleCode;
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //请输入商户
      storeValidate(){
        if(this.formRule.allStore){
          if(!this.formRule.merchantCode){
            this.$message({showClose: true,duration:2000,type: 'error',message:'请输入商户名称！'});
          }
        }
      },
      // 可用门店选择
      querySearchAsyncStore(queryString, cb){
        var self=this;
        self.formRule.storeCode=""
        if(!queryString){
          cb([])
          return
        }
        var url=merchantPostLink+"/merchant/admin/store/storeQueryList.htm";
        var postData={
          sysId:"T0000017",
          token:this.getCookie('token'),
          transactionUuid:this.uuid(),
          storeName:queryString,
          merchantCode:self.formRule.merchantCode
        };
        this.$http.post(url, postData).then(function(res){
          res=res.data
          if(res.code=="S0A00000"){
            var list=[];
            res.data.forEach(function(e){
              if(e.storeName==queryString){
                self.formRule.storeCode=e.storeCode;
              }
              list.push({value: e.storeName, code: e.storeCode})
            })
            cb(list)
          }
          else{
            cb([])
            self.formRule.storeCode=""
          }
        })
      },
      setStoreInfo(item){
        this.formRule.storeName = item.value
        this.formRule.storeCode = item.code
      },
      // 可用门店商户
      setMerchantStore(item){
        this.formRule.merchantName = item.value   // form  value
        this.formRule.merchantCode = item.id // form id
        this.formRule.storeName = ""
        this.formRule.storeCode = ""
      },
      //分摊商户
      setMerchantShare(item){
        this.formRule.shareMerchantName = item.value   // form  value
        this.formRule.shareMerchantCode = item.id // form id
      },
      //上传文件导入模板
      uploadFileExcel(event){
        event.preventDefault();
        this.clearShow=false;
        let formData = new FormData();
        formData.append('file', event.target.files[0] || event.dataTransfer.files[0]);//文件对象
        formData.append('merchantCode',this.formRule.merchantCode);//指定商户编码
        formData.append('ruleCode',this.formRule.ruleCode);//折扣规则编码
        formData.append('token',this.getCookie('token'));//token
        formData.append('transactionUuid',this.uuid());//uuid
        let dataUrl= discountPostLink+'/fin-rules-osprest/rule/storeCodeUpload.htm';
        let config = {headers: {'Content-Type': 'multipart/form-data'}}
        this.$http.post(dataUrl, formData, config).then((response)=>{
          this.clearShow=true;
          if(response.data.code === 'S0A00000'){
            this.formRule.storeTable=response.data.data;
            this.$message({showClose: true,duration:2000,type: 'success',message:'恭喜你，文件导入成功！'});
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //门店手动新增
      setStoreAdd(){
        //选择商户和门店
        if(this.formRule.merchantName&&this.formRule.merchantCode){
          if(this.formRule.storeName&&this.formRule.storeCode){
            var ruleObject = {
              storeCode:this.formRule.storeCode,
              storeName:this.formRule.storeName,
            };
            this.formRule.storeTable.push(ruleObject)
          }
          else{
            this.$message({showClose: true,duration:2000,type: 'warning',message:'请选择可用门店！'});
          }
        }else{
          this.$message({showClose: true,duration:2000,type: 'warning',message:'请选择可用商户！'});
        }
      },
      //门店批量下载
      setStoreDown(){
        var fileUrl= discountPostLink+'/fin-rules-osprest/rule/storeCodeGetDownload.htm?ruleCode='+this.formRule.ruleCode;
        window.open(fileUrl)
      },
      //模板下载
      setModelDown(){
        window.location.href='../../../../static/store_code_upload_tmplate.xlsx';
      },
      /* 门店 分摊数据库删除
       *  type  删除类型 store   share
       *  row   删除行
       */
      setDeleteData(type,row){
        if(type=='store'){
          var dataUrl= discountPostLink+'/fin-rules-osprest/rule/deleteStore.htm';
        }else if(type=='share'){
          var dataUrl= discountPostLink+'/fin-rules-osprest/rule/deleteShare.htm';
        }

        let data={
          ids:[row.id],
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            this.$message({showClose: true,duration:2000,type: 'success',message:'删除成功!'});
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //门店删除表格行
      setStoreDelete(index, row) {
        this.$confirm('删除当前行, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if(row.id){
            this.setDeleteData('store',row);
          }else{
            this.formRule.storeTable.splice(index, 2)
          }

        }).catch(() => {
          this.$message({showClose: true,duration:2000,type: 'info',message:'已取消删除!'});
        });
      },
      //分摊添加
      shareAdd(){
        if(this.formRule.shareMerchantCode&&this.formRule.shareMerchantName){
          var shareObject = {
            shareMerchantCode:this.formRule.shareMerchantCode,
            shareMerchantName:this.formRule.shareMerchantName,
            shareRate:'',
          };
          this.formRule.shareList.push(shareObject)
        }else{
          this.$message({showClose: true,duration:2000,type: 'warning',message:'请选择分摊商户！'});
        }
      },
      //分摊删除
      shareDelete(index, row){
        this.$confirm('删除当前行, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if(row.id){
          this.setDeleteData('share',row);
          }else{
          this.formRule.shareList.splice(index, 1)
          }
        }).catch(() => {
          this.$message({showClose: true,duration:2000,type: 'info',message:'已取消删除！'});
        });
      },
      //选择储蓄卡发卡行
      addBankCard(type){
        this.getCardList(type);
        this.dialogBankCard = true;
      },
      //获取银行卡列表
      getCardList(cardType){
        //清空checked选中状态
        this.formRule.checkedCard = [];
        this.formRule.checkAll = false;
        let dataUrl = bankPostLink + '/fin-account-osprest/bankCard/cardList.htm';
        let data = {
          cardType: cardType,//卡类型
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response) => {
          this.formRule.cardList = []
          if (response.data.code === 'S0A00000') {
            this.formRule.cardType = cardType;
            this.formRule.cardList = response.data.data
          } else {
            this.formRule.cardList = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //全选按钮fun
      handleCheckAllChange(val) {
        let cardListArr = [];
        for (let i = 0; i < this.formRule.cardList.length; i++) {
          cardListArr.push(this.formRule.cardList[i].bankCode);
        }
        this.formRule.checkedCard = val ? cardListArr : [];
        if (val) {
          if (this.formRule.cardType == 'D') {
            this.formRule.checkedDepositList = this.formRule.cardList;
          } else if (this.formRule.cardType == 'C') {
            this.formRule.checkedCreditList = this.formRule.cardList;
          }
        } else {
          this.formRule.checkedDepositList = [];
          this.formRule.checkedCreditList = [];
        }
        this.formRule.isIndeterminate = false;
      },
      //部分选择
      handleCheckedCardChange(value) {
        let checkedCount = value.length;
        let cardListObj = {};
        //组装checked对象，push到checkedList
        if (this.formRule.cardType == 'D') {
          //重新选择清空对象数组
          this.formRule.checkedDepositList = [];
          for (let i = 0; i < this.formRule.cardList.length; i++) {
            for (let j = 0; j < value.length; j++) {
              if (this.formRule.cardList[i].bankCode == value[j]) {
                cardListObj = this.formRule.cardList[i]
                this.formRule.checkedDepositList.push(cardListObj);
              }
            }
          }
        } else if (this.formRule.cardType == 'C') {
          //重新选择清空对象数组
          this.formRule.checkedCreditList = [];
          for (let i = 0; i < this.formRule.cardList.length; i++) {
            for (let j = 0; j < value.length; j++) {
              if (this.formRule.cardList[i].bankCode == value[j]) {
                cardListObj = this.formRule.cardList[i]
                this.formRule.checkedCreditList.push(cardListObj);
              }
            }
          }
        }
        this.formRule.checkAll = checkedCount === this.formRule.cardList.length;
        this.formRule.isIndeterminate = checkedCount > 0 && checkedCount < this.formRule.cardList.length;
      },
      //提交选择的银行卡
      submitBankCard(){
        this.dialogBankCard = false;
        //this.formRule.checkedList=
      },
      //储蓄卡和信用卡切换
      payCard(type){
        if(type=='D'){
          if(this.formRule.payDeposit==false){
            this.formRule.payDeposit=true;
          }else{
            this.formRule.payDeposit=false;
            this.formRule.checkedDepositList=[];
          }
        }
        else if(type=='C') {
          if(this.formRule.payCredit==false){
            this.formRule.payCredit=true;
          }else{
            this.formRule.payCredit=false;
            this.formRule.checkedCreditList=[];
          }
        }
      },
      //启用禁用状态修改
      ableDiscountRule(ableType){
        if(!this.formRule.ruleCode){
          return
        }
        let data={'ruleCode':this.formRule.ruleCode,'status':ableType}     //ruleCode
        let dataUrl= discountPostLink+'/fin-rules-osprest/rule/status/update.htm';
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            if(ableType=='VALID'){
              this.formRule.status='INVALID'
            }else if(ableType=='INVALID'){
              this.formRule.status='VALID'
            }
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      discountRules(){
        // 分摊shareSmList对象数组保存组装
        this.formRule.shareSmList=[];
        this.formRule.shareTotle=0;
        for(let i=0;i<this.formRule.shareList.length;i++){
          var shareSmObj={'shareMerchantCode':this.formRule.shareList[i].shareMerchantCode,'shareRate':this.formRule.shareList[i].shareRate}
          this.formRule.shareTotle+=parseFloat(this.formRule.shareList[i].shareRate);
          this.formRule.shareSmList.push(shareSmObj);
        }
        //银行卡
        this.formRule.payList=[];
        for(let m=0;m<this.formRule.payBankList.length;m++){
          if (this.formRule.payBankList[m] == 'BALANCE') {
            var payItemObj={ bankCodes:[],payType:this.formRule.payBankList[m]}
            this.formRule.payList.push(payItemObj);
          }
          else if(this.formRule.payBankList[m] == 'D'){
            this.formRule.checkedDepositCode=[];
            for(let s = 0; s < this.formRule.checkedDepositList.length; s++){
              this.formRule.checkedDepositCode.push(this.formRule.checkedDepositList[s].bankCode)
            }
            var payItemObj={ bankCodes:this.formRule.checkedDepositCode,payType:this.formRule.payBankList[m]}
            this.formRule.payList.push(payItemObj);
          }
          else if(this.formRule.payBankList[m] == 'C'){
            this.formRule.checkedCreditCode=[];
            for(let s = 0; s < this.formRule.checkedCreditList.length; s++){
              this.formRule.checkedCreditCode.push(this.formRule.checkedCreditList[s].bankCode)
            }
            var payItemObj={ bankCodes:this.formRule.checkedCreditCode,payType:this.formRule.payBankList[m]}
            this.formRule.payList.push(payItemObj);
          }
          else if(this.formRule.payBankList[m] == 'CRTXDFENQI'){
            var payItemObj={ bankCodes:[],payType:this.formRule.payBankList[m]}
            this.formRule.payList.push(payItemObj);
          }
        }
        //清空
        this.formRule.merchantStores[0].storeCodes=[];
        this.formRule.merchantStores[0].allStore=this.formRule.allStore;
        this.formRule.merchantStores[0].merchantCode=this.formRule.merchantCode;
        for(let j=0;j<this.formRule.storeTable.length;j++){
          var storestr=this.formRule.storeTable[j].storeCode;
          this.formRule.merchantStores[0].storeCodes.push(storestr)
        }
      },
      //保存折扣规则
      saveDiscountRule(formName){
        this.discountRules();// 信息获取
        /*if(!this.formRule.ruleName){
          this.$message.error('折扣名称不能为空！');
          return
        }
        if(!this.formRule.ruleType){
          this.$message.error('折扣类型不能为空！');
          return
        }
        if(!this.formRule.discountRate){
          this.$message.error('折扣(百分比)不能为空！');
          return
        }
        if(!this.formRule.limitType){
          this.$message.error('限额类型不能为空！');
          return
        }
        if(this.formRule.limitType!='UNLIMITED'){
          if(!this.formRule.limitValue){
            this.$message.error('限额值不能为空！');
            return
          }
        }
        if(!this.formRule.startDate){
          this.$message.error('有效开始时间不能为空！');
          return
        }
        if(!this.formRule.endDate){
          this.$message.error('有效结束时间不能为空！');
          return
        }
        if(!this.formRule.status){
          this.$message.error('状态不能为空！');
          return
        }
        if(!this.formRule.remark){
          this.$message.error('备注不能为空！');
          return
        }
        if(!this.formRule.merchantName){
          this.$message.error('可用商户不能为空！');
          return
        }
        if(!this.formRule.allUser){
          this.$message.error('可用会员等级不能为空！');
          return
        }
        if(!this.formRule.allPay){
          this.$message.error('可用支付方式不能为空！');
          return
        }*/
        this.$refs[formName].validate((valid) => {
            if (valid) {
              if(this.formRule.storeTable==''&&!this.formRule.allStore){
                this.$message.error('门店不能为空，请新增或导入门店！');
                return
              }
              if(this.formRule.shareTotle!=100){
                this.$message.error('分摊比例总和不是100%, 不能更新为启用状态！');
                return
              }
              this.$confirm('是否保存折扣信息?', '提示', {
                cancelButtonText: '取消',
                confirmButtonText: '确定',
                type: 'warning'
              }).then(() => {
                let dataUrl = discountPostLink + '/fin-rules-osprest/activityRule/saveOrUpdate.htm';
                let data = {
                  'allPay': this.formRule.allPay,
                  'allUser': this.formRule.allUser,
                  'discountRate': this.formRule.discountRate,
                  'endDate': this.filterDateStr(this.formRule.endDate, 13),
                  'id': this.formRule.id,
                  'limitType': this.formRule.limitType,
                  'limitValue': this.formRule.limitValue,
                  'merchantStores': this.formRule.merchantStores,
                  'payList': this.formRule.payList,
                  'remark': this.formRule.remark,
                  'ruleCode': this.formRule.ruleCode,
                  'ruleName': this.formRule.ruleName,
                  'ruleType': this.formRule.ruleType,
                  'shareList': this.formRule.shareSmList,
                  'startDate': this.filterDateStr(this.formRule.startDate, 13),
                  'status': this.formRule.status,
                  'userScope': this.formRule.userScope,
                }
                let params = {'data': data, 'token': this.getCookie('token'), 'transactionUuid': this.uuid()}
                this.$http.post(dataUrl, params).then((response) => {
                  console.log(response);
                  if (response.data.code === 'S0A00000') {
                    this.$router.push({path: '/home/discountRules'})
                    this.$message({showClose: true,duration:2000,type: 'success',message:'折扣规则保存成功!'});
                  } else {
                    this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
                  }
                })
              }).catch(() => {
                this.$message({showClose: true,duration:2000,type: 'info',message:'取消折扣规则信息保存！'});
              });
            }else{
              var that=this;
              setTimeout(function(){
                that.$refs[formName].resetFields();
              },10000)
            }
        });
      },
      // 返回列表页
      goBack(){
        this.$router.push({path: '/home/discountRules'})
      }
    },
    created(){
      this.formRule.ruleCode = this.$route.query.ruleCode;// 获取ruleCode
      this.getRuleCode()//获取折扣规则编码
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style lang="less" scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item {
    margin-bottom: 20px;
  }
  .payDepositList, .payCreditList{
    width: 540px;
  }
  .payDepositList span, .payCreditList span{
    color:#7794b3;
  }

</style>



