<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formTrade"  ref="formTrade"  id="formwrapper"    class="demo-form-inline crm-form" label-width="125px">

      <el-row>

        <div class="el-card-title">退款交易详情</div>

        <el-form-item label="退款单号" >
          <el-input v-model="formTrade.refundOrderNo" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="商户退款单号">
          <el-input v-model="formTrade.outRefundNo"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="退款用户号" >
          <el-input v-model="formTrade.payUserId"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>
        <el-form-item label="退款场景码">
          <el-input v-model="formTrade.channelBizCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="会员编号">
          <el-input v-model="formTrade.memberId" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="交易类型" >
          <el-select v-model="formTrade.tradeType" class="el-winps" disabled="disabled">
            <el-option label="支付" value="pay"></el-option>
            <el-option label="退款" value="girl"></el-option>
            <el-option label="撤销" value="girl"></el-option>
          </el-select>
        </el-form-item>

      </el-row>
      <el-row>

        <el-form-item label="商户名称">
          <el-input v-model="formTrade.merchantName" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="商户编号">
          <el-input v-model="formTrade.merchantCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>


        <el-form-item label="门店编号">
          <el-input v-model="formTrade.storeCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

      </el-row>
      <el-row>

        <el-form-item label="门店名称">
          <el-input v-model="formTrade.storeName" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="源商户交易流水号">
          <el-input v-model="formTrade.oriOutTradeNo" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="源支付订单号">
          <el-input v-model="formTrade.oriPayOrderNo" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="退款交易时间" >
          <el-date-picker v-model="formTrade.createTime" :editable="false"   class="el-winps" disabled="disabled" type="datetime"  placeholder="请输入签发日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="退款请求时间" >
          <el-date-picker v-model="formTrade.refundRequestTime" :editable="false"   class="el-winps" disabled="disabled" type="datetime"  placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="退款完成时间" >
          <el-date-picker v-model="formTrade.refundCompleteTime" :editable="false"   class="el-winps" disabled="disabled" type="datetime"  placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>

      </el-row>
      <el-row>

        <el-form-item label="实际退款金额" >
          <el-input v-model="formTrade.refundAmount"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="退款总金额" >
          <el-input v-model="formTrade.refundTotalAmount"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>


        <el-form-item label="退款状态" >
          <el-select v-model="formTrade.refundStatus" class="el-winps" disabled="disabled">
            <el-option label="冻结" value="FROZEN"></el-option>
            <el-option label="解冻" value="ACTIVE"></el-option>
            <el-option label="注销" value="CANCEL"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="退款原因" >
          <el-input type="textarea" class="el-winpl el-winph" v-model="formTrade.remark" disabled="disabled"></el-input>
        </el-form-item>
      </el-row>


      <el-row  >
        <div class="el-card-title">退款明细列表</div>
            <el-row>
              <el-table :data="refundTradelList" :fit="true" border  max-height="350" style="width: 100%">
                <el-table-column prop="refundOrderNo" align="center" label="华润通退款流水号"></el-table-column>
                <el-table-column prop="refundChannelTradeSn" align="center"  label="银行退款流水号" ></el-table-column>
                <el-table-column prop="payMethodName" align="center" label="退款方式" ></el-table-column>
                <el-table-column prop="refundAmount" align="center" label="实际金额" ></el-table-column>
                <el-table-column prop="promotionNo" align="center" label="营销规则编号" ></el-table-column>
                <el-table-column prop="createTime" align="center" label="交易时间" ></el-table-column>
                <el-table-column prop="tradeStatus" align="center" label="状态" ></el-table-column>
                <el-table-column prop="errorMsg" align="center" label="结果说明" ></el-table-column>
              </el-table>
              <el-row id="pagination" class="crm-pagination"  >
                <el-pagination
                  @current-change="handleCurrentChange"
                  layout="total, prev, pager, next, jumper"
                  :current-page.sync="pagination.currentPage"
                  :page-size.sync="pagination.pageSize"
                  :total.sync="pagination.total">
                </el-pagination>
              </el-row>
            </el-row>

      </el-row>

      <el-row >

        <el-form-item label=" ">

          <el-button type="info" class="el-winpMin"  size="big" @click="goContype">返回</el-button>

        </el-form-item>

      </el-row>

    </el-form>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {
      ElRow
    },
    data () {
      return {
        display: false,           //隐藏状态
        wrapperHeight: '',        //页面高度动态设置
        refundTradelList:[],         //银行卡
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //form 表单字段
        formTrade: {
          createTime:'',	        //退款交易时间	string
          refundAmount:'',	      //退款金额	string
          refundCompleteTime:'',	//退款完成时间	string
          refundOrderNo:'',	      //退款单号	string
          channelBizCode:'',      //退款场景码	string
          memberId:'',            //会员编号	number
          merchantCode:'',        //商户编号	string
          merchantName:'',        //商户名称	string
          oriOutTradeNo:'',       //源商户交易流水号	string
          oriPayOrderNo:'',       //源支付订单号	string
          outRefundNo:'',	        //商户退款单号	string
          payUserId:'',	          //退款用户号	number
          refundTotalAmount:'',	  //退款金额	string
          storeCode:'',	          //门店编码	string
          tradeType:'',	          //交易类型	string
          remark:'',        //退款原因	string
          refundRequestTime:'',   //退款请求时间	string
          refundStatus:'',	      //退款状态	string
          storeName:'',	          //门店名称	string
        },
      }
    },
    methods: {
      //获取钱包用户详情
      getInfoDetail(){
        let dataUrl= payTrade+'/payment/order/refundandcancel/basic.htm';
        let params={
          'refundOrderNo':this.formTrade.refundOrderNo,  //支付用户id
          'tradeType':this.formTrade.tradeType,     //支付交易类型
          'token':this.getCookie('token'),//token
          'transactionUuid':this.uuid()   //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            this.formTrade.refundAmount=response.data.data.refundAmount
            this.formTrade.channelBizCode=response.data.data.channelBizCode
            this.formTrade.refundCompleteTime=response.data.data.refundCompleteTime
            this.formTrade.createTime=response.data.data.createTime
            this.formTrade.memberId=response.data.data.memberId
            this.formTrade.merchantCode=response.data.data.merchantCode
            this.formTrade.merchantName=response.data.data.merchantName
            //this.formTrade.refundOrderNo=response.data.data.refundOrderNo
            this.formTrade.refundTotalAmount=response.data.data.refundTotalAmount
            this.formTrade.payMethodName=response.data.data.payMethodName
            //this.formTrade.payOrderNo=response.data.data.payOrderNo
            this.formTrade.payUserId=response.data.data.payUserId
            this.formTrade.remark=response.data.data.remark
            this.formTrade.refundRequestTime=response.data.data.refundRequestTime
            this.formTrade.oriOutTradeNo=response.data.data.oriOutTradeNo
            this.formTrade.oriPayOrderNo=response.data.data.oriPayOrderNo
            this.formTrade.outRefundNo=response.data.data.outRefundNo
            this.formTrade.refundStatus=response.data.data.refundStatus
            this.formTrade.storeCode=response.data.data.storeCode
            this.formTrade.storeName=response.data.data.storeName
            this.formTrade.tradeType=response.data.data.tradeType
          }else{
            //this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //获取银行卡详情
      getListDetail(offsetNum){
        let params={    //支付用户id
          'refundOrderNo':this.formTrade.refundOrderNo,//退款id
          'offset':offsetNum,//分页偏移量
          'token':this.getCookie('token'),//token
          'transactionUuid':this.uuid()//uuid
        }
          //重置当前页1
          if(offsetNum==0){
            this.pagination.currentPage=1
          }
          let dataUrl= payTrade+'/payment/order/refundandcancel/detaillist.htm';
          this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000'){
              this.refundTradelList =response.data.data
              this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
            }else{
              this.refundTradelList = [];
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
      },
      //返回钱包用户列表
      goContype(){
        this.$router.push({path: '/home/couponType'})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.getListDetail(pageOffset);
      },
    },
    created(){
      this.formTrade.refundOrderNo= this.$route.query.refundOrderNo;
      this.formTrade.tradeType= this.$route.query.tradeType;
      this.getInfoDetail()                    //获取钱包用户信息
      this.getListDetail(0); //获取表格支付交易和验证日志数据
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style lang="less" scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item {
    margin-bottom: 20px;
  }
</style>



