<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formWalletUser"  ref="formWalletUser"  id="formwrapper"    class="demo-form-inline crm-form" label-width="85px">

      <el-row>

        <div class="el-card-title">钱包用户详情</div>

        <el-form-item label="用户编号" prop="userId">
          <el-input v-model="formWalletUser.userId" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="姓名" prop="userName">
          <el-input v-model="formWalletUser.userName"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="手机号码" prop="userMobile">
          <el-input v-model="formWalletUser.userMobile"   class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="生日" prop="bornDate">
          <el-date-picker v-model="formWalletUser.bornDate" :editable="false"   class="el-winps" type="date"  disabled="disabled" placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>



        <el-form-item label="性别" prop="sex" >
          <el-input v-model="formWalletUser.sex"   class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="证件类型" prop="certType">
          <el-select v-model="formWalletUser.certType" class="el-winps" disabled="disabled">
            <el-option label="身份证" value="1"></el-option>
            <el-option label="护照" value="2"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="证件号码" prop="md5CertNo">
          <el-input v-model="formWalletUser.md5CertNo"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="签发机关" prop="signOrg">
          <el-input v-model="formWalletUser.signOrg"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="签发日期" prop="signDate">
          <el-date-picker v-model="formWalletUser.signDate" :editable="false"   class="el-winps" type="date" disabled="disabled"  placeholder="请输入签发日期"></el-date-picker>
        </el-form-item>


        <el-form-item label="有效日期" prop="expiryDate">
          <el-date-picker v-model="formWalletUser.expiryDate" :editable="false"   class="el-winps" type="date"  disabled="disabled" placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="身份证住址" prop="address">
          <el-input type="textarea" class="el-winpls el-winph" v-model="formWalletUser.address" disabled="disabled"  placeholder=" "></el-input>
        </el-form-item>
      </el-row>

      <el-row>
        <el-form-item label="钱包余额" prop="balanceAmt">
          <el-input v-model="formWalletUser.balanceAmt"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="渠道" prop="channelId">
          <el-input v-model="formWalletUser.channelId"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model="formWalletUser.status" class="el-winps" disabled="disabled">
            <el-option label="冻结" value="FROZEN"></el-option>
            <el-option label="解冻" value="ACTIVE"></el-option>
            <el-option label="注销" value="CANCEL"></el-option>
          </el-select>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="开户时间" prop="walletOpenTime">
          <el-date-picker v-model="formWalletUser.walletOpenTime" :editable="false" disabled="disabled"  class="el-winps" type="datetime"  placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>


        <el-form-item label="关联会员编号" prop="memberId" label-width="100px">
          <el-input v-model="formWalletUser.memberId"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item  label=" ">
          <el-checkbox disabled="disabled" v-model="formWalletUser.ocrFlg" >是否OCR验证</el-checkbox>
          <el-checkbox disabled="disabled" v-model="formWalletUser.balanceOpenFlag" >是否开通II类户</el-checkbox>
          <el-checkbox disabled="disabled" v-model="formWalletUser.livingBody" >是否活体检测</el-checkbox>
        </el-form-item>

      </el-row>

      <el-row  >
        <div class="el-card-title">钱包列表详情</div>
          <el-tabs class="el-tabs" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="银行卡" name="bankCard">

              <el-row>
                <el-table :data="bankCard.bankCardList" :fit="true" border max-height="300" style="width: 100%">
                  <el-table-column prop="md5CardNo" align="center"  width="180" label="银行卡号"></el-table-column>
                  <el-table-column prop="cardStatus" align="center" width="90"  label="状态" ></el-table-column>
                  <el-table-column prop="bankAcctLevelromeSymbol" align="center" label="账户等级" ></el-table-column>
                  <el-table-column prop="bankCardType" align="center" label="卡类型" ></el-table-column>
                  <el-table-column prop="bankShortName" align="center" width="100"  label="发卡行简称" ></el-table-column>
                  <el-table-column prop="bankCode" align="center"  width="100" label="发卡行代码" ></el-table-column>
                  <el-table-column prop="bindMobile" align="center"  width="120" label="银行预留手机号" ></el-table-column>
                  <el-table-column prop="securityCard" align="center" width="110" label="是否安全卡" ></el-table-column>
                  <el-table-column prop="livingBody" align="center" width="110" label="是否活体检测" ></el-table-column>
                  <el-table-column prop="txnLmt" align="center" label="单笔限额" ></el-table-column>
                  <el-table-column prop="dayLmt" align="center" width="90" label="每日限额" ></el-table-column>
                  <el-table-column prop="signNo" align="center"  width="270" label="签约协议号" ></el-table-column>
                  <el-table-column prop="signTime" align="center"  width="160" label="签约时间" ></el-table-column>
                  <el-table-column prop="signChannel" align="center" label="签约渠道" ></el-table-column>
                </el-table>
                <el-row id="pagination" class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="bankCard.pagination.currentPage"
                    :page-size.sync="bankCard.pagination.pageSize"
                    :total.sync="bankCard.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
            <el-tab-pane label="支付交易" name="payTrade">

              <el-row>
                <el-table :data="payTrade.payTradeList" :fit="true" border  max-height="300" style="width: 100%">
                  <el-table-column prop="payOrderNo" align="center" width="" label="华润通交易流水号"></el-table-column>
                  <el-table-column prop="outTradeNo" align="center"  label="商户订单号" ></el-table-column>
                  <el-table-column prop="payUserId" align="center" label="支付用户编号" ></el-table-column>
                  <el-table-column prop="channelBizCode" align="center" label="支付场景码" ></el-table-column>
                  <el-table-column prop="tradeType" align="center" label="交易类型" ></el-table-column>
                  <el-table-column prop="payAmount" align="center" label="订单金额" ></el-table-column>
                  <el-table-column prop="cashAmount" align="center" label="实付/退金额" ></el-table-column>
                  <el-table-column prop="merchantName" align="center" label="交易商户" ></el-table-column>
                  <el-table-column prop="storeName" align="center" label="交易门店" ></el-table-column>
                  <el-table-column prop="tradeTime" align="center" label="交易时间" ></el-table-column>
                  <el-table-column prop="status" align="center" label="状态" ></el-table-column>
                  <el-table-column prop="remark" align="center" label="结果说明" ></el-table-column>
                  <el-table-column align="center" label="操作" >
                    <template slot-scope="scope">
                      <span  class="el-alink" @click="handleDetail(scope.$index, scope.row)">查看</span>
                    </template>
                  </el-table-column>
                </el-table>
                <el-row  class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="payTrade.pagination.currentPage"
                    :page-size.sync="payTrade.pagination.pageSize"
                    :total.sync="payTrade.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
            <el-tab-pane label="余额交易" name="balanceTrade">
              <el-row>
                <el-table :data="balanceTrade.balanceTradeList" :fit="true" border  max-height="300" style="width: 100%">
                  <el-table-column prop="outReferNo" align="center"  width="270" label="华润通交易流水号"></el-table-column>
                  <el-table-column prop="bankCode" align="center"  width="180"  label="银行交易流水号" ></el-table-column>
                  <el-table-column prop="userId" width="110" align="center" label="支付用户编号" ></el-table-column>
                  <el-table-column prop="txnType" align="center" label="交易类型" ></el-table-column>
                  <el-table-column prop="md5CardNo" width="180" align="center" label="交易安全卡号" ></el-table-column>
                  <el-table-column prop="txnAmt" width="80" align="center" label="交易金额" ></el-table-column>
                  <el-table-column prop="status" align="center" label="状态" ></el-table-column>
                  <el-table-column prop="result"  align="center" label="结果说明" ></el-table-column>
                  <el-table-column prop="createTime" width="160" align="center" label="交易时间" ></el-table-column>
                  <el-table-column prop="updateTime" width="160" align="center" label="处理时间" ></el-table-column>
                </el-table>
                <el-row  class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="balanceTrade.pagination.currentPage"
                    :page-size.sync="balanceTrade.pagination.pageSize"
                    :total.sync="balanceTrade.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
            <el-tab-pane label="身份证照片" name="IdCard">
              <el-row>
                <div class="el-tab-photoBox">
                  <span class="el-span">身份证照片面:</span>
                  <img :src="IdCard.frontPhotoData"/>
                </div>
                <div class="el-tab-photoBox">
                  <span class="el-span">身份证国徽面:</span>
                  <img :src="IdCard.backPhotoData"/>
                </div>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="人脸照片" name="face">
              <el-row>
                <div class="el-tab-photoBox">
                  <span class="el-span">人脸照片1:</span>
                  <img :src="face.frontPhotoData"/>
                </div>
                <div class="el-tab-photoBox">
                  <span class="el-span">人脸照片2:</span>
                  <img :src="face.backPhotoData"/>
                </div>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="状态日志" name="statusLog">

              <el-row>
                <el-table :data="statusLog.statusLogList" :fit="true" border  max-height="300" style="width: 100%">
                  <el-table-column prop="operateNo" align="center" label="操作编号"></el-table-column>
                  <el-table-column prop="oprType" align="center"  label="操作类型" ></el-table-column>
                  <el-table-column prop="oprTime" align="center" label="操作时间" ></el-table-column>
                  <el-table-column prop="oprRemark" align="center" label="操作原因" ></el-table-column>
                  <el-table-column prop="oprByName" align="center" label="操作用户" ></el-table-column>
                </el-table>
                <el-row  class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="statusLog.pagination.currentPage"
                    :page-size.sync="statusLog.pagination.pageSize"
                    :total.sync="statusLog.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
          </el-tabs>
      </el-row>

      <el-row class="ln-ac">

          <el-button type="info" class="el-winpMin"  size="big" @click="goContype">返回</el-button>

      </el-row>

    </el-form>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {
      ElRow
    },
    data () {
      return {
        display: false,           //隐藏状态
        wrapperHeight: '',        //页面高度动态设置
        activeName: 'bankCard',     //选项卡默认值
        bankCard:{
          bankCardList:[],         //银行卡
          //表格分页
          pagination: {
            currentPage:1,//当前页
            pageSize:4,
            total:0
          },
        },
        payTrade:{
          payTradeList:[],         //支付交易
          //表格分页
          pagination: {
            currentPage:1,//当前页
            pageSize:20,
            total:0
          },
        },
        balanceTrade:{
          balanceTradeList:[],         //余额支付交易
          //表格分页
          pagination: {
            currentPage:1,//当前页
            pageSize:20,
            total:0
          },
        },
        statusLog:{
          statusLogList:[],         //余额支付交易
          //表格分页
          pagination: {
          currentPage:1,//当前页
            pageSize:20,
            total:0
        },
       },
        IdCard:{
          frontPhotoData:{},      //正面身份证照片
          backPhotoData:{},       //反面身份证照片
        },
        face:{
          frontPhotoData:{},      //人脸1
          backPhotoData:{},       //人脸2
        },
        //form 表单字段
        formWalletUser: {
          address:'',			    //OCR识别出来的住址,
          balanceAmt:'',	    //账户余额，单位分
          balanceOpenFlag:'',	//是否开通II类户	string	是为Y ，否为N
          bornDate:'',		    //OCR识别出来的出生日期, yyyy-MM-dd
          cerTypeDes:'',      //证件类型描述		证件类型描述，身份证，护照等
          certType:'',	      //证件类型
          channelId:'',	      //渠道(开户渠道)	string	用户创建时的渠道
          expiryDate:'',		  //OCR识别出来的有效日期/有效期限的结束日期，yyyy-MM-dd
          livingBody:'',		  //是否已通过活体检测，Y是，N否
          md5CertNo:'',       //MD5证件证号(解密后)	string
          memberId:'',	      //会员编号	string
          ocrFlg:'',	        //是否已通过OCR认证，Y是，N否
          sex:'',             //身份证读出来的性别
          signDate:'',		    //OCR识别出来的证件签发日期/有效期开始日期，yyyy-MM-dd,
          signOrg:'',		      //OCR识别出来的签发单位，身份证上的签发机关
          status:'',	        //ACTIVE-有效，FROZEN-冻结，CANCEL-注销。锁定状态放支付密码
          statusDes:'',	      //用户状态描述	string	有效，冻结，注销
          userId:'',	        //用户编号，支付用户id	number
          userMobile:'',	    //用户注册的手机号	string
          userName:'',	      //用户姓名	string
          walletOpenTime:'',	//开户时间	string	钱包功能开通时间 yyyy-MM-dd hh:mm:ss
        },
      }
    },
    methods: {
      //获取钱包用户详情
      getInfoDetail(){
        let dataUrl= walletUser+'/fin-account-osprest/account/userDetail.htm';
        let data={
          userId:this.formWalletUser.userId,            //支付用户id
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
              this.formWalletUser.address=response.data.data.address//OCR识别出来的住址,
              this.formWalletUser.balanceAmt=response.data.data.balanceAmt//账户余额，单位分
              this.formWalletUser.balanceOpenFlag=response.data.data.balanceOpenFlag//是否开通II类户	string	是为Y ，否为N
              if(this.formWalletUser.balanceOpenFlag=='Y'){
                this.formWalletUser.balanceOpenFlag=true;
              }else{
                this.formWalletUser.balanceOpenFlag=false;
              }
              this.formWalletUser. bornDate=this.filterDate(response.data.data.bornDate,'YMDHMS')//OCR识别出来的出生日期, yyyy-MM-dd
              this.formWalletUser.cerTypeDes=response.data.data.cerTypeDes//证件类型描述		证件类型描述，身份证，护照等
              this.formWalletUser.certType=response.data.data.certType//证件类型
              this.formWalletUser.channelId=response.data.data.channelId //渠道(开户渠道)	string	用户创建时的渠道
              this.formWalletUser.expiryDate=this.filterDate(response.data.data.expiryDate,'YMDHMS')//OCR识别出来的有效日期/有效期限的结束日期，yyyy-MM-dd
              this.formWalletUser.livingBody=response.data.data.livingBody//是否已通过活体检测，Y是，N否
              if(this.formWalletUser.livingBody=='Y'){
                this.formWalletUser.livingBody=true;
              }else{
                this.formWalletUser.livingBody=false;
              }
              this.formWalletUser.md5CertNo=response.data.data.md5CertNo//MD5证件证号(解密后)	string
              this.formWalletUser.memberId=response.data.data.memberId //会员编号	string
              this.formWalletUser.ocrFlg=response.data.data.ocrFlg//是否已通过OCR认证，Y是，N否
              if(this.formWalletUser.ocrFlg=='Y'){
                this.formWalletUser.ocrFlg=true;
              }else{
                this.formWalletUser.ocrFlg=false;
              }
              this.formWalletUser.sex=response.data.data.sex//身份证读出来的性别
              this.formWalletUser.signDate=this.filterDate(response.data.data.signDate,'YMDHMS')//OCR识别出来的证件签发日期/有效期开始日期，yyyy-MM-dd,
              this.formWalletUser.signOrg=response.data.data.signOrg//OCR识别出来的签发单位，身份证上的签发机关
              this.formWalletUser.status=response.data.data.status//ACTIVE-有效，FROZEN-冻结，CANCEL-注销。锁定状态放支付密码
              this.formWalletUser.statusDes=response.data.data.statusDes //用户状态描述	string	有效，冻结，注销
              this.formWalletUser.userMobile=response.data.data.userMobile//用户注册的手机号	string
              this.formWalletUser.userName=response.data.data.userName //用户姓名	string
            /*  this.formWalletUser.userId=response.data.data.userId //用户id*/
              this.formWalletUser.walletOpenTime=this.filterDate(response.data.data.walletOpenTime,'YMDHMS')//开户时间	string	钱包功能开通时间 yyyy-MM-dd hh:mm:ss

          }else{
            //this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //获取银行卡详情  type 表格类型
      getListDetail(type,offsetNum){
        let data={    //支付用户id
          userId:this.formWalletUser.userId,
          offset:offsetNum,
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        //card 银行卡
        if(type=='bankCard'){
          //重置当前页1
          if(offsetNum==0){
            this.bankCard.pagination.currentPage=1
          }
          let dataUrl= walletUser+'/fin-account-osprest/account/bankDetail.htm';
          this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000'){
              this.bankCard.bankCardList =response.data.data.recordList
              this.bankCard.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
            }else{
              this.bankCard.bankCardList = [];
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //交易类型
        else if(type=='payTrade'){
          //重置当前页1
          if(offsetNum==0){
            this.payTrade.pagination.currentPage=1
          }
          let dataUrl= payTrade+'/payment/order/payandtopup/list.htm';
          let params={    //支付用户id
            'payUserId':this.formWalletUser.userId,
            'offset':offsetNum,
            'token':this.getCookie('token'),
            'transactionUuid':this.uuid()
          }
          this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000'){
              this.payTrade.payTradeList =response.data.data;
              this.payTrade.pagination.total=parseInt(response.data.totalRecordCount); // 总数
            }else{
              this.payTrade.payTradeList = [];
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //余额交易类型
        else if(type=='balanceTrade'){
          //重置当前页1
          if(offsetNum==0){
            this.balanceTrade.pagination.currentPage=1
          }
          let dataUrl= walletUser+'/fin-account-osprest/account/userTrace.htm';
          let data={    //支付用户id
            userId:this.formWalletUser.userId,
            offset:offsetNum,
          }
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.balanceTrade.balanceTradeList =response.data.data.recordList;
              this.balanceTrade.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
            }else{
              this.balanceTrade.balanceTradeList = []
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //身份证照片
        else if(type=='IdCard'){
          let dataUrl= walletUser+'/fin-account-osprest/account/IdcardPhoto.htm';
          let data={    //支付用户id
            userId:this.formWalletUser.userId,
          }
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.IdCard.frontPhotoData =response.data.data.frontPhotoData;
              this.IdCard.backPhotoData =response.data.data.backPhotoData;
            }else{
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //人脸照片
        else if(type=='face') {
          let dataUrl= walletUser+'/fin-account-osprest/account/IdcardPhoto.htm';
          let data={    //支付用户id
            userId:this.formWalletUser.userId,
          }
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.face.frontPhotoData =response.data.data.frontPhotoData;
              this.face.backPhotoData =response.data.data.backPhotoData;
            }else{
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //状态日志
        else if(type=='statusLog'){
          //重置当前页1
          if(offsetNum==0){
            this.statusLog.pagination.currentPage=1
          }
          let dataUrl= walletUser+'/fin-account-osprest/account/statusLog.htm';
          let data={    //支付用户id
            userId:this.formWalletUser.userId,
            offset:offsetNum,
          }
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.statusLog.statusLogList =response.data.data.recordList
              this.statusLog.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
            }else{
              this.statusLog.statusLogList = [];
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
      },
      //表格 查看  编辑  禁用 操作
      handleDetail(index, row){
        this.$router.push({path: '/home/payTradeList/payTradeDetail', query: {payOrderNo: row.payOrderNo,tradeType:row.tradeType}})
      },
      //切换选项卡点击操作
      handleClick(tab, event) {
        console.log(tab, event);
        //表格请求的数据
        this.getListDetail(tab.paneName,0);
      },
      //返回钱包用户列表
      goContype(){
        this.$router.push({path: '/home/couponType'})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1));
        this.getListDetail(this.activeName,pageOffset)
      },
    },
    created(){
      this.formWalletUser.userId= this.$route.query.userId;
      this.getInfoDetail()                 //获取钱包用户信息
      this.getListDetail(this.activeName,0); //获取银行卡数据
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style lang="less" scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item {
    margin-bottom: 20px;
  }
  .el-tab-photoBox{
    width: 50%;
    float: left;
    img{
      display: inline-block;
      width: 90%;
    }
  }
</style>



