<template>
  <div class="crm-main" >
    <el-form :inline="true" :model="billFileFrom" id="formwrapper"  class="demo-form-inline crm-form" label-width="90px">
      <el-row>
        <el-form-item label="支持渠道">
          <el-select v-model="billFileFrom.billChannelCodes" class="el-winp" filterable placeholder="请选择">
            <el-option v-for="item in channelList"  :key="item.index"  :label="item.label" :value="item.value"></el-option>
          </el-select>

        </el-form-item>
        <el-form-item label="提交人">
          <el-select v-model="billFileFrom.createdBy" class="el-winp" placeholder=" ">
            <el-option label="全部" value="" ></el-option>
          </el-select>
        </el-form-item>
      </el-row>
      <el-row>
        <el-form-item label="导入日期起" >
          <el-date-picker v-model="billFileFrom.beginTime"    class="el-winp"  type="date" :editable="false" placeholder="导入日期起"></el-date-picker>
        </el-form-item>
        <el-form-item label="导入日期止" >
          <el-date-picker v-model="billFileFrom.endtime"  class="el-winp"  type="date" :editable="false" placeholder="导入日期止"></el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" class="el-btnw"  icon="el-icon-search" @click="handleInquire(0)">查询</el-button>
          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <el-row>
      <el-table :data="billFileData" :fit="true" border  :max-height="wrapperHeight"   style="width: 100%">
        <el-table-column prop="fileTradeNo" align="center" label="流水号" min-width="180"></el-table-column>
        <el-table-column prop="importDate" align="center" :formatter="formatcreate" label="导入日期" min-width="120"></el-table-column>
        <el-table-column prop="fileName" align="center" label="文件名" min-width="100"></el-table-column>
        <el-table-column prop="beginTradeTime" align="center" :formatter="formatbegin" label="起始日期" min-width="120"></el-table-column>
        <el-table-column prop="endTradeTime" align="center" :formatter="formatend" label="终止日期" min-width="120"></el-table-column>
        <el-table-column prop="status" align="center" label="状态" min-width="70">
          <template slot-scope="scope">
            <span v-if="scope.row.status=='01'">已导入</span>
            <span v-if="scope.row.status=='02'">已对账</span>
          </template>
        </el-table-column>
        <el-table-column prop="createBy" align="center" label="提交人" min-width="80"></el-table-column>
        <el-table-column  align="center" label="操作" min-width="70">
          <template slot-scope="scope">
            <span class="el-alink" @click="handleDetail(scope.$index, scope.row)">查看明细</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size="pagination.pageSize"
          :total="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        wrapperHeight:'',
        billFileFrom: {
          billChannelCodes:[],              //对账渠道编码
          createdBy:'',                     //记录数量
          beginTime:(Date.now()- 86400000), //导入日期起
          endtime:Date.now(),               //导入日期止
          //起始偏移量
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:30,
          total:0
        },
        billFileData: [],
        channelList:[],
      }
    },
    methods: {
      //格式化导入时间
      formatcreate(row, column){
        var createTime='';
        if(row.createTime){
          createTime=this.filterDate(row.createTime,'YMDHMS');
        }
        return createTime;
      },
      //格式化时间
      formatbegin(row, column){
        var beginTradeTime='';
        if(row.beginTradeTime){
          beginTradeTime=this.filterDate(row.beginTradeTime,'YMDHMS');
        }
        return beginTradeTime;
      },
      //格式化时间
      formatend(row, column){
        var endTradeTime='';
        if(row.endTradeTime){
          endTradeTime=this.filterDate(row.endTradeTime,'YMDHMS');
        }
        return endTradeTime;
      },
      loadSearchRoad() {
        let dataUrl= billLink+'/payment/bill/rel/pay/channel/list.htm';
        this.$http.post(dataUrl).then((response)=>{
          console.log(response)
         if(response.data.code === 'S0A00000'){
           response.data.data.forEach((result, index) => {
             this.channelList.push({id:index,label: result.billChannelName,value: result.billChannelCodes})
             if(result.billChannelName=='华润银行'){
               this.billFileFrom.billChannelCodes=result.billChannelCodes; //对账渠道编码
             }
           })
          }else{
           this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      handleSelect(item) {
        this.billFileFrom.billChannelCodes=item.id; //对账渠道编码
      },
      // 表单查询
      handleInquire(offsetNum) {
       if(!this.billFileFrom.beginTime){
         this.$message({showClose: true,duration:2000,type: 'error',message:'请输入导入日期起！'});
          return
        }
        if(!this.billFileFrom.endtime){
          this.$message({showClose: true,duration:2000,type: 'error',message:'请输入导入日期止！'});
          return
        }
        if(this.billFileFrom.beginTime>=this.billFileFrom.endtime){
          this.$message({showClose: true,duration:2000,type: 'error',message:'请导入日期起必须小于导入日期止！'});
          return
        }
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let dataUrl= billLink+'/payment/bill/file/list.htm';
        var beginTime=this.filterDateStr(this.billFileFrom.beginTime,10);
        var endtime=this.filterDateStr(this.billFileFrom.endtime,10);
        let params={
          billChannelCodes:this.billFileFrom.billChannelCodes, //对账渠道编码
          createdBy:this.billFileFrom.createdBy,             //记录数量
          beginTime:beginTime,
          endtime:endtime,
          offset:offsetNum,                                 //起始偏移量
          sysId:'',
          'token':this.getCookie('token'), //token
          'transactionUuid':this.uuid()    //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
         if(response.data.code === 'S0A00000'){
            this.billFileData =response.data.data
            this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
          }else{
           this.billFileData =[];
           this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      // 表单重置
      handleReset() {
        let billFileFrom={
          billChannelCodes:[],              //对账渠道编码
          createdBy:'',                     //记录数量
          beginTime:'',                     //导入日期起
          endtime:'',                       //导入日期止
          offset:0,                         //起始偏移量
        }
        this.billFileFrom=billFileFrom
      },
      //查看明细
      handleDetail(index, row){
        this.$router.push({path: '/home/billMatchFile/billMatchFileDetail', query: {partnerId:row.partnerId,billChannelCode:row.billChannelCode,fileName:row.fileName}})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*30);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.loadSearchRoad();
      var that=this;
      setTimeout(function(){that.handleInquire(0);},1000)
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



