<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formRule" id="formwrapper"  class="demo-form-inline crm-form" label-width="98px">

      <el-row>

        <el-form-item label="规则编号">
          <el-input v-model="formRule.ruleCode" class="el-winp" placeholder=" " ></el-input>
        </el-form-item>

        <el-form-item label="规则名称">
          <el-input v-model="formRule.ruleName" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="状态">
          <el-select v-model="formRule.status" class="el-winp" placeholder=" " >
            <el-option label="全部" value=""></el-option>
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="启用" value="VALID"></el-option>
            <el-option label="禁用" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="商户名称">
          <search-Merchant v-on:communicate="setMerchantInfo" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formRule.merchantName"></search-Merchant>
        </el-form-item>

        <el-form-item label="门店名称">
          <el-autocomplete v-model="formRule.storeName" :fetch-suggestions="querySearchAsyncStore" placeholder="请输入并选择门店" class="el-winp"  :disabled="!formRule.merchantCode" @select="setStoreInfo">
          </el-autocomplete>
        </el-form-item>

      </el-row>

      <el-row>


        <el-form-item label="有效开始时间">
          <el-date-picker v-model="formRule.startDate" :editable="false" class="el-winp"  type="datetime"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="有效结束时间">
          <el-date-picker v-model="formRule.endDate" :editable="false" class="el-winp"  type="datetime"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button type="primary" class="el-btnw el-marl" icon="el-icon-plus" @click="handleAdd">新增</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>
      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData"  border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="ruleCode" align="center" label="折扣规则编码" ></el-table-column>
        <el-table-column prop="ruleName" align="center" label="折扣规则名称" ></el-table-column>
        <el-table-column prop="ruleTypDesc" align="center" label="折扣规则类型" ></el-table-column>
        <el-table-column prop="merchantName" align="center" label="商户名称" ></el-table-column>
        <el-table-column prop="startDate" align="center" :formatter="forstartDate" label="有效开始日期" ></el-table-column>
        <el-table-column prop="endDate" align="center" :formatter="forendDate" label="有效结束时间" ></el-table-column>
        <el-table-column prop="status" align="center" label="状态" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='VALID'">启用</span>
            <span v-if="scope.row.status=='INVALID'">禁用</span>
            <span v-if="scope.row.status=='NEW'">新建</span>
          </template>
        </el-table-column>
        <el-table-column  align="center" label="操作">
          <template slot-scope="scope">
            <span  class="el-alink" v-if="scope.row.status=='NEW'" :class="scope.row.status=='NEW'?'el-disable':''" >查看</span>
            <span  class="el-alink" v-if="scope.row.status=='INVALID'||scope.row.status=='VALID'" @click="handleDetail(scope.$index, scope.row)">查看</span>
            <span  class="el-alink"  @click="handleEdit(scope.$index, scope.row)"  >编辑</span>
            <span  class="el-alink"   @click="handleDisabled(scope.$index, scope.row)" >
              <i v-if="scope.row.status=='INVALID'||scope.row.status=='NEW'">启用</i>
              <i v-if="scope.row.status=='VALID'">禁用</i>
            </span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
         @current-change="handleCurrentChange"
         layout="total, prev, pager, next, jumper"
         :current-page.sync="pagination.currentPage"
         :page-size="pagination.pageSize"
         :total="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>

  </div>
</template>
<script>
  import searchMerchant from 'components/common/search'
  export default {
    components: {searchMerchant },
    name: 'crmMenu',
    data () {
      return {
        display:false,
        time:'',
        wrapperHeight:'',
        formRule:{
          merchantCode:'',      //商户编号
          merchantName:'',      //商户名字
          offset:0,            //请求分页的offset
          ruleCode:'',          //规则编码
          ruleName:'',          //规则名称
          startDate:'',         //有效开始日期
          endDate: '',          //有效结束日期
          status: '',           //状态
          storeCode:'',         //可用门店编码
          storeName:'',         //可用门店名称
        },
        inquiryTable:{
          merchantCode:'',      //商户编号
          merchantName:'',      //商户名字
          offset:0,            //请求分页的offset
          ruleCode:'',          //规则编码
          ruleName:'',          //规则名称
          startDate:'',         //有效开始日期
          endDate: '',          //有效结束日期
          status: '',           //状态
          statusDesc:'',        //状态描述
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        tableData: [],
      }
    },
    methods: {
      // 可用门店选择
      querySearchAsyncStore(queryString, cb){
        var self=this;
        self.formRule.storeCode=""
        if(!queryString){
          cb([])
          return
        }
        var url=merchantPostLink+"/merchant/admin/store/storeQueryList.htm";
        var postData={
          sysId:"T0000017",
          token:this.getCookie('token'),
          transactionUuid:this.uuid(),
          storeName:queryString,
          merchantCode:self.formRule.merchantCode
        };
        this.$http.post(url, postData).then(function(res){
          res=res.data
          if(res.code=="S0A00000"){
            var list=[];
            res.data.forEach(function(e){
              if(e.storeName==queryString){
                self.formRule.storeCode=e.storeCode;
              }
              list.push({value: e.storeName, code: e.storeCode})
            })
            cb(list)
          }
          else{
            cb([])
            self.formRule.storeCode=""
          }
        })
      },
      setStoreInfo(item){
        this.formRule.storeName = item.value
        this.formRule.storeCode = item.code
      },
      setMerchantInfo(item){
        this.formRule.merchantName = item.value   // form  value
        this.formRule.merchantCode = item.id // form id
        this.formRule.storeName = ""
        this.formRule.storeCode = ""
      },
      // 折扣规则新增
      handleAdd(){
        this.$router.push({path: '/home/discountRules/discountRulesAdd'})
      },
      //重置清空form表单内容
      handleReset() {
        let formRule={
          merchantCode:'',
          merchantName:'',
          offset:0,
          ruleCode:'',
          ruleName:'',
          startDate:'',
          endDate: '',
          status: '',
          storeCode:'',
          storeName:'',
        }
        this.formRule=formRule
      },
      // 表格查询
      handleInquire(offsetNum) {
        var startDate =this.filterDateStr(this.formRule.startDate,13);
        var endDate= this.filterDateStr(this.formRule.endDate,13);
        if(startDate==0||endDate==0){
          startDate=null;
          endDate=null;
        }
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }

        let dataUrl= discountPostLink+'/fin-rules-osprest/rule/queryByPage.htm';
        let data={
          merchantCode:this.formRule.merchantCode,
          offset:offsetNum,
          ruleCode:this.formRule.ruleCode,
          ruleName:this.formRule.ruleName,
          startDate:startDate,
          endDate: endDate,
          status: this.formRule.status,
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },

      // 更新时间格式化
      forstartDate(row, column){
        return this.filterDate(row.startDate,'YMDHMS');
       },
      forendDate(row, column){
        return this.filterDate(row.endDate,'YMDHMS');
      },
      handleDetail(index, row){
        this.$router.push({path: '/home/discountRules/discountRulesDetail', query: { ruleCode:row.ruleCode}})
      },
      handleEdit(index, row) {
        this.$router.push({path: '/home/discountRules/discountRulesEdit', query: { ruleCode:row.ruleCode}})
      },
      //表格禁用启用设置
      handleDisabled(index, row) {
        if(!row.ruleCode){
          return
        }
        if(row.status=='VALID'){
          var status='INVALID'
        }
        else if(row.status=='INVALID'){
          var status='VALID'
        }else if(row.status=='NEW'){
          var status='VALID'
        }

        if(row.status=='VALID'){
          this.$confirm('是否禁用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data={'ruleCode':row.ruleCode,'status':status}     //ruleCode
            let dataUrl= discountPostLink+'/fin-rules-osprest/rule/status/update.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response)=>{
              if(response.data.code === 'S0A00000'){
                  row.status='INVALID'
                  row.statusDesc='禁用'
              }else{
                this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
              }
            })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消禁用操作！'});
          });

        }
        else if(row.status=='INVALID'){
          this.$confirm('是否启用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
          let data={'ruleCode':row.ruleCode,'status':status}     //ruleCode
          let dataUrl= discountPostLink+'/fin-rules-osprest/rule/status/update.htm';
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000'){
                row.status='VALID'
                row.statusDesc='启用'
            }else{
              this.$message.error(response.data.msg);
            }
          })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消启用操作！'});
          });
        }else if(row.status=='NEW'){
          this.$confirm('是否启用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
          let data={'ruleCode':row.ruleCode,'status':status}     //ruleCode
          let dataUrl= discountPostLink+'/fin-rules-osprest/rule/status/update.htm';
          let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
          this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000'){
                row.status='VALID'
                row.statusDesc='启用'
            }else{
              this.$message.error(response.data.msg);
            }
          })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消启用操作！'});
          });
        }
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



