<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formInline" id="formwrapper"  class="demo-form-inline crm-form" label-width="90px">
      <el-row>
          <el-form-item label="商户">
            <search-Merchant v-on:communicate="setMerchantInfo"  description="merchant" placeholder="请输入商户名称" :empty="formInline.merchantName"></search-Merchant>
          </el-form-item>
        <el-form-item label="结算户">

          <search-Merchant v-on:communicate="setSettlementInfo" description="settlement" placeholder="请选择结算户" :empty="formInline.subMerchantName"></search-Merchant>

        </el-form-item>
        <el-form-item label="结算单号">
          <el-input v-model="formInline.settleOrderNo" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="状态">
          <el-select v-model="formInline.settleOrderStatus" class="el-winp" placeholder=" ">
            <el-option label="已生成" value="00"></el-option>
            <el-option label="已确认" value="01|02|03"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="日清结算起">
          <el-date-picker v-model="formInline.generatorBeginDate"  :editable="false" class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="日清结算止">
          <el-date-picker v-model="formInline.generatorEndDate"  :editable="false" class="el-winp" type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>
          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>
        </el-form-item>
     </el-row>
    </el-form>
    <el-row>
      <el-table :data="tableData" fit border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="choose" align="center" label="选择" width="55" >
          <template slot-scope="scope">
            <input type="checkbox" class="el-checkbox__inner" v-show="!scope.row.settleOrderNo" :id="'scopeChecked'+scope.$index" >
            <input type="checkbox" class="el-checkbox__inner" v-show="scope.row.settleOrderNo" disabled="disabled">
          </template>
        </el-table-column>
        <el-table-column prop="settleOrderNo" align="center" label="结算单号"></el-table-column>
        <el-table-column prop="settleOrderPreNo" align="center" width="100" label="预结算单号" ></el-table-column>
        <el-table-column prop="merchantName" align="center" label="商户" ></el-table-column>
        <el-table-column prop="subMerchantName" align="center" label="结算户" ></el-table-column>
        <el-table-column prop="settleAmount" align="center" width="140" label="待结算金额(元)" >
          <template slot-scope="scope">
            {{scope.row.settleAmount/100}}
          </template>
        </el-table-column>
        <el-table-column prop="settleGeneratorTime" align="center" width="160" label="日清开始日期" ></el-table-column>
        <el-table-column prop="settleGeneratorTime" align="center" width="160" label="日清结束日期" ></el-table-column>
        <el-table-column prop="settleOrderStatus" align="center" label="状态" ></el-table-column>
        <el-table-column prop="settleConfirmTime" align="center" width="160" label="结算确认日期" ></el-table-column>
        <el-table-column prop="remark" align="center" label="备注" ></el-table-column>
        <el-table-column  align="center" label="操作">
          <template slot-scope="scope">
            <span class="el-alink" @click="handleDetail(scope.$index, scope.row)">详情</span>
            <span class="el-alink" @click="handleEdit(scope.$index, scope.row)"  v-if="display">编辑</span>
            <span class="el-alink" @click="handleDelete(scope.$index, scope.row)" v-if="display">下载</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
        <span class="el-lookBtn" @click="handleAllDetail">查看结算详情</span>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  import searchMerchant from 'components/common/search'
export default {
  components: {searchMerchant},
  name: 'crmMenu',
  data () {
    return {
      display:false,
      wrapperHeight:'',
      formInline: {
        merchantName: '',
        merchantCode:'',
        subMerchantName:'',
        subMerchantCode:'',
        settleOrderNo:'',
        settleOrderStatus:'',
        generatorBeginDate: '',
        generatorEndDate:'',
      },
      //表格分页
      pagination: {
        currentPage:1,//当前页
        pageSize:20,
        total:0
      },
      tableData: [],
      settleOrderArrays:[],
      settleAmountTotle:0,
      disChecked:false
    }
  },
  methods: {
    //商户获取值
    setMerchantInfo(item){
      this.formInline.merchantName = item.value
      this.formInline.merchantCode=item.id
    },
    //结算户
    setSettlementInfo(item){
      this.formInline.subMerchantName = item.value
      this.formInline.subMerchantCode = item.id
    },
    handleInquire(offsetNum) {
      if(this.formInline.generatorBeginDate&&this.formInline.generatorEndDate){
        if(this.formInline.generatorBeginDate>=this.formInline.generatorEndDate){
          this.$message({showClose: true,duration:2000,type: 'error',message:'日清结算起必须小于日清结算止！'});
          return
        }
      }
      //重置当前页1
      if(offsetNum==0){
        this.pagination.currentPage=1
      }
      let dataUrl= accountPostLink+'/pay/settlement/querySettleOrderPre';
      let params={
        'merchantCode':this.formInline.merchantCode,
        'subMerchantCode':this.formInline.subMerchantCode,
        'settleOrderNo':this.formInline.settleOrderNo,
        'settleOrderStatus':this.formInline.settleOrderStatus,
        'generatorBeginDate':this.dateFormat(this.formInline.generatorBeginDate,"yyyy-MM-dd"),
        'generatorEndDate':this.dateFormat(this.formInline.generatorEndDate,"yyyy-MM-dd"),
        'offset':offsetNum,
        'token':this.getCookie('token'), //token
        'transactionUuid':this.uuid()    //uuid
      }
      this.$http.post(dataUrl, params).then((response)=>{
        if(response.data.code === 'S0A00000' && response.data.preSettleOrders){
          this.tableData =response.data.preSettleOrders
          this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
        }else{
          this.tableData=[];
          this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
        }
      })
    },
    handleReset() {
      let formInline={
          merchantName: '',
          merchantCode:'',
          subMerchantName:'',
          subMerchantCode:'',
          settleOrderNo:'',
          settleOrderStatus:'',
          generatorBeginDate: '',
          generatorEndDate:'',
        }
        this.queryString='';
      this.formInline=formInline
    },
    //单条信息查询
    handleDetail(index, row){
      if(row.settleOrderNo){
       var queryObj={settleOrderNo: row.settleOrderNo};
      }else{
       var queryObj={settleOrderPreNos:[row.settleOrderPreNo]};
      }
      this.$router.push({path: '/home/accountInquiry/accountInquiryDetail', query: queryObj})
    },
    //settleOrderPreNos数组 是否可选
    handleUnique(){
      this.settleOrderArrays=[];//清空数组
      for(var i=0; i<this.tableData.length;i++){
        var scopeName='scopeChecked'+i;
        var el=document.getElementById(scopeName);
        if(el.checked==true){
          this.disChecked=true;
          this.settleAmountTotle+=parseFloat(this.tableData[i].settleAmount);
          this.settleOrderArrays.push(this.tableData[i].settleOrderPreNo);
        }
      }
    },
    //查看结算详情
    handleAllDetail(){
      this.handleUnique();
      if(this.settleAmountTotle>0){
        let dataUrl=  accountPostLink+'/pay/settlement/querySettleDetail';
            var params={
              'settleOrderPreNos':this.settleOrderArrays, // 预结算单号列表
              'token':this.getCookie('token'), //token
              'transactionUuid':this.uuid()    //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
            if(response.data.code === 'S0A00000' && response.data.settleOrderDetailDTO){
                this.$router.push({path: '/home/accountInquiry/accountInquiryDetail', query: { settleOrderPreNos: this.settleOrderArrays}})
            }else{
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
         })
       }else{
        this.$message({showClose: true,duration:2000,type: 'error',message:'结算总金额必须大于0才能查看！'});
      }
    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.tableData.splice(index, 1)
        this.$message({showClose: true,duration:2000,type: 'success',message:'删除成功!'});
      }).catch(() => {
        this.$message({showClose: true,duration:2000,type: 'info',message:'已取消删除!'});
      });
    },
    //分页查询
    handleCurrentChange(val) {
      var  pageOffset=parseInt((val-1)*20);
      this.handleInquire(pageOffset)
    },
  },
  created(){
    this.handleInquire(0);
  },
  mounted () {
    this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
  },
}
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
    position: relative;
  }
  .el-lookBtn{
    position: absolute;
    right:17%;
    top:50%;
    transform:translate(0,-50%);
    font-size:14px ;
    background-color: #409EFF;
    border: 1px solid #409EFF;
    border-color: #409EFF;
    border-radius: 5px;
    padding: 6px 9px;
    cursor: pointer;
    color: #fff;
    z-index: 100;
  }
  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
</style>



