<template id="Menu">
  <el-menu :default-active="onRoutes" :unique-opened="true" class="el-menu-vertical-demo crm-left" @select="handleSelect" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router>
      <el-submenu index="/home"  v-if="display">
        <template slot="title"><i class="el-icon-search"></i><span slot="title">主要</span></template>
        <el-menu-item-group>
          <el-menu-item index="/home/table"><i class="el-icon-message"></i>基础表格</el-menu-item>
          <el-menu-item index="/home/charts"><i class="el-icon-edit"></i>图表</el-menu-item>
          <el-menu-item index="/register"><i class="el-icon-menu"></i>注册</el-menu-item>
          <el-menu-item index="/home/mapDemo"><i class="el-icon-menu"></i>map地图</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

    <el-menu-item index="/home/walletUser"><i class="el-icon-setting"></i><span slot="title">钱包用户</span></el-menu-item>

    <el-submenu index="/home/payTradeList">
      <template slot="title"><i class="el-icon-sort"></i><span slot="title">交易明细</span></template>
      <el-menu-item-group>
        <el-menu-item index="/home/payTradeList"><i class="el-icon-tickets"></i>支付交易</el-menu-item>
        <el-menu-item index="/home/refundTradeList"><i class="el-icon-tickets"></i>退款交易</el-menu-item>
        <el-menu-item index="/home/walletTradeList"><i class="el-icon-tickets"></i>钱包交易</el-menu-item>
      </el-menu-item-group>
    </el-submenu>

    <el-menu-item index="/home/accountInquiry"><i class="el-icon-search"></i><span slot="title">结算单查询</span></el-menu-item>

    <el-submenu index="/home/incidentMarket" >
      <template slot="title"><i class="el-icon-menu"></i><span slot="title">营销事件管理</span></template>
      <el-menu-item-group>
        <el-menu-item index="/home/incidentMarket"><i class="el-icon-edit"></i>营销事件</el-menu-item>
        <el-menu-item index="/home/incidentType"><i class="el-icon-edit"></i>事件类型</el-menu-item>
      </el-menu-item-group>
    </el-submenu>

    <el-submenu index="/home/couponType">
      <template slot="title"><i class="el-icon-view"></i><span slot="title">钱包券管理</span></template>
      <el-menu-item-group>
        <el-menu-item index="/home/couponType"><i class="el-icon-edit"></i>钱包券类型管理</el-menu-item>
        <el-menu-item index="/home/couponInfomation"><i class="el-icon-edit"></i>钱包券信息明细</el-menu-item>
      </el-menu-item-group>
    </el-submenu>

    <el-menu-item index="/home/discountRules" ><i class="el-icon-goods"></i><span slot="title">折扣规则</span></el-menu-item>

    <el-submenu index="/home/billMatch" >
      <template slot="title"><i class="el-icon-document"></i><span slot="title">对账管理</span></template>
      <el-menu-item-group>
        <el-menu-item index="/home/billMatchFile"><i class="el-icon-edit"></i>对账文件</el-menu-item>
        <el-menu-item index="/home/billMatch"><i class="el-icon-edit"></i>对账匹配</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
    <div class="sidebar-collapse" id="sidebar-collapse">
      <i :class="isArrow" data-icon1="icon-double-angle-left" @click="sidebar" data-icon2="icon-double-angle-right"></i>
    </div>
  </el-menu>
</template>
<script>
export default {
  props: ['childMsg'],
  data () {
    return {
       display:false,
       isCollapse: false,
       isArrow:'icon-double-angle-left',
    }
  },
  methods: {
    sidebar(){ // 菜单栏展开切换
        if(this.isCollapse){
          this.isCollapse=false
          this.isArrow="icon-double-angle-left"
          this.$emit('upMsg',false);
        }else{
          this.isCollapse=true
          this.isArrow="icon-double-angle-right"
          this.$emit('upMsg',true);
        }
    },
    loadSidebar(){
      if(this.childMsg){
        this.isCollapse=true
        this.isArrow="icon-double-angle-right"
      }
    },
    handleSelect (key, route) { // 左侧菜单栏选中方法
     // console.log(key, route);

    },
    handleOpen (key, keyPath) { // 左侧菜单栏展开方法
     // console.log(key, keyPath);
    },
    handleClose (key, keyPath) { // 左侧菜单栏收缩方法
     // console.log(key, keyPath);
    },
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace('/', '');
    }
  },
  mounted(){
   this.loadSidebar();
  }
}
</script>
<style scoped>
  .crm-left{
    position: absolute;
    top: 0px;
    left: 0px;
    z-index:90;
  }
  .sidebar-collapse {
    text-align: center;
    padding: 3px 0;
    margin-top:15px;
    position: relative;
  }
  .sidebar-collapse:before {
    content: "";
    display: inline-block;
    height: 0;
    border-top: 1px dashed #B5B5B5;
    position: absolute;
    left: 15px;
    right: 15px;
    top: 13px;
  }
  .sidebar-collapse>[class*="icon-"] {
    display: inline-block;
    cursor: pointer;
    font-size: 14px;
    color: #2c445a;
    border: 1px solid #B5B5B5;
    padding: 0 5px;
    line-height: 18px;
    border-radius: 16px;
    background-color: #B5B5B5;
    position: relative;
  }
</style>



