<template>
  <div class="crm-main">
   <el-row class="el-mart">
     <div class="el-card-title"  id="formwrapper" >对账单明细</div>
      <el-table :data="billFileData"  :fit="true" border  :max-height="wrapperHeight"   style="width: 100%">
        <el-table-column prop="tradeNo" align="center" label="支付流水号" min-width="100"></el-table-column>
        <el-table-column prop="tradeTime"  align="center" :formatter="tradeTime" label="支付日期" min-width="100"></el-table-column>
        <el-table-column prop="status" align="center" label="支付状态" min-width="50">
          <template slot-scope="scope">
            <span v-if="scope.row.status=='fail'">失败</span>
            <span v-if="scope.row.status=='success'">成功</span>
          </template>
        </el-table-column>
        <el-table-column prop="payAmount"  align="center" label="支付金额(元)" min-width="120"></el-table-column>
        <el-table-column prop="tradeType" align="center" label="交易类型" min-width="50"></el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        wrapperHeight:'',
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:30,
          total:0
        },
        fileName:'',
        partnerId :'',
        billChannelCode:'',
        billFileData: [],
        channelList:[],
      }
    },
    methods: {
      // 表单查询
      handleInquire(offsetNum) {
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let dataUrl= billLink+'/payment/bill/file/view.htm';
        let params={
          partnerId:this.partnerId, //
          billChannelCode:this.billChannelCode,
          fileName:this.fileName,  //对账单文件名字
          offset:offsetNum,   //起始偏移量
          'token':this.getCookie('token'), //token
          'transactionUuid':this.uuid()    //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.billFileData =response.data.data
            this.pagination.total=parseInt(response.data.totalRecordCount); // 总数
          }else{
            this.billFileData =[];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});

          }
        })
      },
      //格式化导入时间
      tradeTime(row, column){
        var tradeTime='';
        if(row.tradeTime){
          tradeTime=this.filterDate(row.tradeTime,'YMDHMS');
        }
        return tradeTime;
      },
      //查看明细
      handleDetail(index, row){
        this.$router.push({path: '/home/billMatchFile/billMatchFileDetail', query: { fileName:row.fileName}})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*30);
          this.handleInquire(pageOffset)
      },
    },
    created(){
      this.fileName = this.$route.query.fileName;
      this.partnerId = this.$route.query.partnerId;
      this.billChannelCode=this.$route.query.billChannelCode;
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-document.getElementById('pagination').clientHeight-140;
    }
  }
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



