<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formCoupon" id="formwrapper"  class="demo-form-inline crm-form" label-width="96px">
      <el-row>
        <el-form-item label="券类型编号">
          <el-input v-model="formCoupon.couponTypeCode" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="券类型名称">
          <el-input v-model="formCoupon.couponName" class="el-winp" placeholder=" "></el-input>
        </el-form-item>

        <el-form-item label="有效开始时间">
          <el-date-picker v-model="formCoupon.validDateStartFrom" :editable="false"  class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
          <span class="el-span">至</span>
          <el-date-picker v-model="formCoupon.validDateStartTo" :editable="false" class="el-winp"   type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="券类型类别">
          <el-select v-model="formCoupon.couponCategory" class="el-winp" placeholder=" ">
            <el-option label="满减券" value="MONEYOFF"></el-option>
            <el-option label="折扣券" value="DISCOUNT"></el-option>
            <el-option label="现金券" value="CASH"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="适用商户">
          <search-Merchant v-on:communicate="setMerchantInfo" description="merchant" placeholder="请输入商户名称" class="el-winp" :empty="formCoupon.merchantName"></search-Merchant>
        </el-form-item>

        <el-form-item label="有效截止时间">
          <el-date-picker v-model="formCoupon.validDateEndFrom" :editable="false" class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
          <span class="el-span">至</span>
          <el-date-picker v-model="formCoupon.validDateEndTo"  :editable="false" class="el-winp"  type="date"  placeholder="选择日期"></el-date-picker>
        </el-form-item>

      </el-row>
      <el-row>
        <el-form-item label="状态">
          <el-select v-model="formCoupon.status" class="el-winp" placeholder=" ">
            <el-option label="全部" value=""></el-option>
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="启用" value="VALID"></el-option>
            <el-option label="禁用" value="INVALID"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button type="primary" class="el-btnw el-marl"  icon="el-icon-plus" @click="handleAdd">新增</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>
      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" :fit="true" border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="couponTypeCode" align="center" label="券类型编码"></el-table-column>
        <el-table-column prop="couponName" align="center" label="券类型名称" ></el-table-column>
        <el-table-column prop="couponCategoryAmount" align="center" label="券额度" ></el-table-column>
        <el-table-column prop="couponCategory" align="center" label="券类型类别" >
          <template slot-scope="scope">
            <span v-if="scope.row.couponCategory=='MONEYOFF'">满减券</span>
            <span v-if="scope.row.couponCategory=='DISCOUNT'">折扣券</span>
            <span v-if="scope.row.couponCategory=='CASH'">现金券</span>
          </template>
        </el-table-column>
        <el-table-column prop="issueCount" align="center" label="最大数量" ></el-table-column>
        <el-table-column prop="issuedCount" align="center" label="已发行" ></el-table-column>
        <el-table-column prop="usedCount" align="center" label="已使用" ></el-table-column>
        <el-table-column prop="validDateInterval" align="center" label="有效期区间" ></el-table-column>
        <el-table-column prop="updateTime" align="center" :formatter="updateTime"  label="更新时间" >
        </el-table-column>
        <el-table-column prop="status" align="center"  label="状态" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='VALID'">启用</span>
            <span v-if="scope.row.status=='INVALID'">禁用</span>
            <span v-if="scope.row.status=='NEW'">新建</span>
          </template>
        </el-table-column>
        <el-table-column  align="center" label="操作" fixed="right" width="120px">
          <template slot-scope="scope">
            <span  class="el-alink" v-if="scope.row.status=='NEW'" :class="scope.row.status=='NEW'?'el-disable':''" >查看</span>
            <span  class="el-alink" v-if="scope.row.status=='INVALID'||scope.row.status=='VALID'" @click="handleDetail(scope.$index, scope.row)">查看</span>
            <span  class="el-alink"  v-if="scope.row.issuedCount>=1" :class="scope.row.issuedCount>=1?'el-disable':''"   >编辑</span>
            <span  class="el-alink"  v-if="scope.row.issuedCount==0||scope.row.issuedCount==null||scope.row.issuedCount==undefined||scope.row.issuedCount==''"  @click="handleEdit(scope.$index, scope.row)">编辑</span>
            <span  class="el-alink" v-if="scope.row.status=='INVALID'||scope.row.status=='VALID'"  @click="handleDisabled(scope.$index, scope.row)" >
              <i v-if="scope.row.status=='INVALID'">启用</i>
              <i v-if="scope.row.status=='VALID'">禁用</i>
            </span>
            <span  class="el-alink el-disable" v-if="scope.row.status=='NEW'"  >
              <i>禁用</i>
            </span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  import searchMerchant from 'components/common/search'
  export default {
    components: {ElRow, searchMerchant},
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置

        //form 表单字段
        formCoupon: {
          couponTypeCode:'',     //券编码
          couponName:'',         //券名称
          couponCategory:'',     //券类型
          offset:0,             //请求分页的offset
          validDateStartFrom:'', //生效期开始范围起始
          validDateStartTo:'',   //生效期开始范围终止
          validDateEndFrom:'',   //生效期截止范围起始
          validDateEndTo:'',     //生效期截止范围终止
          status:'',
          merchantName: '',
          merchantCode:'',
        },
        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
      }
    },
    methods: {
      setMerchantInfo(item){
        this.formCoupon.merchantName = item.value
        this.formCoupon.merchantCode = item.id

      },
      handleMIconClick(ev) { // 商户搜索方法
      },
      // 更新时间格式化
      updateTime(row, column){
        return this.filterDate(row.updateTime,'YMDHMS');
      },
      // 钱包券新增
      handleAdd(){
        this.$router.push({path: '/home/couponType/couponTypeAdd'})
      },

      // 表单重置
      handleReset() {
        let formCoupon={
          couponTypeCode:'',     //券编码
          couponName:'',         //券名称
          couponCategory:'',     //券类型
          offset:0,              //请求分页的offset
          validDateStartFrom:'', //生效期开始范围起始
          validDateStartTo:'',   //生效期开始范围终止
          validDateEndFrom:'',   //生效期截止范围起始
          validDateEndTo:'',     //生效期截止范围终止
          status:'',
          merchantName: '',
          merchantCode:'',
        }
        this.formCoupon=formCoupon
      },
      // 表单查询
      handleInquire(offsetNum) {
        let dataUrl= couponPostLink+'/fin-rules-osprest/couponType/couponTypeList.htm';
        var validDateStartFrom =this.filterDateStr(this.formCoupon.validDateStartFrom,10);
        var validDateStartTo= this.filterDateStr(this.formCoupon.validDateStartTo,10);
        var validDateEndFrom=this.filterDateStr(this.formCoupon.validDateEndFrom,10);
        var validDateEndTo=this.filterDateStr(this.formCoupon.validDateEndTo,10)
        if(validDateStartFrom==0||validDateStartTo==0){
          validDateStartFrom=null;
          validDateStartTo=null;
        }
        if(validDateEndFrom==0||validDateEndTo==0){
          validDateEndFrom=null;
          validDateEndTo=null;
        }
        if(validDateStartFrom){
          validDateStartFrom=validDateStartFrom+'000'
        }
        if(validDateStartTo){
          validDateStartTo=validDateStartTo+'000'
        }
       if(validDateEndFrom){
         validDateEndFrom=validDateEndFrom+'000'
       }
       if(validDateEndTo){
         validDateEndTo=validDateEndTo+'000'
       }
        //重置当前页1
        if(offsetNum==0){
          this.pagination.currentPage=1
        }
        let data={
          couponTypeCode:this.formCoupon.couponTypeCode,     //券编码
          couponName:this.formCoupon.couponName,             //券名称
          couponCategory:this.formCoupon.couponCategory,     //券类型
          offset:offsetNum,                      //请求分页的offset
          validDateStartFrom:validDateStartFrom, //生效期开始范围起始
          validDateStartTo:validDateStartTo,   //生效期开始范围终止
          validDateEndFrom:validDateEndFrom,   //生效期截止范围起始
          validDateEndTo:validDateEndTo,     //生效期截止范围终止
          status:this.formCoupon.status,
          merchantCode:this.formCoupon.merchantCode,
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //表格 查看  编辑  禁用 操作
      handleDetail(index, row){
        this.$router.push({path: '/home/couponType/couponTypeDetail', query: { id: row.id,status:row.status}})
      },
      handleEdit(index, row) {
        this.$router.push({path: '/home/couponType/couponTypeEdit', query: { id: row.id,status:row.status}})
      },
      //表格禁用启用设置
      handleDisabled(index, row) {
         if(!row.id){return}
         if(row.status=='VALID'){
           var status='INVALID'
         }
         else{
           var status='VALID'
         }
        if(row.status=='VALID') {
          this.$confirm('是否禁用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'id': row.id, 'status': status}     //券id
            let dataUrl = couponPostLink + '/fin-rules-osprest/couponType/couponTypeOpenOrClose.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              if (response.data.code === 'S0A00000') {
                  row.status = 'INVALID'
              } else {
                this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
              }
            })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消禁用操作！'});
          });
        }
        else if(row.status == 'INVALID'){
          this.$confirm('是否启用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'id': row.id, 'status': status}     //券id
            let dataUrl = couponPostLink + '/fin-rules-osprest/couponType/couponTypeOpenOrClose.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              if (response.data.code === 'S0A00000') {
                  row.status = 'VALID'
              } else {
                this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
              }
            })
          }).catch(() => {
            this.$message({showClose: true,duration:2000,type: 'info',message:'取消启用操作！'});
          });
        }
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>

  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



