<template>
  <div class="crm-main">

    <el-form :inline="true" :model="inquiryTable" id="formwrapper" readonly="readonly"  class="demo-form-inline crm-form" label-width="90px">

      <el-row>
        <el-col :span="8">
          <el-form-item label="结算单号">
            <el-input v-model="inquiryTable.settleOrderNo" class="el-winps" readonly="readonly" placeholder=" "></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="结算户名称">
            <el-input  class="el-winps" readonly="readonly"  v-model="inquiryTable.subMerchantName" ></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="8">
          <el-form-item label="结算户编号">
            <el-input  class="el-winps" readonly="readonly"  v-model="inquiryTable.subMerchantCode" ></el-input>
          </el-form-item>
        </el-col>

      </el-row>

      <el-row>
        <el-col :span="8">
          <el-form-item label="商户名称">
            <el-input  class="el-winps" readonly="readonly"  v-model="inquiryTable.merchantName" ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="商户编码">
            <el-input  class="el-winps" readonly="readonly"   v-model="inquiryTable.merchantCode" ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="状态">
            <el-select v-model="inquiryTable.settleOrderStatus" class="el-winps" disabled  placeholder=" ">
              <el-option label="已生成" value="00"></el-option>
              <el-option label="已确认" value="01|02|03"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>

        <el-col :span="8">
          <el-form-item label="开始日期">
            <el-date-picker v-model="inquiryTable.tradeBeginDate" readonly="readonly" type="date" class="el-winps" placeholder="开始日期"></el-date-picker>
          </el-form-item>
        </el-col>

        <el-col :span="8">
          <el-form-item label="结束日期" >
            <el-date-picker v-model="inquiryTable.tradeEndDate" readonly="readonly" type="date" class="el-winps" placeholder="开始日期"></el-date-picker>
          </el-form-item>
        </el-col>

      </el-row>

    </el-form>
    <el-row>
      <!--:summary-method="getSummaries"-->
      <el-table :data="tableData"  fit  border  style="width: 100%">
        <el-table-column prop="dataType" align="center" label="交易类型" ></el-table-column>
        <el-table-column prop="totalAmount" align="center" label="交易金额" ></el-table-column>
        <el-table-column prop="discountAmount" align="center" label="折扣金额" ></el-table-column>
        <el-table-column prop="pointsAmount" align="center" label="积分抵扣金额"  min-width="130px"></el-table-column>
        <el-table-column prop="couponAmount" align="center" label="卷抵扣金额" ></el-table-column>
        <el-table-column prop="realAmount" align="center" label="实付金额" ></el-table-column>
        <el-table-column prop="feeAmount" align="center" label="手续费" ></el-table-column>
        <el-table-column prop="discountApportionAmount" align="center" label="折扣分摊金额" min-width="130px"></el-table-column>
        <el-table-column prop="settleAmount" align="center" label="结算金额" ></el-table-column>
      </el-table>
    </el-row>
    <el-row class="el-pad">

      <el-button type="primary" class="el-btnw btn-default  el-fr  " @click="goBack">返回</el-button>
      <el-button type="primary" class="el-btnw el-fr el-marr " @click="onSureAccount" :disabled="btnStatue" >确认结算</el-button>

    </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      btnStatue:false,
      settleOrderNo:'',
      settleOrderPreNos:'',
      inquiryTable:{
        merchantName:'',
        merchantCode:'',
        subMerchantName:'',
        subMerchantCode:'',
        settleOrderPreNo:'',
        settleOrderStatus:'',
        tradeBeginDate:'',
        tradeEndDate:'',
      },
      tableData: [],
    }
  },
  methods: {
    //查询数据
    handleInquire(settleOrderNo,settleOrderPreNos) {
      let dataUrl=  accountPostLink+'/pay/settlement/querySettleDetail';
      if(settleOrderNo){
        var params={
          'settleOrderNo':settleOrderNo,         // 结算单号
          'token':this.getCookie('token'), //token
          'transactionUuid':this.uuid()    //uuid
        }
      }else{
        if(settleOrderPreNos instanceof Array){
          var params={
            'settleOrderPreNos':settleOrderPreNos, // 预结算单号列表
            'token':this.getCookie('token'), //token
            'transactionUuid':this.uuid()    //uuid
          }
        }else{
          var params={
            'settleOrderPreNos':[settleOrderPreNos], // 预结算单号列表
            'token':this.getCookie('token'), //token
            'transactionUuid':this.uuid()    //uuid
          }
        }
      }
      this.$http.post(dataUrl, params).then((response)=>{
        if(response.data.code === 'S0A00000' && response.data.settleOrderDetailDTO){
          this.tableData =response.data.settleOrderDetailDTO.settleAmountDetails
          this.inquiryTable.merchantName=response.data.settleOrderDetailDTO.merchantName;
          this.inquiryTable.merchantCode=response.data.settleOrderDetailDTO.merchantCode;
          this.inquiryTable.subMerchantName=response.data.settleOrderDetailDTO.subMerchantName;
          this.inquiryTable.subMerchantCode=response.data.settleOrderDetailDTO.subMerchantCode;
          this.inquiryTable.settleOrderNo=response.data.settleOrderDetailDTO.settleOrderNo;
          if( this.inquiryTable.settleOrderNo){
            this.btnStatue=true;
          }
          this.inquiryTable.settleOrderStatus=response.data.settleOrderDetailDTO.settleOrderStatus;
          this.inquiryTable.tradeBeginDate=response.data.settleOrderDetailDTO.tradeBeginDate;
          this.inquiryTable.tradeEndDate=response.data.settleOrderDetailDTO.tradeEndDate;
          if(this.inquiryTable.settleStatus=='00'){
            this.show=true
          }else{
            this.show=false
          }
        }else{
          this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
        }
      })
    },
    onSureAccount(){
      let dataUrl= accountPostLink+'/pay/settlement/singleSettleConfirm';
      if(this.settleOrderNo){
        var params={
          'settleOrderPreNos':this.settleOrderNo,         // 结算单号
          'token':this.getCookie('token'), //token
          'transactionUuid':this.uuid()    //uuid
        }
      }else{
        if(this.settleOrderPreNos instanceof Array){
          var params={
            'settleOrderPreNos':this.settleOrderPreNos, // 预结算单号列表
            'token':this.getCookie('token'), //token
            'transactionUuid':this.uuid()    //uuid
          }
        }else{
          var params={
            'settleOrderPreNos':[this.settleOrderPreNos], // 预结算单号列表
            'token':this.getCookie('token'), //token
            'transactionUuid':this.uuid()    //uuid
          }
        }
      }
      this.$http.post(dataUrl, params).then((response)=>{
        console.log(response)
        if(response.data.code === 'S0A00000'){
          swal("结算确认成功！",'',"success")
          this.inquiryTable.settleOrderStatus="已确认";
          this.btnStatue=true;
        }
        else{
          swal("结算确认失败！",response.data.msg,"warning")
        }
      })
    },
    goBack(){
      this.$router.push({path: '/home/accountInquiry'});
    },
  },
  created (){
    this.settleOrderNo=this.$route.query.settleOrderNo;
    this.settleOrderPreNos=this.$route.query.settleOrderPreNos;
    this.handleInquire(this.settleOrderNo,this.settleOrderPreNos);
  },
}
</script>
<style scoped>
  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
</style>



