<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formTrade"  ref="formTrade"  id="formwrapper"    class="demo-form-inline crm-form" label-width="125px">

      <el-row>

        <div class="el-card-title">支付交易详情</div>

        <el-form-item label="交易流水号" >
          <el-input v-model="formTrade.payOrderNo" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="商户订单号">
          <el-input v-model="formTrade.outTradeNo"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="支付用户编号" >
          <el-input v-model="formTrade.payUserId"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>
        <el-form-item label="支付场景码">
          <el-input v-model="formTrade.channelBizCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="会员编号">
          <el-input v-model="formTrade.memberId" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="支付方式">
          <el-input v-model="formTrade.payMethodName" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

      </el-row>
      <el-row>

        <el-form-item label="交易类型" >
          <el-select v-model="formTrade.tradeType" class="el-winps" disabled="disabled">
            <el-option label="支付" value="pay"></el-option>
            <el-option label="退款" value="girl"></el-option>
            <el-option label="撤销" value="girl"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="交易商户名称">
          <el-input v-model="formTrade.merchantName" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="交易商户编号">
          <el-input v-model="formTrade.merchantCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>


      </el-row>

      <el-row>

        <el-form-item label="交易门店编号">
          <el-input v-model="formTrade.storeCode" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="交易门店名称">
          <el-input v-model="formTrade.storeName" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="交易终端编号">
          <el-input v-model="formTrade.terminalId" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="交易服务IP">
          <el-input v-model="formTrade.spbillCreateIp" class="el-winps"disabled="disabled"></el-input>
        </el-form-item>



        <el-form-item label="授权码">
          <el-input v-model="formTrade.openId" class="el-winps" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="交易时间" >
          <el-date-picker v-model="formTrade.createTime" :editable="false"   class="el-winps" type="datetime"  disabled="disabled" placeholder="请输入签发日期"></el-date-picker>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="请求时间" >
          <el-date-picker v-model="formTrade.requestTime" :editable="false"   class="el-winps" type="datetime" disabled="disabled"  placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="完成时间" >
          <el-date-picker v-model="formTrade.completeTime" :editable="false"   class="el-winps" type="datetime" disabled="disabled"  placeholder="请输入有效日期"></el-date-picker>
        </el-form-item>

        <el-form-item label="订单金额" >
          <el-input v-model="formTrade.payAmount"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>
        <el-form-item label="实付金额" >
          <el-input v-model="formTrade.cashAmount"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="优惠金额" >
          <el-input v-model="formTrade.discountAmount"   class="el-winps"  disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="状态" >
          <el-input  class="el-winps"  v-model="formTrade.status" disabled="disabled"></el-input>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="结果说明" >
          <el-input type="textarea" class="el-winpl el-winph" v-model="formTrade.remark" disabled="disabled"></el-input>
        </el-form-item>
      </el-row>


      <el-row  >
        <div class="el-card-title">钱包列表详情</div>
          <el-tabs class="el-tabs" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="支付详情" name="payDetail">

              <el-row>
                <el-table :data="payDetail.payDetailList" :fit="true" border   max-height="350" style="width: 100%">
                  <el-table-column prop="tradeNo" align="center" label="华润通交易流水号"></el-table-column>
                  <el-table-column prop="channelTradeSn" align="center"  label="银行交易流水号" ></el-table-column>
                  <el-table-column prop="payMethodName" align="center" label="付款/优惠方式" ></el-table-column>
                  <el-table-column prop="actPayAmount" align="center" label="金额" ></el-table-column>
                  <el-table-column prop="cardMask" align="center" label="银行卡号" ></el-table-column>
                  <el-table-column prop="promotionNo" align="center" label="营销规则编号" ></el-table-column>
                  <el-table-column prop="tradeTime" align="center" label="交易时间" ></el-table-column>
                  <el-table-column prop="tradeStatus" align="center" label="状态" ></el-table-column>
                  <el-table-column prop="errorMsg" align="center" label="结果说明" ></el-table-column>
                </el-table>
                <el-row id="pagination" class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="payDetail.pagination.currentPage"
                    :page-size.sync="payDetail.pagination.pageSize"
                    :total.sync="payDetail.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
            <el-tab-pane label="验证日志" name="verifyLog">

              <el-row>
                <el-table :data="verifyLog.verifyLogList" :fit="true" border   max-height="350" style="width: 100%">
                  <el-table-column prop="payOrderNo" align="center" label="交易流水号"></el-table-column>
                  <el-table-column prop="responseData" align="center"  label="验证方式" ></el-table-column>
                  <el-table-column prop="responseCode" align="center" label="验证结果" ></el-table-column>
                  <el-table-column prop="createTime" align="center" label="验证时间" ></el-table-column>
                  <el-table-column prop="responseMsg" align="center" label="备注" ></el-table-column>
                </el-table>
                <el-row  class="crm-pagination"  >
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="verifyLog.pagination.currentPage"
                    :page-size.sync="verifyLog.pagination.pageSize"
                    :total.sync="verifyLog.pagination.total">
                  </el-pagination>
                </el-row>
              </el-row>

            </el-tab-pane>
          </el-tabs>
      </el-row>

      <el-row class="ln-ac">

          <el-button type="info" class="el-winpMin"  size="big" @click="goContype">返回</el-button>

      </el-row>

    </el-form>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {
      ElRow
    },
    data () {
      return {
        display: false,           //隐藏状态
        wrapperHeight: '',        //页面高度动态设置
        activeName: 'payDetail',     //选项卡默认值
        payDetail:{
          payDetailList:[],         //银行卡
          //表格分页
          pagination: {
            currentPage:1,//当前页
            pageSize:20,
            total:0
          },
        },
        verifyLog:{
          verifyLogList:[],         //支付交易
          //表格分页
          pagination: {
            currentPage:1,//当前页
            pageSize:20,
            total:0
          },
        },
        //form 表单字段
        formTrade: {
          cashAmount:'',	     //实付金额	string
          channelBizCode:'',	 //支付场景码	string
          completeTime:'',	   //完成时间	string
          createTime:'',	     //交易时间	string
          discountAmount:'',   //优惠金额	string
          memberId:'',	       //会员编号	number
          merchantCode:'',	   //交易商户编号	string
          merchantName:'',	   //交易商户名称	string
          openId:'',	         //授权码	string
          status:'',	     //状态	string
          outTradeNo:'',	     //商户订单号	string
          payAmount:'',	       //订单金额	string
          payMethodName:'',	   //支付方式	string
          payOrderNo:'',	     //交易流水号	string
          payUserId:'',	       //支付用户编号	string
          remark:'',	         //结果说明	string
          requestTime:'',	     //请求时间	string
          spbillCreateIp:'',	 //交易服务IP	string
          storeCode:'',	       //交易门店编码	string
          terminalId:'',	     //交易终端编号	string
          tradeType:'',	       //交易类型	number
        },
      }
    },
    methods: {
      //获取钱包用户详情
      getInfoDetail(){
        let dataUrl= payTrade+'/payment/order/payandtopup/basic.htm';
        let params={
          'payOrderNo':this.formTrade.payOrderNo,            //支付用户id
          'tradeType':this.formTrade.tradeType,            //支付交易类型
          'token':this.getCookie('token'),//token
          'transactionUuid':this.uuid()   //uuid
        }
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
              this.formTrade.cashAmount=response.data.data.cashAmount
              this.formTrade.channelBizCode=response.data.data.channelBizCode
              this.formTrade.completeTime=response.data.data.completeTime
              this.formTrade.createTime=response.data.data.createTime
              this.formTrade.discountAmount=response.data.data.discountAmount
              this.formTrade.memberId=response.data.data.memberId
              this.formTrade.merchantCode=response.data.data.merchantCode
              this.formTrade.merchantName=response.data.data.merchantName
              this.formTrade.openId=response.data.data.openId
              this.formTrade.status=response.data.data.status
              this.formTrade.outTradeNo=response.data.data.outTradeNo
              this.formTrade.payAmount=response.data.data.payAmount
              this.formTrade.payMethodName=response.data.data.payMethodName
              //this.formTrade.payOrderNo=response.data.data.payOrderNo
              this.formTrade.payUserId=response.data.data.payUserId
              this.formTrade.remark=response.data.data.remark
              this.formTrade.requestTime=response.data.data.requestTime
              this.formTrade.spbillCreateIp=response.data.data.spbillCreateIp
              this.formTrade.storeCode=response.data.data.storeCode
              this.formTrade.terminalId=response.data.data.terminalId
          }else{
            // this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //获取银行卡详情  type 表格类型
      getListDetail(type,offsetNum){
        console.log(1111)
        let params={    //支付用户id
          'payOrderNo':this.formTrade.payOrderNo,
          'offset':offsetNum,
          'token':this.getCookie('token'),//token
          'transactionUuid':this.uuid()   //uuid
        }
        //card 银行卡
        if(type=='payDetail'){
          //重置当前页1
          if(offsetNum==0){
            this.payDetail.pagination.currentPage=1
          }
          let dataUrl= payTrade+'/payment/order/payandtopup/detaillist.htm';
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.payDetail.payDetailList =response.data.data
              this.payDetail.pagination.total=parseInt(response.data.totalRecordCount); // 总数
            }else{
              this.payDetail.payDetailList = [];
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
        }
        //余额交易类型
        else if(type=='verifyLog'){
          //重置当前页1
          if(offsetNum==0){
            this.verifyLog.pagination.currentPage=1
          }
          let dataUrl= payTrade+'/payment/order/payandtopup/risklist.htm';
          this.$http.post(dataUrl, params).then((response)=>{
            console.log(response)
            if(response.data.code === 'S0A00000'){
              this.verifyLog.verifyLogList =response.data.data;
              this.verifyLog.pagination.total=parseInt(response.data.totalRecordCount); // 总数
            }else{
              this.verifyLog.verifyLogList = []
            }
          })
        }
      },
      //切换选项卡点击操作
      handleClick(tab, event) {
        console.log(tab, event);
        //表格请求的数据
        this.getListDetail(tab.paneName,0);
      },
      //返回钱包用户列表
      goContype(){
        this.$router.push({path: '/home/couponType'})
      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.getListDetail(this.activeName,pageOffset);
      },
    },
    created(){
      this.formTrade.payOrderNo= this.$route.query.payOrderNo;
      this.formTrade.tradeType= this.$route.query.tradeType;
      this.getInfoDetail()                    //获取钱包用户信息
      this.getListDetail(this.activeName,0); //获取表格支付交易和验证日志数据
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style lang="less" scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }
  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item {
    margin-bottom: 20px;
  }
</style>



