<template>
  <div class="crm-main"  :style="{ height: wrapperHeight + 'px' }">
    <el-form :inline="true" :model="formMarket" id="formwrapper" :rules="ruleValidate"  ref="formMarket"  class="demo-form-inline crm-form" label-width="110px">

      <el-row>
        <div class="el-card-title">营销事件基本信息</div>
        <el-form-item label="营销事件名称" prop="eventName">
          <el-input v-model="formMarket.eventName"  class="el-winps" placeholder=" "></el-input>
        </el-form-item>
      </el-row>

      <el-row>

        <el-form-item label="所属事件类型" prop="typeCode">
          <el-select v-model="formMarket.typeCode" class="el-winps" placeholder=" " @change="changeType">
            <el-option v-for="(item,index)  in formMarket.typeList" :key="index"  :label="item.triggerType" :value="item.typeCode"></el-option>
          </el-select>
          <span class="el-span el-ftred el-tip"> {{formMarket.typeName}}</span>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="状态" prop="status">
          <el-select v-model="formMarket.status" class="el-winps" placeholder=" ">
            <el-option label="新建" value="NEW"></el-option>
            <el-option label="已提交" value="COMMITTED"></el-option>
            <el-option label="有效" value="VALID"></el-option>
            <el-option label="无效" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="有效开始时间" prop="startDate">
          <el-date-picker v-model="formMarket.startDate"  class="el-winps"  type="datetime"  placeholder="请输入有效开始时间"></el-date-picker>
        </el-form-item>

        <el-form-item label="有效结束时间" prop="endDate">
          <el-date-picker v-model="formMarket.endDate"  class="el-winps" type="datetime"  placeholder="请输入有效结束时间"></el-date-picker>
        </el-form-item>

      </el-row>

      <el-row>

        <el-form-item label="备注" prop="desc">
          <el-input type="textarea" class="el-winpl el-winph" v-model="formMarket.remark"></el-input>
        </el-form-item>
      </el-row>

      <el-row>
        <div class="el-card-title">营销事件规则</div>

        <el-form-item label="商户/门店"   prop="merchantFlag">
          <el-radio-group v-model="formMarket.merchantFlag" @change="emptyData('merchantFlag')">
            <el-radio label="ALL">全部可用</el-radio>
            <el-radio label="NOT">部分可用</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item  v-if="formMarket.merchantFlag=='NOT'" >

          <el-button type="primary" class="el-btnw  el-marl" @click="downLoadFile"><i class="el-icon-download"></i>下载模板</el-button>

          <div  class="el-upload el-upload--picture el-relative">
            <el-button type="primary" class="el-btnw el-marl10" ><span>文件导入</span></el-button>
            <input type="file" name="file" v-if="clearShow" :value="formMarket.uploadFile" class="el-file-input"  @change="uploadFileExcel">
          </div>

        </el-form-item>


        <el-form-item  label="上传导入详情" v-if="formMarket.merchantFlag=='NOT'" >

          <el-table :data="formMarket.infoData"  border  style="width: 100%">
            <el-table-column prop="merchantCode" align="center" width="210" label="商户编码"></el-table-column>
            <el-table-column prop="merchantName" align="center" width="210" label="商户名称"></el-table-column>
            <el-table-column prop="storeCode" align="center" width="180" label="门店编码" ></el-table-column>
            <el-table-column prop="storeName" align="center" width="180" label="门店名称" ></el-table-column>
            <el-table-column prop="storeScope" align="center" width="120" label="全部和部分" v-if="display" ></el-table-column>

          </el-table>

        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label="可用会员等级" prop="memberFlag" @change="emptyData('memberFlag')">
          <el-radio-group v-model="formMarket.memberFlag">
            <el-radio label="ALL">全部可用</el-radio>
            <el-radio label="NOT">部分可用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item >
          <el-checkbox-group v-model="formMarket.member.leveList" v-if="formMarket.memberFlag=='NOT'" >
            <el-row>
              <el-checkbox label="1">V1 - 畅享会员</el-checkbox>
              <el-checkbox label="2">V2 - 优享会员</el-checkbox>
              <el-checkbox label="3">V3 - 尊享会员</el-checkbox>
            </el-row>
          </el-checkbox-group>
        </el-form-item>

      </el-row>

      <el-row>
        <el-form-item label=" " prop="bizType">
          <el-radio-group v-model="formMarket.bizType"  @change="emptyData('bizType')">
            <el-radio  label="IPT">赠送积分</el-radio>
            <el-radio  label="ICP">赠送润钱包券</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-row>

      <el-row>
        <el-form-item label="积分事件编号" prop="pointEventCode" v-if="formMarket.bizType=='IPT'">
          <el-input v-model="formMarket.pointEventCode"  placeholder=" "></el-input>
        </el-form-item>
        <span class="el-tip" v-if="formMarket.bizType=='IPT'">此处应填写在积分系统中维护好的赠送积分事件的编号，由积分系统的时间触发对应的积分规则</span>

        <el-form-item label="券类型编号" prop="couponTypeCode" v-if="formMarket.bizType=='ICP'">
          <el-input v-model="formMarket.couponTypeCode"  placeholder=" "></el-input>
        </el-form-item>
        <span class="el-tip" v-if="formMarket.bizType=='ICP'">此处应填写在润钱包券类型管理模块中维护好的券类型编号，请务必先维护好券类型</span>
      </el-row>

      <el-row  class="ln-ac">

          <el-button class="el-winpMin el-marr" type="success" size="big" @click="incidentSave('formMarket')">保存</el-button>

          <el-button type="info" class="el-winpMin"  size="big" @click="goIncident">返回</el-button>

      </el-row>

    </el-form>

  </div>
</template>
<script>
  import searchMerchant from 'components/common/search'
  import ElRow from "element-ui/packages/row/src/row";
  export default {
    components: {
      ElRow,
      searchMerchant},
    name: 'crmMenu',
    data () {
      //输入2位正数
      var  number=(rule, value, callback) => {
        var reg = /^[0-9]*$/;
        console.log(value)
        if(!value){
          return callback(new Error('不能为空'));
        }
        if (!reg.test(value)) {
          return callback(new Error('请输入正确的数字格式'));
        }else{
          callback();
        }
      };
      return {
        display:false,
        wrapperHeight:'',
        clearShow:true,
        formMarket: {
          couponTypeCode:'',   //券编码	string
          bizType:'',          //	事件触发的业务类型	string	IPT-送积分，ICP-送优惠券
          endDate:'',	         //结束时间	string
          eventName:'',        //事件名称	string
          startDate:'',        //开始时间
          status:'',	         //状态		新建NEW, 已提交COMMITTED, 有效(审核通过)VALID, 无效(停用)INVALID'
          typeCode:'',	       //事件类型编码	string	T00000001 ：OPENACCOUNT；T00000002：FIRSTTXN
          memberFlag:'',      	 //会员范围	string	全部：ALL；否则为NOT
          member:{            //会员数组	array<object>
            leveList:[],  	 //会员等级	array
          },
          merchantFlag:'',	     //商户范围	string	全部：ALL；否则为NOT
          pointEventCode:'',
          storeList:[],         //门店数组	array<object>
          infoData:[],           // 门店列表数据
          uploadFile:'',         //
          typeList:[],           //事件类型列表
          handselStatus:'',
          rowid:'',              //列表行的id
          remark:'',              //备注
          typeName:'',
        },
        ruleValidate: {
          eventName: [
            { required: true, message: '请输入营销事件名称', trigger: 'blur' },
          ],
          typeCode: [
            { required: true, message: '请输入所属事件类型', trigger: 'blur' },
          ],
          status: [
            { required: true, message: '请输入状态', trigger: 'blur' },
          ],
          startDate: [
            {type: 'date', required: true, message: '请输入有效开始时间', trigger: 'blur' },
          ],
          endDate: [
            { type: 'date',required: true, message: '请输入有效结束时间', trigger: 'blur' },
          ],
          merchantFlag: [
            { required: true, message: '请选择商户/门店', trigger: 'blur' },
          ],
          memberFlag: [
            { required: true, message: '请选择可用会员等级', trigger: 'blur' },
          ],
          bizType: [
            { required: true, message: '请选择赠送积分还是', trigger: 'blur' },
          ],
          pointEventCode: [
            {required: true,validator:number, trigger: 'blur' }
          ],
          couponTypeCode: [
            {required: true,validator:number, trigger: 'blur' }
          ],
        },
      }
    },
    methods: {
      //获取营销事件详情
      getEventInfo(){
        let dataUrl = incidentMarket + '/fin-rules-osprest/event/getEventInfo.htm';
        let data = {
          id:this.formMarket.rowid
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response) => {
            console.log(response);
            if (response.data.code === 'S0A00000') {
              this.formMarket.eventName=response.data.data.eventScope.eventName;
              this.formMarket.bizType=response.data.data.eventScope.bizType;
              this.formMarket.endDate=this.filterToString(response.data.data.eventScope.endDate);
              this.formMarket.startDate=this.filterToString(response.data.data.eventScope.startDate);
              this.formMarket.status=response.data.data.eventScope.status;
              this.formMarket.typeCode=response.data.data.eventScope.typeCode;
              for(var i=0;i<this.formMarket.typeList.length;i++){
                if(this.formMarket.typeCode==this.formMarket.typeList[i].typeCode){
                  this.formMarket.typeName=this.formMarket.typeList[i].typeName;
                }
              }
              this.formMarket.remark=response.data.data.eventScope.remark;
              this.formMarket.memberFlag=response.data.data.memberFlag;
              this.formMarket.member.leveList=response.data.data.memberOut.leveList;
              this.formMarket.merchantFlag=response.data.data.merchantFlag;
              if(response.data.data.pointList){
                this.formMarket.pointEventCode=response.data.data.pointList[0].pointEventCode;
              }else{
                this.formMarket.pointEventCode='';
              }
              if(response.data.data.couponList){
                this.formMarket.couponTypeCode=response.data.data.couponList[0].couponTypeCode;
              }else{
                this.formMarket.couponTypeCode='';
              }
              this.formMarket.infoData=response.data.data.storeList;
              for(var i=0;i<this.formMarket.infoData.length;i++){
                var  fileData={'merchantCode':this.formMarket.infoData[i].merchantCode,'storeCode':this.formMarket.infoData[i].storeCode}
                this.formMarket.storeList.push(fileData);

              }
            } else {
              this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
            }
          })
      },
      //获取事件类型
      getIncidentType(){
        let dataUrl= incidentMarket+'/fin-rules-osprest/eventType/eventTypeList.htm';
        let data={}
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.formMarket.typeList =response.data.data.recordList
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //事件类型描述
      changeType(){
        for(var i=0;i<this.formMarket.typeList.length;i++){
          if(this.formMarket.typeCode==this.formMarket.typeList[i].typeCode){
            this.formMarket.typeName=this.formMarket.typeList[i].typeName;
          }
        }
      },
      // 下载文件
      downLoadFile(){
        window.location.href='../../../../static/marketing _events.xlsx';
      },
      //上传文件导入模板
      uploadFileExcel(event){
        console.log(21221)
        event.preventDefault();
        this.clearShow=false;
        let formData = new FormData();
        formData.append('file', event.target.files[0] || event.dataTransfer.files[0]);
        formData.append('token',this.getCookie('token'));//token
        formData.append('transactionUuid',this.uuid());//uuid
        let dataUrl= incidentMarket+'/fin-rules-osprest/event/eventUpload.htm';
        let config = {headers: {'Content-Type': 'multipart/form-data'}}
        this.$http.post(dataUrl, formData, config).then((response)=>{
          console.log(2321412)
          console.log(response)
          this.clearShow=true;//input 回调显示
          if(response.data.code === 'S0A00000'){
            this.formMarket.infoData=response.data.data;
            for(var i=0;i<this.formMarket.infoData.length;i++){
              var  fileData={'merchantCode':this.formMarket.infoData[i].merchantCode,'storeCode':this.formMarket.infoData[i].storeCode}
              this.formMarket.storeList.push(fileData);
            }
            this.$message({showClose: true,duration:2000,type: 'success',message:'恭喜你，文件导入成功！'});
          }else{
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.tableData.splice(index, 1)
          this.$message({showClose: true,duration:2000,type: 'success',message:'删除成功!'});
        }).catch(() => {
          this.$message({showClose: true,duration:2000,type: 'info',message:'已取消删除!'});
        });
      },
      incidentSave(formName){
        this.$confirm('是否保存?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              let dataUrl = incidentMarket + '/fin-rules-osprest/event/saveEvent.htm';
              let data = {
                couponList: [{'couponTypeCode':this.formMarket.couponTypeCode}],
                eventScope: {
                  bizType: this.formMarket.bizType,          //	事件触发的业务类型	string	IPT-送积分，ICP-送优惠券
                  endDate: this.filterDateStr(this.formMarket.endDate, 13),	         //结束时间	string
                  eventName: this.formMarket.eventName,        //事件名称	string
                  startDate: this.filterDateStr(this.formMarket.startDate, 13),        //开始时间
                  status: this.formMarket.status,	         //状态		新建NEW, 已提交COMMITTED, 有效(审核通过)VALID, 无效(停用)INVALID'
                  typeCode: this.formMarket.typeCode,	       //事件类型编码	string	T00000001 ：OPENACCOUNT；T00000002：FIRSTTXN
                  remark:this.formMarket.remark,
                },
                memberFlag: this.formMarket.memberFlag,
                member: this.formMarket.member,
                merchantFlag: this.formMarket.merchantFlag,
                pointList: [{'pointEventCode':this.formMarket.pointEventCode}],
                storeList: this.formMarket.storeList,

              };
              let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
              this.$http.post(dataUrl, params).then((response) => {
                console.log(response);
                if (response.data.code === 'S0A00000') {
                  this.$router.push({path: '/home/incidentMarket'})
                  this.$message({showClose: true,duration:2000,type: 'success',message:'营销规则保存成功!'});
                } else {
                  this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
                }
              })
            }else{
              var that=this;
              setTimeout(function(){
                that.$refs[formName].resetFields();
              },5000)
            }
          });
        }).catch(() => {
          this.$message({showClose: true,duration:2000,type: 'info',message:'取消营销规则信息保存！'});
        });
      },
      //返回营销事件列表
      goIncident(){
        this.$router.push({path: '/home/incidentMarket'})
      },
      // 切换清空数据
      // paramsName  radio
      emptyData(paramsName){
        //清空券数量
        if(paramsName=='merchantFlag'){
          if(this.formMarket.merchantFlag=='ALL'){
            this.formMarket.infoData=[];
          }
        }
        else if(paramsName=='memberFlag'){
          if(this.formMarket.memberFlag=='ALL'){
            this.formMarket.member.leveList=[];
          }
        }
        else if(paramsName=='bizType'){
           if(this.formMarket.bizType=='IPT'){
             this.formMarket.couponTypeCode='';
           }else if(this.formMarket.bizType=='ICP'){
             this.formMarket.pointEventCode='';
           }
        }
      }
    },
    created(){
      this.formMarket.rowid = this.$route.query.id;// 获取rowid
      this.getIncidentType();//获取事件类型
      this.getEventInfo()//获取详情
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - 120;
    }
  }
</script>
<style scoped>

  .crm-main{
    padding:0 15px ;
    overflow-y: scroll;
  }
  .crm-form{
    padding-top:20px ;
  }
</style>



