<template>
  <div class="crm-main">
    <el-form :inline="true" :model="formType" id="formwrapper"  class="demo-form-inline crm-form">
      <el-row>
        <el-form-item label="事件类型编码">
          <el-input v-model="formType.typeCode" class="el-winp" placeholder=" "></el-input>
        </el-form-item>
        <el-form-item label="事件类型名称">
          <el-input v-model="formType.typeName" class="el-winp" placeholder=" "></el-input>
        </el-form-item>
        <el-form-item label="是否有效">
          <el-select v-model="formType.status" class="el-winp" placeholder=" ">
            <el-option label="有效" value="VALID"></el-option>
            <el-option label="无效" value="INVALID"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>

          <el-button type="primary" class="el-btnw  el-marl" icon="el-icon-search" @click="handleInquire(0)">查询</el-button>

          <el-button class="btn-default el-btnw "  icon="el-icon-refresh" @click="handleReset">重置</el-button>

        </el-form-item>

      </el-row>

    </el-form>
    <el-row>
      <el-table :data="tableData" fit border  :max-height="wrapperHeight" style="width: 100%">
        <el-table-column prop="typeCode" align="center" label="事件类型编码"></el-table-column>
        <el-table-column prop="typeName" align="center" label="事件类型名称" ></el-table-column>
        <el-table-column prop="status" align="center" label="是否启用" >
          <template slot-scope="scope">
            <span v-if="scope.row.status=='VALID'">启用</span>
            <span v-if="scope.row.status=='INVALID'">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="remark" align="center" label="备注" ></el-table-column>
        <el-table-column  align="center" label="操作">
          <template slot-scope="scope">
           <span  class="el-alink" v-if="scope.row.status=='INVALID'||scope.row.status=='VALID'"  @click="handleDisabled(scope.$index, scope.row)" >
              <i v-if="scope.row.status=='INVALID'">启用</i>
              <i v-if="scope.row.status=='VALID'">禁用</i>
            </span>
          </template>
        </el-table-column>
      </el-table>
      <el-row id="pagination" class="crm-pagination"  >
        <el-pagination
          @current-change="handleCurrentChange"
          layout="total, prev, pager, next, jumper"
          :current-page.sync="pagination.currentPage"
          :page-size.sync="pagination.pageSize"
          :total.sync="pagination.total">
        </el-pagination>
      </el-row>
    </el-row>

  </div>
</template>
<script>
  import ElRow from "element-ui/packages/row/src/row";
  import searchMerchant from 'components/common/search'
  export default {
    components: {ElRow, searchMerchant},
    name: 'crmMenu',
    data () {
      return {
        display:false,           //隐藏状态
        wrapperHeight:'',        //页面高度动态设置
        //formType 表单字段
        formType: {
          offset:0,              //请求分页的offset
          status:'',             //状态
          typeCode:'',           //事件类型编码
          typeName:'',           //事件类型名称
        },

        //表格分页
        pagination: {
          currentPage:1,//当前页
          pageSize:20,
          total:0
        },
        //表格数组
        tableData: [],
      }
    },
    methods: {
      // 表单重置
      handleReset() {
        let formType={
          offset:0,              //请求分页的offset
          status:'',             //状态
          typeCode:'',           //事件类型编码
          typeName:'',           //事件类型名称
        }
        this.formType=formType
      },
      // 表单查询
      handleInquire(offsetNum) {
        let dataUrl= couponPostLink+'/fin-rules-osprest/eventType/eventTypeList.htm';
        let data={
          offset:offsetNum,                           //请求分页的offset
          status:this.formType.status,                //状态
          typeCode:this.formType.typeCode,            //事件类型编码
          typeName:this.formType.typeName,            //事件类型名称
        }
        let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
        this.$http.post(dataUrl, params).then((response)=>{
          console.log(response)
          if(response.data.code === 'S0A00000'){
            this.tableData =response.data.data.recordList
            this.pagination.total=parseInt(response.data.data.totalRecordCount); // 总数
          }else{
            this.tableData = [];
            this.$message({showClose: true,duration:2000,type: 'error',message:response.data.msg});
          }
        })
      },
      //表格禁用启用设置
      handleDisabled(index, row) {
        if(!row.id){return}
        if(row.status=='VALID'){
          var status='INVALID'
        }
        else{
          var status='VALID'
        }
        if(row.status=='VALID') {
          this.$confirm('是否禁用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'id': row.id, 'status': status}     //券id
            let dataUrl = couponPostLink + '/fin-rules-osprest/eventType/updateEventTypeStatus.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              console.log(response)
              if (response.data.code === 'S0A00000') {
                  row.status='INVALID'
                  row.statusDesc='禁用'
              } else {
                this.$message.error(response.data.msg);
              }
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '取消禁用操作！'
            });
          });
        }else if(row.status == 'INVALID'){
          this.$confirm('是否启用?', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定',
            type: 'warning'
          }).then(() => {
            let data = {'id': row.id, 'status': status}     //券id
            let dataUrl = couponPostLink + '/fin-rules-osprest/eventType/updateEventTypeStatus.htm';
            let params={'data':data,'token':this.getCookie('token'),'transactionUuid':this.uuid()}
            this.$http.post(dataUrl, params).then((response) => {
              console.log(response)
              if (response.data.code === 'S0A00000') {
                row.status='VALID'
                row.statusDesc='启用'
              } else {
                this.$message.error(response.data.msg);
              }
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '取消启用操作！'
            });
          });
        }

      },
      //分页查询
      handleCurrentChange(val) {
        var  pageOffset=parseInt((val-1)*20);
        this.handleInquire(pageOffset)
      },
    },
    created(){
      this.handleInquire(0);
    },
    mounted () {
      this.wrapperHeight = document.documentElement.clientHeight - document.getElementById('formwrapper').clientHeight-160;
    }
  }
</script>
<style scoped>
  .crm-pagination{
    padding: 15px 0;
    text-align: center;
  }

  .crm-main{
    padding:0 15px ;
  }
  .crm-form{
    padding-top:20px ;
  }
  .crm-form .el-form-item{
    margin-bottom:20px;
  }
</style>



